import {
    CCFloat,
    CCInteger,
    Component,
    Node,
    SpriteFrame,
    UITransform,
    _decorator as decor,
    instantiate,
    randomRange,
    randomRangeInt,
} from 'cc'
import { Direction } from '../../constants/Directions'
import GameplayEvents from '../../constants/GameEvents'
import { GridEvents } from '../../constants/GridEvents'
import LevelGravity from '../../constants/LevelGravity'
import { TileTypes } from '../../constants/TileType'
import { parseLevelGravity } from '../../scripts/applyGravity'
import { ShuffleConfig } from '../../scripts/shuffleTiles'
import shuffleArray from '../../utils/array/shuffle'
import pad from '../../utils/string/pad'
import Tile, { ClearFX } from '../tile/Tile'
import GridMachine from './GridMachine'
import Clear, { ClearEvent } from './states/Clear'
import Idle from './states/Idle'
import Shuffle, { ShuffleEvent } from './states/Shuffle'

const { ccclass, property, requireComponent } = decor

@ccclass('Grid')
@requireComponent([UITransform])
export default class Grid extends Component {
    // @property(GridFX)
    // private gridFx!: GridFX

    @property({ type: [Tile] })
    private tilePrefabs!: Tile

    @property({ group: { name: 'Grid Size' }, type: CCInteger, displayName: 'Width', min: 1 })
    public gridW = 1

    @property({ group: { name: 'Grid Size' }, type: CCInteger, displayName: 'Height', min: 1 })
    public gridH = 1

    @property({
        group: { name: 'Grid Size' },
        type: CCFloat,
        displayName: 'Max tile scale',
        min: 0,
    })
    private maxTileScale = 2

    @property(Node)
    private tilesHolder!: Node

    public frames!: SpriteFrame[]

    private tiles: Tile[][] = []

    private uiTransform!: UITransform

    private gridMachine!: GridMachine
    private normalMatches!: number

    private tileSize!: [number, number]
    public gravityList: LevelGravity[] = []

    protected start(): void {
        // throw error if grid size is not even
        if ((this.gridW * this.gridH) % 2 !== 0) {
            throw new Error('Grid size must be even')
        }

        // get ui transform
        this.uiTransform = this.node.getComponent(UITransform)!

        // disable prefab
        this.tilePrefabs.node.active = false
    }

    public create(frames: SpriteFrame[], levelData: ILevelData): Promise<Grid> {
        this.setNormalMatches(0)
        this.gridW = levelData.columns
        this.gridH = levelData.rows
        this.frames = frames

        this.tiles = this.attachTiles(this, this.tilePrefabs, {
            frames,
            gridW: this.gridW,
            gridH: this.gridH,
            screenW: this.uiTransform.width,
            screenH: this.uiTransform.height,
            maxTileScale: this.maxTileScale,
        })

        this.placeMatchableTiles(this.tiles)

        // NOTE: the number of bomb tiles must be less than the total number of tiles
        const specials: Tile[] = []
        for (let i = 0; i < levelData.specials.bomb; i++) {
            let randomTile = this.getTileAt(
                Math.floor(Math.random() * this.gridW),
                Math.floor(Math.random() * this.gridH)
            )

            while (randomTile === null || specials.find((tile) => tile === randomTile)) {
                randomTile = this.getTileAt(
                    Math.floor(Math.random() * this.gridW),
                    Math.floor(Math.random() * this.gridH)
                )
            }

            randomTile.setSpecialType(TileTypes.BOMB)
            specials.push(randomTile)
        }

        // apply gravity
        this.gravityList = parseLevelGravity(levelData.mode)

        // emit create
        this.node.emit(GridEvents.CREATE)

        // reset grid machine
        if (!this.gridMachine) {
            this.gridMachine = new GridMachine(new Idle(this))
        }

        this.gridMachine.updateState(new Idle(this))

        return new Promise((resolve) => {
            this.syncTiles({
                easing: 'backOut',
                duration: 0.8,
                stagger: 0.2,
            }).then(() => {
                resolve(this)
            })
        })
    }

    private placeMatchableTiles(initiatedGrid: Tile[][]): void {
        const randomTile =
            initiatedGrid[randomRangeInt(0, initiatedGrid.length - 1)][
                randomRangeInt(0, initiatedGrid[0].length - 1)
            ]

        // pick a random tile next to the random tile, in a random direction
        const nearbyTiles = Object.values(Direction)
            .map(({ x, y }) =>
                this.getTileAt(randomTile.getCoord().y + x, randomTile.getCoord().x + y)
            )
            .filter((tile): tile is Tile => tile !== null)

        if (nearbyTiles.length === 0) {
            return
        }

        const randomTile2 = nearbyTiles[randomRangeInt(0, nearbyTiles.length - 1)]

        if (randomTile.isSame(randomTile2)) {
            // already has a match
            return
        }

        // find the first tile that is the same as the second tile, excluding the first tile
        const randomTile22 = this.findActiveTile(0, 0, randomTile2, randomTile2)!

        // make 2-s the same as 1
        randomTile2.setIdentity(randomTile.id!)
        randomTile22.setIdentity(randomTile.id!)
    }

    private attachTiles(grid: Grid, tilePrefabs: Tile, config: GridConfig): Tile[][] {
        // get tile size
        const tileDefaultSize = tilePrefabs.getSize()

        // calculate grid scale after fitting the grid to the screen
        const gridScale = Math.min(
            config.screenW / (tileDefaultSize[0] * config.gridW),
            config.screenH / (tileDefaultSize[1] * config.gridH)
        )

        // calculate tile size after scaling
        grid.tileSize = tileDefaultSize

        // Spacing between tiles
        grid.tileSize = [grid.tileSize[0] * 0.98, grid.tileSize[1] * 0.94]

        // set grid size
        grid.node.setScale(gridScale, gridScale)

        // generate frame to pair up, this array is half the size of the amount of tiles in the grid
        const framePairs: SpriteFrame[] = []

        // assume that there are an even number of tiles
        for (let i = 0; i < (config.gridW * config.gridH) / 2; i++) {
            // push two of the same random frame
            const randomIndex = Math.floor(Math.random() * config.frames.length)

            framePairs.push(config.frames[randomIndex])
            framePairs.push(config.frames[randomIndex])
        }

        // shuffle frame pairs
        const randomlyPairedFrames = shuffleArray(framePairs)

        // clone tile prefabs into a grid
        const tiles: Tile[][] = []
        for (let y = config.gridH - 1; y >= 0; y--) {
            tiles[y] = []
            for (let x = 0; x < config.gridW; x++) {
                const tile = instantiate(tilePrefabs.node).getComponent(Tile)

                if (!tile) {
                    throw new Error('Tile prefab must have Tile component')
                }

                this.tilesHolder.addChild(tile.node)

                // set tile's identity
                const frame = randomlyPairedFrames.pop()!
                tile.setIdentity(frame)

                // add tile to grid
                tile.setCoord(x, y, false)
                tiles[y][x] = tile

                // place the tiles next to each other
                tile.node.setPosition(
                    grid.tileSize[0] * (x - (config.gridW - 1) / 2),
                    grid.tileSize[1] * (y - (config.gridH - 1) / 2)
                )

                // place the tiles on top of each other at 0, 0, remember to sync the tiles
                tile.node.setPosition(0, 0)

                // enable the tile
                tile.node.active = true
            }
        }

        return tiles
    }

    public getTiles(): Tile[] {
        return this.tiles.reduce((acc, val) => acc.concat(val), [])
    }

    public setTileIgnoreInput(tiles: Tile | Tile[], ignore = true): void {
        if (Array.isArray(tiles)) {
            tiles.forEach((tile) => {
                tile.buttonComponent.interactable = !ignore
            })
            return
        }

        tiles.buttonComponent.interactable = !ignore
    }

    public getTileGrid(): Tile[][] {
        return this.tiles
    }

    public emitTileDown(tile: Tile): void {
        this.gridMachine.emitTileDown(tile)
    }

    public getTileAt(x: number, y: number): Tile | null {
        if (x < 0 || x >= this.gridW || y < 0 || y >= this.gridH) return null
        return this.tiles[y]?.[x] ?? null
    }

    public clearTilesAt(pairs: [Tile, Tile][], animationType: ClearFX = 'clear'): Promise<void> {
        if (!this.isReady()) {
            throw new Error('Cannot clear tiles when grid is not ready')
        }

        const clearState = new Clear(
            this,
            pairs.map((pair) => ({
                tile1: pair[0],
                tile2: pair[1],
                fx: animationType,
            }))
        )

        return new Promise((resolve) => {
            clearState.eventEmitter.on(ClearEvent.CLEAR_END, () => {
                resolve()
            })

            this.gridMachine.updateState(clearState)
        })
    }

    public swapTilesImmediate(tile1: Tile, tile2: Tile): void {
        const coord1 = tile1.getCoord().clone()
        const coord2 = tile2.getCoord().clone()

        tile1.setCoord(coord2.x, coord2.y)
        tile2.setCoord(coord1.x, coord1.y)

        this.tiles[coord1.y][coord1.x] = tile2 as Tile
        this.tiles[coord2.y][coord2.x] = tile1 as Tile
    }

    public async syncTiles(
        config?: Pick<SyncConfig, 'easing' | 'duration' | 'stagger'>
    ): Promise<void> {
        const stagger = config?.stagger ?? 0
        const promises = this.getTiles().map((tile) => {
            return tile.sync({
                tileSize: this.tileSize,
                frames: this.frames,
                gridW: this.gridW,
                gridH: this.gridH,
                screenW: this.uiTransform.width,
                screenH: this.uiTransform.height,
                maxTileScale: this.maxTileScale,

                ...config,

                stagger: randomRange(0, stagger),
            })
        })

        await Promise.all(promises)
    }

    public async padTiles(
        config?: Pick<PaddingConfig, 'easing' | 'duration' | 'stagger' | 'padding'>
    ): Promise<void[]> {
        const stagger = config?.stagger ?? 0
        const promises = this.getTiles().map((tile) => {
            return tile.pad({
                tileSize: this.tileSize,
                frames: this.frames,
                gridW: this.gridW,
                gridH: this.gridH,
                screenW: this.uiTransform.width,
                screenH: this.uiTransform.height,
                maxTileScale: this.maxTileScale,

                ...config,

                stagger: randomRange(0, stagger),
                padding: config?.padding ?? 0,
            })
        })

        return Promise.all(promises)
    }

    public async flyTiles(
        config?: Pick<PaddingConfig, 'easing' | 'duration' | 'stagger' | 'padding'>
    ) {
        const fly = this.getTiles().map((tile) => {
            return tile.fly({
                tileSize: this.tileSize,
                frames: this.frames,
                gridW: this.gridW,
                gridH: this.gridH,
                screenW: this.uiTransform.width,
                screenH: this.uiTransform.height,
                maxTileScale: this.maxTileScale,

                ...config,

                stagger: config?.stagger ?? 0,
                padding: config?.padding ?? 0,
            })
        })

        await Promise.all(fly)
    }

    public sortTiles(): void {
        // sort tiles objects by their y position
        this.getTiles()
            .sort((a, b) => {
                return b.getCoord().y - a.getCoord().y
            })
            .forEach((tile) => {
                tile.node.setSiblingIndex(this.tilesHolder.children.length - 1)
            })
    }

    public sortTilesForTornadoAnim(): void {
        //? more close to center, more higher
        let tiles = this.getTiles()
        tiles = tiles.sort((a, b) => {
            const { x: xa, y: ya } = a.node.position
            const { x: xb, y: yb } = b.node.position
            return xb ** 2 + yb ** 2 - (xa ** 2 + ya ** 2)
        })
        tiles.forEach((tile) => {
            tile.node.setSiblingIndex(this.tilesHolder.children.length - 1)
        })
    }

    public clearAll(): Promise<void> {
        return new Promise((resolve) => {
            Promise.all(
                this.getTiles().map((tile) => {
                    return tile.setEmpty()
                })
            ).then(() => {
                this.node.emit(GridEvents.CLEAR)
                this.node.emit(GridEvents.CLEAR_ALL)
                resolve()
            })
        })
    }

    public destroyAll(): void {
        this.getTiles().forEach((tile) => {
            tile.node.destroy()
        })
    }

    public getRandomActivePair(): [Tile, Tile] | null {
        // get random active tile
        let randomTile = this.findActiveTile(
            Math.floor(Math.random() * this.gridW),
            Math.floor(Math.random() * this.gridH)
        )

        if (randomTile === null) {
            // try again from 0, 0
            randomTile = this.findActiveTile()
        }

        if (randomTile === null) {
            // no active tile left
            return null
        }

        // find a random tile that is not the same as the first tile but has the same id
        let randomTile2 = this.findActiveTile(
            Math.floor(Math.random() * this.gridW),
            Math.floor(Math.random() * this.gridH),
            randomTile,
            randomTile
        )

        if (randomTile2 === null) {
            // try again from 0, 0
            randomTile2 = this.findActiveTile(0, 0, randomTile, randomTile)
        }

        if (randomTile2 === null) {
            // met exception
            throw new Error('Cannot find a pair for tile')
        }

        return [randomTile, randomTile2]
    }

    public getRandomInactivePair(): [Tile, Tile] | null {
        // get random inactive tile
        let randomTile = this.findInactiveTile(
            Math.floor(Math.random() * this.gridW),
            Math.floor(Math.random() * this.gridH)
        )

        if (randomTile === null) {
            // try again from 0, 0
            randomTile = this.findInactiveTile()
        }

        if (randomTile === null) {
            // no inactive tile left
            return null
        }

        // find a random tile that is not the same as the first tile but has the same id
        let randomTile2 = this.findInactiveTile(
            Math.floor(Math.random() * this.gridW),
            Math.floor(Math.random() * this.gridH),
            randomTile
        )

        if (randomTile2 === null) {
            // try again from 0, 0
            randomTile2 = this.findInactiveTile(0, 0, randomTile)
        }

        if (randomTile2 === null) {
            // met exception
            throw new Error('Cannot find a pair for tile')
        }

        return [randomTile, randomTile2]
    }

    private findActiveTile(startX = 0, startY = 0, similar?: Tile, exceptionTile?: Tile | Tile[]) {
        const exceptionTiles = Array.isArray(exceptionTile) ? exceptionTile : [exceptionTile]

        const isSkipped = (tile: Tile) => {
            return exceptionTiles.some((exception) => {
                return exception?.getCoord().equals(tile.getCoord())
            })
        }

        for (let y = startY; y < this.gridH; y++) {
            for (let x = startX; x < this.gridW; x++) {
                // skips all empty tiles
                // skip exception tile
                if (this.tiles[y][x].isTraversable() || isSkipped(this.tiles[y][x])) {
                    continue
                }

                // if id is not specified, return the first tile found
                if (similar === undefined) {
                    return this.tiles[y][x]
                }

                // if id is specified, only return tiles with the same id
                if (this.tiles[y][x].isSame(similar)) {
                    return this.tiles[y][x]
                }
            }
        }

        return null
    }

    private findInactiveTile(startX = 0, startY = 0, exceptionTile?: Tile): Tile | null {
        for (let y = startY; y < this.gridH; y++) {
            for (let x = startX; x < this.gridW; x++) {
                // skips all non-empty tiles
                if (!this.tiles[y][x].isTraversable()) continue

                // skip exception tile
                if (exceptionTile?.getCoord().equals(this.tiles[y][x].getCoord())) continue

                return this.tiles[y][x]
            }
        }

        return null
    }

    public toWorldPosition(position: Vec2Like): Vec2Like {
        let x = this.tileSize[0] * (position.x - (this.gridW - 1) / 2)
        let y = this.tileSize[1] * (position.y - (this.gridH - 1) / 2)

        if (position.x === -1) {
            x += this.tileSize[0] / 4
        }

        if (position.x === this.gridW) {
            x -= this.tileSize[0] / 4
        }

        if (position.y === -1) {
            y += this.tileSize[1] / 4
        }

        if (position.y === this.gridH) {
            y -= this.tileSize[1] / 4
        }

        return {
            x: x,
            y: y,
        }
    }

    public serialize(): SerializableGrid {
        const serializedGrid: SerializableGrid = {
            columns: this.gridW,
            rows: this.gridH,
            code: '',
        }

        this.tiles.forEach((row) => {
            row.forEach((tile) => {
                if (tile.isTraversable()) {
                    const id = this.frames.indexOf(tile.id!).toString()

                    serializedGrid.code += `t${pad('00', id, true)}`
                } else {
                    serializedGrid.code += 'i01'
                }
            })
            serializedGrid.code += ':'
        })

        return serializedGrid
    }

    public deserialize(frames: SpriteFrame[], json: SerializableGrid): Promise<Grid> {
        this.setNormalMatches(0)
        this.gridW = json.columns
        this.gridH = json.rows
        this.frames = frames

        this.tiles = this.attachTiles(this, this.tilePrefabs, {
            frames,
            gridW: this.gridW,
            gridH: this.gridH,
            screenW: this.uiTransform.width,
            screenH: this.uiTransform.height,
            maxTileScale: this.maxTileScale,
        })

        // ":t01t02t03t03t04:t05t06t05t07t08:t04t09i01t09t07:t10t02i01i01t06:t08t10i01i01t01"
        const parsedGrid = json.code.match(/([ti]\d{2})+/g)

        if (!parsedGrid || parsedGrid.length !== this.gridH) {
            throw new Error('Invalid grid code')
        }

        // flip the rows to match the grid's coordinate system
        parsedGrid.reverse()

        parsedGrid.forEach((row, y) => {
            // ex: t01t02t03t03t04
            const parsedCol = row.match(/([ti]\d{2})/g)

            if (!parsedCol || parsedCol.length !== this.gridW) {
                throw new Error('Invalid grid code')
            }

            // if t, normal tile
            // if i, empty tile -> call setEmpty(undefined, true) on tile to set empty quietly
            parsedCol.forEach((tileCode, x) => {
                const tile = this.getTileAt(x, y)

                if (!tile) {
                    throw new Error('Invalid grid code')
                }

                if (tileCode.startsWith('t')) {
                    const frameIndex = parseInt(tileCode.slice(1), 10)

                    if (frameIndex >= this.frames.length) {
                        throw new Error(
                            'Invalid grid code or insufficient theme. Not enough frames to satisfy grid.'
                        )
                    }

                    tile.setIdentity(this.frames[parseInt(tileCode.slice(1), 10)])
                }

                if (tileCode.startsWith('i')) {
                    tile.setEmpty(undefined, true)
                }
            })
        })

        // apply gravity
        // TODO: use event instead
        // this.gravityList = parseLevelGravity(this.levelManager.getLevelData().mode)
        this.node.scene.emit(GameplayEvents.APPLY_GRAVITY)

        this.node.emit(GridEvents.CREATE)

        // reset grid machine
        if (!this.gridMachine) {
            this.gridMachine = new GridMachine(new Idle(this))
        }

        this.gridMachine.updateState(new Idle(this))

        return new Promise((resolve) => {
            this.syncTiles({
                easing: 'backOut',
                duration: 0.8,
                stagger: 0.2,
            }).then(() => {
                resolve(this)
            })
        })
    }

    public shuffleTiles(config: Omit<ShuffleConfig, 'grid'>): Promise<void> {
        if (!this.gridMachine.awaitingInput()) {
            throw new Error('Cannot shuffle tiles when grid is not awaiting input')
        }

        const shuffleState = new Shuffle(this, { ...config, grid: this }, true)
        return new Promise((resolve) => {
            shuffleState.eventEmitter.on(ShuffleEvent.SHUFFLE_COMPLETE, () => {
                resolve()
            })

            this.gridMachine.updateState(shuffleState)
        })
    }

    public isReady(): boolean {
        return this.gridMachine.awaitingInput()
    }

    public getNormalMatches(): number {
        return this.normalMatches
    }

    public setNormalMatches(value: number): void {
        this.normalMatches = value
    }

    public getCleared(): Tile[] {
        return this.getTiles().filter((tile) => tile.isTraversable())
    }

    public getUncleared(): Tile[] {
        return this.getTiles().filter((tile) => !tile.isTraversable())
    }

    public isHaveBombActive(): boolean {
        return this.getTiles().some(
            (tile) => !tile.isTraversable() && tile.getSpecialType() === 'bomb'
        )
    }
}
