import { Button, Component, Label, Node, UIOpacity, _decorator, find, tween } from 'cc'
import { Music } from '../../components/audio/Music'
import { Sound } from '../../components/audio/Sound'
import ScreenKeys from '../../constants/ScreenKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { BaseScreen } from '../BaseScreen'
const { ccclass, property, disallowMultiple, requireComponent } = _decorator

const { Events, Configs } = GameCore
const { ads, language, event } = globalThis.game

@ccclass('LoseScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class LoseScreen extends Component {
    private screen!: BaseScreen

    @property(Label)
    public levelText!: Label

    @property(Button)
    public retryButton!: Button

    @property(Node)
    public title!: Node

    @property(Button)
    private retryButtonComponent!: Button

    @property(Button)
    private closeButtonComponent!: Button

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('LoseScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.enabled = false
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.levelText) {
            throw new Error('LoseScreen: levelText not found')
        }

        if (!this.retryButton) {
            throw new Error('LoseScreen: retryButton not found')
        }

        if (!this.title) {
            throw new Error('LoseScreen: title not found')
        }
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.retryButtonComponent.interactable = true
        this.closeButtonComponent.interactable = true
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    public open() {
        this.setLevelText()
        this.resetState()
        this.runOpenAnimation()
    }

    public close() {
        this.closeButtonComponent.interactable = false
        this.retryButtonComponent.interactable = false
        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    private runOpenAnimation() {
        const ribbonAnim = this.createRibbonAnim()
        const levelAnim = this.createLevelAnim()
        const retryButtonAnim = this.createRetryButtonAnim()

        if (!ribbonAnim || !levelAnim || !retryButtonAnim) {
            console.warn('LoseScreen: animations not found')
            return
        }

        // * This is a demo of how to use tween for parallel any type of tween
        tween({})
            .delay(0.2)
            .call(() => ribbonAnim.start())
            .delay(0.2)
            .call(() => levelAnim.start())
            .delay(0.2)
            .call(() => retryButtonAnim.start())
            .start()
    }

    private resetState() {
        const retryOpacity = this.retryButton.getComponent(UIOpacity)
        const titleOpacity = this.title.getComponent(UIOpacity)
        const levelOpacity = this.levelText.getComponent(UIOpacity)

        if (!retryOpacity || !titleOpacity || !levelOpacity) {
            console.warn('LoseScreen: UIOpacity of some component not found')
            return
        }

        retryOpacity.opacity = 0
        titleOpacity.opacity = 0
        levelOpacity.opacity = 0

        this.retryButton.node.active = false
    }

    private setLevelText() {
        const level = this.screen.data['level']
        const levelText = language.getText('level')
        this.levelText.string = levelText + ` ${level}`
    }

    private createRibbonAnim() {
        const opacity = this.title.getComponent(UIOpacity)
        if (!opacity) return null

        return tween(opacity).set({ opacity: 0 }).to(0.3, { opacity: 255 }, { easing: 'smooth' })
    }

    private createLevelAnim() {
        const opacity = this.levelText.getComponent(UIOpacity)
        if (!opacity) return null

        return tween(opacity).set({ opacity: 0 }).to(0.3, { opacity: 255 }, { easing: 'smooth' })
    }

    private createRetryButtonAnim() {
        const opacity = this.retryButton.getComponent(UIOpacity)
        if (!opacity) return null

        this.retryButton.node.active = true

        return tween(opacity)
            .set({ opacity: 0 })
            .to(0.4, { opacity: 255 }, { easing: 'smooth' })
            .call(this.showInterstitialAdAsync)
    }

    private showInterstitialAdAsync = async () => {
        if (GameSDK.getSDKName() === 'CrazyGames') return

        const level = this.screen.data['level']
        if (!level) return

        if (Configs.Gameplay.StartAdAtLevel > level) return

        if (!ads.canbeShowInterstitialAd()) return

        const beforeTime = Date.now()

        const { INTERSTITIAL } = GameCore.Plugins.Ads.Types

        try {
            this.muteAudio(true)

            const loadingText = language.getText('loading')
            this.showNotificationScreen(loadingText)

            await ads.showAdAsync(INTERSTITIAL)
        } catch (error) {
            console.error('showInterstitialAdAsync failed', error)
        } finally {
            this.muteAudio(false)

            const displayTime = (Date.now() - beforeTime) / 1000
            this.scheduleOnce(
                () => {
                    this.closeNotificationScreen()
                },
                displayTime > 0.5 ? 0 : 0.5
            )

            ads.loadAdAsync(INTERSTITIAL).catch(() => {
                // Safe to ignore
            })
        }
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    private closeNotificationScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
        })
    }

    private muteAudio(isMute: boolean): void {
        this.muteMusic(isMute)
        this.muteSound(isMute)
    }

    private getMusicComponent(): Music | null {
        const music = find('Music')?.getComponent(Music)
        if (!music) {
            console.warn('LoseScreen: Music not found')
            return null
        }

        return music
    }

    private getSoundComponent(): Sound | null {
        const sound = find('Sound')?.getComponent(Sound)
        if (!sound) {
            console.warn('LoseScreen: Sound not found')
            return null
        }

        return sound
    }

    private muteMusic(isMute: boolean): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.mute(isMute)
    }

    private muteSound(isMute: boolean): void {
        const sound = this.getSoundComponent()
        if (!sound) return

        sound.mute(isMute)
    }

    // ? Callback for click event of RetryButton
    public handleClickedRetryGame(): void {
        this.close()
        this.requestGameReplay()
    }

    // ? Callback for click event of CloseButton
    public handleClickedClose(): void {
        this.close()
        this.requestGameReplay()
    }

    private requestGameReplay() {
        this.node.scene.emit(GameplayEvents.GAME_REPLAY, {
            level: this.screen.data['level'],
        })
    }
}
