import { Animation, Canvas, Component, Director, Node, _decorator, director, instantiate } from 'cc'
const { ccclass, requireComponent, property } = _decorator

/**
 * `LoadingSceneTransition` require Scene not use `AutoRelease` options
 */
@ccclass('LoadingSceneTransition')
@requireComponent([Canvas])
export class LoadingSceneTransition extends Component {
    @property([Node])
    targets: Node[] = []

    private realTargets: Node[] = []

    public start(): void {
        for (let i = 0; i < this.targets.length; i++) {
            const target = this.targets[i]

            if (!target) continue

            const clone = instantiate(target)
            clone.setParent(this.node)
            clone.active = false
            
            this.realTargets.push(clone)
        }

        director.addPersistRootNode(this.node)
        director.once(Director.EVENT_BEFORE_SCENE_LAUNCH, () => {
            this.playFadeOutAnimationClip()
        })
    }

    public playFadeOutAnimationClip(): void {
        // const targets = this.targets.map((target) => {
        //     const clone = instantiate(target)
        //     clone.setParent(this.node)
        //     return clone
        // })

        let numOfAnimFinish = 0

        if (!this.realTargets || this.realTargets.length === 0) return

        this.realTargets.forEach((target: Node) => {
            target.active = true

            const animCom = target.getComponent(Animation)
            if (!animCom) return

            const isFadeOutExist = animCom.clips.some((clip) => clip?.name === 'FadeOut')
            if (!isFadeOutExist) {
                this.node.destroy()
                console.warn('Not have FadeOut clip')
                return
            }

            animCom.play('FadeOut')
            animCom.once(Animation.EventType.FINISHED, () => {
                numOfAnimFinish++
                if (numOfAnimFinish === this.realTargets.length) {
                    this.node.destroy()
                }
            })
        })
    }
}
