import { find } from 'cc'
import { Music } from '../../components/audio/Music'
import { Sound } from '../../components/audio/Sound'
import ScreenKeys from '../../constants/ScreenKeys'
import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import CompleteState from './CompleteState'
import FailState from './FailState'
import FinishState from './FinishState'
import InitializeState from './InitializeState'
import PlayingState from './PlayingState'

const { Events } = GameCore
const { event } = globalThis.game

class ReadyState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)
        this.updateStateData()

        this.stopAllSounds()
        this.turnUpBackgroundMusic()

        this.setLevelTime()
        this.showTutorialUI()

        this.openLevelStartScreen()
    }

    public initialize(): void {
        const state = new InitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // This is ready state, do nothing
    }

    public playing(): void {
        const state = new PlayingState(this.manager)
        this.manager.change(state)
    }

    public fail(): void {
        // ? Allow fail by dev tools
        if (!GameCore.Utils.Valid.isDebugger()) return

        const state = new FailState(this.manager)
        this.manager.change(state)
    }

    public complete(): void {
        // ? Allow complete by dev tools
        if (!GameCore.Utils.Valid.isDebugger()) return

        const state = new CompleteState(this.manager)
        this.manager.change(state)
    }

    public finish(): void {
        // ? Allow complete by dev tools
        if (!GameCore.Utils.Valid.isDebugger()) return

        const state = new FinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        //
    }

    private openLevelStartScreen() {
        const { levelData } = this.data
        if (!levelData) return

        const { time: timeInSecond } = levelData
        const description = `Clear all tiles in ${timeInSecond} seconds`

        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.START_LEVEL_SCREEN,
            screenData: {
                level: this.data.level,
                message: description,
            },
        })
    }

    private stopAllSounds() {
        find('Sound')?.getComponent(Sound)?.stopAll()
    }

    private turnUpBackgroundMusic(): void {
        find('Music')?.getComponent(Music)?.setVolume(0.8)
    }

    private setLevelTime(): void {
        if (!this.game) return

        const { levelData } = this.data
        if (!levelData) return

        this.game.uiManager.setCountdownTime(levelData.time)
    }

    private showTutorialUI(): void {
        if (!this.game) return

        const { level, levelData } = this.data
        if (!level || !levelData) return

        if (level > 3) return

        //this.game.uiManager.stopCountDownHeaderHintBubbleAnim()
    }
}

export default ReadyState
