import {
    Camera,
    Canvas,
    Component,
    ResolutionPolicy,
    View,
    _decorator,
    macro,
    screen,
    sys,
} from 'cc'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import CocosUtils from '../../utils/CocosUtils'
const { ccclass, executionOrder, requireComponent, disallowMultiple } = _decorator

const { Function: F } = GameCore.Utils

const { Layout, DesignSize } = GameWorld
const { Portrait, Landscape } = DesignSize

@ccclass('Orientation')
@disallowMultiple
@executionOrder(-999)
@requireComponent([Camera])
export class Orientation extends Component {
    private camera!: Camera
    private canvas!: Canvas

    private currentLayout = ''

    public __preload(): void {
        this.camera = this.getComponent(Camera)!
        this.canvas = this.node.scene.getComponentInChildren(Canvas)!

        this.validateRequiredComponents()

        this.listenResizeEvent()
    }

    private validateRequiredComponents() {
        if (!this.camera) {
            throw new Error('Orientation: Camera not found')
        }

        if (!this.canvas) {
            throw new Error('Orientation: Canvas not found')
        }
    }

    public onLoad() {
        this.attachCameraToCanvas()
        this.setupOrientationByScreenSize()
    }

    private attachCameraToCanvas() {
        this.canvas.cameraComponent = this.camera
    }

    private setupOrientationByScreenSize() {
        const width = CocosUtils.getScreenWidthCss()
        const height = CocosUtils.getScreenHeightCss()

        const ratio = width / height
        const isPortraitLossRatio = ratio > this.getPortraitRatio()

        if (!isPortraitLossRatio) {
            this.setPortraitView()
        } else {
            this.setLandscapeView()
        }

        this.node.scene.emit(GameEvents.VIEW_RESIZE, { width, height })
    }

    private setPortraitView() {
        const { resolution } = screen

        View.instance.setOrientation(macro.ORIENTATION_AUTO)

        const worldRatio = this.getWorldRatio()
        const screenRatio = this.getScreenRatio()

        // ? Keep landscape ratio, change FULL_PORTRAIT_SIZE if you want to stretch the game
        let resolutionPolicy = ResolutionPolicy.FIXED_WIDTH

        if (worldRatio >= screenRatio && !GameWorld.FULL_PORTRAIT_SIZE) {
            resolutionPolicy = ResolutionPolicy.SHOW_ALL
        }

        View.instance.setDesignResolutionSize(Portrait.WIDTH, Portrait.HEIGHT, resolutionPolicy)

        console.info('setPortraitView', { resolution, worldRatio, screenRatio })

        if (this.currentLayout !== Layout.Portrait) {
            this.node.scene.emit(GameEvents.LAYOUT_CHANGED, Layout.Portrait)
            this.currentLayout = Layout.Portrait
        }
    }

    private setLandscapeView() {
        const { resolution } = screen

        View.instance.setOrientation(macro.ORIENTATION_AUTO)

        const worldRatio = this.getWorldRatio()
        const screenRatio = this.getScreenRatio()

        // ? Keep landscape ratio, change FULL_LANDSCAPE_SIZE if you want to support full screen size
        let resolutionPolicy = ResolutionPolicy.FIXED_HEIGHT

        if (worldRatio < screenRatio && !GameWorld.FULL_LANDSCAPE_SIZE) {
            resolutionPolicy = ResolutionPolicy.EXACT_FIT
            // resolutionPolicy = ResolutionPolicy.SHOW_ALL
        }

        View.instance.setDesignResolutionSize(Landscape.WIDTH, Landscape.HEIGHT, resolutionPolicy)

        console.info('setLandscapeView', { resolution, worldRatio, screenRatio })

        if (this.currentLayout !== Layout.Landscape) {
            this.node.scene.emit(GameEvents.LAYOUT_CHANGED, Layout.Landscape)
            this.currentLayout = Layout.Landscape
        }
    }

    private listenResizeEvent() {
        if (sys.isBrowser) {
            screen.on('window-resize', this.handleViewResize, this)
        } else if (sys.isMobile) {
            window.addEventListener('resize', this.handleViewResize, {
                capture: false,
                passive: true,
            })
        }
    }

    private handleViewResize = F.debounce(() => {
        this.setupOrientationByScreenSize()
    }, 200)

    private getScreenRatio() {
        const { resolution } = screen
        return resolution.width / resolution.height
    }

    private getWorldRatio() {
        const { width, height } = View.instance.getDesignResolutionSize()
        return width / height
    }

    private getPortraitRatio() {
        return Portrait.WIDTH / Portrait.HEIGHT
    }
}
