import { Component, _decorator } from 'cc'
import BaseState from '../states/BaseState'
import CompleteState from '../states/CompleteState'
import FailState from '../states/FailState'
import FinishState from '../states/FinishState'
import InitializeState from '../states/InitializeState'
import PlayingState from '../states/PlayingState'
import ReadyState from '../states/ReadyState'
const { ccclass } = _decorator

// ? "isX" methods only used for specific state checking, don't use it for other purpose. ex: check when switch state

@ccclass('StateManager')
export class StateManager extends Component {
    private state!: BaseState

    public init() {
        this.state = new InitializeState(this)
    }

    public change(state: BaseState): void {
        this.state = state
    }

    public initialize(): void {
        this.state.initialize()
    }

    public isInitialize(): boolean {
        return this.state instanceof InitializeState
    }

    public ready(): void {
        this.state.ready()
    }

    public isReady(): boolean {
        return this.state instanceof ReadyState
    }

    public playing(): void {
        this.state.playing()
    }

    public isPlaying(): boolean {
        return this.state instanceof PlayingState
    }

    public fail(): void {
        this.state.fail()
    }

    public isFail(): boolean {
        return this.state instanceof FailState
    }

    public complete(): void {
        this.state.complete()
    }

    public isComplete(): boolean {
        return this.state instanceof CompleteState
    }

    public finish(): void {
        this.state.finish()
    }

    public isFinished(): boolean {
        return this.state instanceof FinishState
    }

    public update(deltaTime: number) {
        if (!this.state) return
        this.state.update(deltaTime)
    }
}
