import { Component, Director, _decorator, director, game } from 'cc'
import GameSettings from '../../constants/GameSettings'
import ItemKeys from '../../gameplay/constants/ItemKeys'
const { ccclass, disallowMultiple, executionOrder } = _decorator

const { Utils, Events, Configs, Dtos } = GameCore
const { event, context } = globalThis.game

@ccclass('Corecos')
@disallowMultiple
@executionOrder(1000)
export class Corecos extends Component {
    public __preload(): void {
        // called before onLoad, use for initialization
        this.listenEvents()
    }

    private listenEvents(): void {
        event.once(Events.CORE_BOOTING, this.handleWhenCoreBooting)
        event.once(Events.CORE_STARTING, this.handleWhenCoreStarting)
        event.once(Events.CORE_READY, this.handleWhenCoreReady)
    }

    public start(): void {
        // Check GameCore has emit CORE_BOOTING event
        const isGameCoreBooted = event.getEventEmitCount(Events.CORE_BOOTING) > 0

        if (isGameCoreBooted) {
            this.handleWhenCoreBooting()
        }
    }

    private handleWhenCoreBooting = (): void => {
        // ? Control GameCore flow for easy injection
        event.emit(Events.REQUEST_CORE_START)
    }

    private handleWhenCoreStarting = (): void => {
        this.setupDefaultPlayerGameData()
    }

    private handleWhenCoreReady = async (): Promise<void> => {
        await this.startGameAsync()

        this.startProcessContextData()

        this.initCoreDebuggerTools()
    }

    private setupDefaultPlayerGameData() {
        const { MaxLevel } = GameSettings

        const { Player } = Dtos

        // * Add default player game data
        // ? Check interface from /types/game-core/player.d.ts
        const defaultData = {
            level: 1,
            coins: 0,
            totalLevel: MaxLevel,
            levelVersion: 0,
            passAllLevels: false,
            items: {
                [ItemKeys.HINT]: 0,
                [ItemKeys.THUNDER]: 0,
                [ItemKeys.STORM]: 0,
                [ItemKeys.PLIERS]: 0,
            },
            themes: {} as GameCore.Plugins.Player.GameData['themes'],
        }
        Player.GameData.addDefaultData(defaultData)
        //TODO: fix core to not modify defaultData
        //@ts-expect-error waiting update core
        window.__defaultData = Utils.Json.clone(Player.Data.prototype.data)

        // ? Override validate function or add new validate function
        Player.GameData.addValidateFunction('totalLevel', (totalLevel: number) => {
            if (!Utils.Valid.isNumber(totalLevel) || totalLevel < 1) {
                throw new Error(`totalLevel ${totalLevel} is invalid`)
            }
        })

        Player.GameData.addValidateFunction('levelVersion', (levelVersion: number) => {
            if (!Utils.Valid.isNumber(levelVersion) || levelVersion < 0) {
                throw new Error(`levelVersion ${levelVersion} is invalid`)
            }
        })

        Player.GameData.addValidateFunction('passAllLevels', (passAllLevels: boolean) => {
            if (!Utils.Valid.isBoolean(passAllLevels)) {
                throw new Error(`passAllLevels ${passAllLevels} is invalid`)
            }
        })

        Player.GameData.addValidateFunction('items', (items: unknown) => {
            if (!Utils.Valid.isObject(items)) {
                throw new Error(`items ${items} is invalid`)
            }

            const keys = Object.keys(items)
            const values = Object.values(items)

            if (keys.length !== values.length) {
                throw new Error(`items ${items} is invalid`)
            }

            if (
                !keys.includes(ItemKeys.HINT) ||
                !keys.includes(ItemKeys.THUNDER) ||
                !keys.includes(ItemKeys.STORM) ||
                !keys.includes(ItemKeys.PLIERS)
            ) {
                throw new Error(`items ${items} is invalid`)
            }

            values.forEach((value) => {
                if (!Utils.Valid.isNumber(value) || value < 0) {
                    throw new Error(`items ${items} is invalid`)
                }
            })
        })

        Player.GameData.addValidateFunction('themes', (themes: unknown) => {
            if (!Utils.Valid.isObject(themes)) {
                throw new Error(`passAllLevels ${themes} is invalid`)
            }
        })
    }

    // ? Why call startGameAsync here?
    // * We want to make sure that the game is ready to start, not show
    private async startGameAsync() {
        try {
            await GameSDK.startGameAsync()
        } catch (error) {
            console.warn('GameSDK.startGameAsync: error', error)
        }
    }

    private async startProcessContextData() {
        // this.isProgressContextData = true

        // processContextData method will be auto switch to next scene
        // * It will also automatically call the flows from the match plugin and context plugin
        // ? This means that the match flow of the GameScene will need to be skipped when first entering your game
        context.processContextData().catch((error: Error) => {
            console.error('ContextPlugin.processContextData', error)
        })
    }

    private initCoreDebuggerTools() {
        const isDebug = Utils.Valid.isDebugger()
        if (!isDebug) return

        this.initCanvasRecorder()
    }

    private initCanvasRecorder() {
        const { Enable } = Configs.Debugger.CanvasRecorder
        if (!Enable) return

        const { canvasRecorder } = globalThis.game

        if (!game.canvas || !canvasRecorder) return

        canvasRecorder.setCanvas(game.canvas)

        // canvasRecorder.runTestCanvas()
        // return

        const options = canvasRecorder.getOptions()

        const nextFrame = () => {
            game.pause()
            game.step()
        }

        const originWaitNextFrame = canvasRecorder.waitNextFrame

        canvasRecorder.waitNextFrame = async () => {
            if (options.syncFps) {
                return new Promise((resolve) => {
                    director.once(Director.EVENT_AFTER_RENDER, resolve)
                    nextFrame()
                })
            } else {
                game.resume()
                await originWaitNextFrame.call(canvasRecorder)
                return Promise.resolve()
            }
        }
    }
}
