import { CCBoolean, Component, UIOpacity, _decorator } from 'cc'
const { ccclass, requireComponent, property } = _decorator

@ccclass('OpacityToggler')
@requireComponent([UIOpacity])
export class OpacityToggler extends Component {
    @property(CCBoolean)
    private isActive = true

    @property
    private activeValue = 255

    @property
    private inActiveValue = 128

    private opacityComponent!: UIOpacity

    public onLoad() {
        this.opacityComponent = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.opacityComponent) {
            throw new Error('OpacityToggler: UIOpacity not found')
        }
    }

    public changeState(active?: boolean) {
        if (active === undefined) {
            active = !this.isActive
        } else if (active) {
            this.opacityComponent.opacity = this.activeValue
            this.isActive = true
        } else {
            this.opacityComponent.opacity = this.inActiveValue
            this.isActive = false
        }
    }
}
