import { Component, Label, _decorator } from 'cc'
import ScreenKeys from '../../constants/ScreenKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'

const { ccclass, property } = _decorator
const { event } = globalThis.game
const { Events } = GameCore

@ccclass('CoinContainer')
export class CoinContainer extends Component {
    @property(Label)
    private coinLabel!: Label

    public setCoinText(value: string): void {
        this.coinLabel.string = value
    }

    public handleAddCoinButton(): void {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message: 'Coming soon!' },
        })
    }

    public onLoad(): void {
        this.validateRequiredComponents()
        this.listenToEvents()
    }

    private validateRequiredComponents(): void {
        if (!this.coinLabel) {
            throw new Error('CoinContainer: coinLabel is not assigned')
        }
    }

    private listenToEvents(): void {
        this.node.scene.on(GameplayEvents.COIN_CHANGED, this.updateCoins, this)
    }

    private updateCoins(value: CoinDataPayload): void {
        this.setCoinText(value.coins.toString())
    }
}
