import { math, SpriteFrame } from 'cc'

export default class BaseTile {
    coord: math.Vec2
    id: SpriteFrame | null

    constructor(coord: math.Vec2) {
        this.coord = coord
        this.id = null
    }

    getCoord(): math.Vec2 {
        return this.coord
    }

    setCoord(x: number, y: number): void {
        this.coord.set(x, y)
    }

    isTraversable(): boolean {
        return true
    }
}
