import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

type JoinTournamentPayload = GameCore.Plugins.Match.Concrete.Payload.Tournament.Join
type ContinueTournamentPayload = GameCore.Plugins.Match.Concrete.Payload.Tournament.Continue

const { match, player, event } = globalThis.game

@ccclass('FlowTournament')
export class FlowTournament extends Component {
    private playerId = ''

    public onLoad() {
        this.playerId = player.getPlayerId()
    }

    private showLoadingScreen(duration: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
            screenData: { duration },
        })
    }

    private closeLoadingScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    public async startTournament() {
        try {
            this.showLoadingScreen(0)

            await match.tournament.start.processAsync({ playerId: this.playerId })
        } catch (error) {
            console.warn('startTournament', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async joinTournament(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as JoinTournamentPayload

        try {
            this.showLoadingScreen(0)

            await match.tournament.join.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('joinTournament', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async continueTournament(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as ContinueTournamentPayload

        try {
            this.showLoadingScreen(0)

            await match.tournament.continue.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('continueTournament', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async finishTournament() {
        try {
            this.showLoadingScreen(0)

            player.setPlayerDataByName('score', Math.floor(Math.random() * 2))
            await match.single.finish.processAsync()
        } catch (error) {
            console.warn('finishTournament', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async createTournament() {
        try {
            this.showLoadingScreen(0)

            await match.tournament.create.processAsync({ playerId: this.playerId })
        } catch (error) {
            console.warn('createTournament', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }
}
