const Direction = {
    Up: { x: 0, y: 1 },
    Down: { x: 0, y: -1 },
    Left: { x: -1, y: 0 },
    Right: { x: 1, y: 0 },
} as const

type DirectionType = (typeof Direction)[keyof typeof Direction]

export { Direction }
export default DirectionType

export function Invert(direction: DirectionType): DirectionType {
    switch (direction) {
        case Direction.Up:
            return Direction.Down
        case Direction.Down:
            return Direction.Up
        case Direction.Left:
            return Direction.Right
        case Direction.Right:
            return Direction.Left
    }

    throw new Error(`Unknown direction: ${direction}`)
}
