import { Button, Component, Node, _decorator, find } from 'cc'
import { Music } from '../../components/audio/Music'
import { OpacityToggler } from '../../components/common/OpacityToggler'
import { SwitchButton } from '../../components/switch-button/SwitchButton'
import DevTools from '../../constants/DevTools'
import { CodeSoundKeys } from '../../constants/SoundKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { BaseScreen } from '../BaseScreen'

const { ccclass, property, requireComponent } = _decorator

const { Events, Utils } = GameCore
const { player, event, match, storage } = globalThis.game

@ccclass('SettingsScreen')
@requireComponent(BaseScreen)
export class SettingsScreen extends Component {
    private screen!: BaseScreen

    @property(Node)
    public musicSetting!: Node

    @property(Node)
    public soundSetting!: Node

    @property(Node)
    public vibrateSetting!: Node

    @property(Button)
    private closeButtonComponent!: Button

    @property(Button)
    private continueButtonComponent!: Button

    @property(Button)
    private restartButtonComponent!: Button

    @property(Button)
    private inviteFriendsButtonComponent!: Button

    // ! For Unlocking DevTools
    private triggerSequence = ''

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
        this.listenUnlockDevTools()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
        this.unListenUnlockDevTools()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('SettingsScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.validateRequiredComponents()

        this.inviteFriendsButtonComponent.node.active = false
        if (Utils.Device.isDesktop()) {
            this.vibrateSetting.active = false
        }
    }

    private validateRequiredComponents() {
        if (!this.musicSetting) {
            throw new Error('SettingsScreen: musicSetting not found')
        }

        if (!this.soundSetting) {
            throw new Error('SettingsScreen: soundSetting not found')
        }

        if (!this.vibrateSetting) {
            throw new Error('SettingsScreen: vibrateSetting not found')
        }
    }

    private listenScreenEvents() {
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        console.log('SettingsScreen.OPENING_SCREEN', payload)
        if (!this.validateSwitchScreenPayload(payload)) return

        this.closeButtonComponent.interactable = true
        this.continueButtonComponent.interactable = true
        this.restartButtonComponent.interactable = true
        this.inviteFriendsButtonComponent.interactable = true
        this.updateStatus()
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateSwitchScreenPayload(payload)) return
        this.open()
    }

    private validateSwitchScreenPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    // * Music setting switch on/off will have a code: 0 for off, 1 for on
    // * Sound setting switch on/off will have a code: 2 for off, 3 for on
    private listenUnlockDevTools() {
        this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY)

        this.node.on(DevTools.Events.TRIGGER_RESET_SEQUENCE, this.handleResetSequence)
        this.node.on(DevTools.Events.TRIGGER_UNLOCK_KEY, this.handleTriggerUnlockKey)
    }

    private unListenUnlockDevTools() {
        this.node.off(DevTools.Events.TRIGGER_RESET_SEQUENCE, this.handleResetSequence)
        this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY, this.handleTriggerUnlockKey)
    }

    private handleResetSequence = () => {
        this.triggerSequence = ''
    }

    private handleTriggerUnlockKey = (payload: Object) => {
        if (!('code' in payload)) return

        const { code } = payload

        const { UNLOCK_SEQUENCE } = DevTools

        if (typeof code !== 'number') return
        if (UNLOCK_SEQUENCE.indexOf(code.toString()) === -1) return

        this.triggerSequence += code.toString()

        console.log(
            'SettingsScreen: handleTriggerUnlockKey',
            code,
            this.triggerSequence,
            this.triggerSequence === UNLOCK_SEQUENCE
        )

        if (this.triggerSequence === UNLOCK_SEQUENCE) {
            this.node.scene.emit(DevTools.Events.UNLOCK_DEV_TOOLS)
            this.node.off(DevTools.Events.TRIGGER_UNLOCK_KEY)
        }
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
        this.node.emit(DevTools.Events.TRIGGER_RESET_SEQUENCE)
    }

    public close() {
        this.closeButtonComponent.interactable = false
        this.continueButtonComponent.interactable = false
        this.restartButtonComponent.interactable = false
        this.inviteFriendsButtonComponent.interactable = false

        this.node.emit(DevTools.Events.TRIGGER_RESET_SEQUENCE)

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    private updateStatus() {
        const settings = {
            music: player.getPlayerSetting('music') ?? false,
            sound: player.getPlayerSetting('sound') ?? false,
            vibrate: player.getPlayerSetting('vibrate') ?? false,
        }

        const musicSwitch = this.musicSetting.getComponentInChildren(SwitchButton)
        const soundSwitch = this.soundSetting.getComponentInChildren(SwitchButton)

        if (!musicSwitch) {
            console.warn('SettingsScreen: musicSwitch not found')
            return
        }

        if (!soundSwitch) {
            console.warn('SettingsScreen: soundSwitch not found')
            return
        }

        musicSwitch.setEnabled(settings.music)
        soundSwitch.setEnabled(settings.sound)

        if (GameSDK.getSDKName() === 'GameDistributions') return

        if (!Utils.Device.isDesktop()) {
            this.scheduleOnce(this.updateVibrateButton, 0.1)
        }
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('SettingsScreen: screen not found')
        }
    }

    public switchMusicButton(): void {
        const musicSwitch = this.musicSetting.getComponentInChildren(SwitchButton)

        if (!musicSwitch) {
            console.warn('SettingsScreen: musicSwitch not found')
            return
        }

        const state = musicSwitch.switch()
        player.setSetting('music', state)

        // ! For Unlocking DevTools
        this.node.emit(DevTools.Events.TRIGGER_UNLOCK_KEY, { code: state ? 1 : 0 })

        if (state) {
            this.playMusic()
        } else {
            this.pauseMusic()
        }
    }

    public switchSoundButton(): void {
        const soundSwitch = this.soundSetting.getComponentInChildren(SwitchButton)

        if (!soundSwitch) {
            console.warn('SettingsScreen: soundSwitch not found')
            return
        }

        const state = soundSwitch.switch()
        player.setSetting('sound', state)

        // ! For Unlocking DevTools
        this.node.emit(DevTools.Events.TRIGGER_UNLOCK_KEY, { code: state ? 3 : 2 })
    }

    public switchVibrateButton(): void {
        const vibrateSwitch = this.vibrateSetting.getComponentInChildren(SwitchButton)

        if (!vibrateSwitch) {
            console.warn('SettingsScreen: vibrateSwitch not found')
            return
        }

        const state = vibrateSwitch.switch()
        player.setSetting('vibrate', state)
    }

    private updateVibrateButton = (): void => {
        const button = this.vibrateSetting.getComponentInChildren(Button)
        const switchState = this.vibrateSetting.getComponentInChildren(SwitchButton)
        const opacityState = this.vibrateSetting.getComponentInChildren(OpacityToggler)

        if (!button || !switchState || !opacityState) {
            console.warn('SettingsScreen: vibrate button missing components')
            return
        }

        if (!Utils.Device.isMobile()) {
            button.interactable = false
            switchState.setEnabled(false)
            opacityState.changeState(false)
        } else {
            const vibrate = player.getPlayerSetting('vibrate') ?? false
            // switchState.updateStatus(vibrate)
            switchState.setEnabled(vibrate)
        }
    }

    // ? Callback for click event of ContinueButton
    public handleClickedResume() {
        this.resumeGame()
    }

    // ? Callback for click event of CloseButton
    public handleClickedClose() {
        this.resumeGame()
    }

    // ? Callback for click event of ReplayButton
    public handleClickedReplayGame() {
        this.resumeGame()

        const { level = 1 } = player.getGameData() ?? {}
        this.node.scene.emit(GameplayEvents.GAME_REPLAY, {
            level,
        })
    }

    // ? Callback for click event of InviteFriendsButton
    public handleClickedInviteFriends() {
        // Not implemented
    }

    private resumeGame() {
        this.close()
        this.node.scene.emit(GameplayEvents.GAME_RESUME)
    }

    private pauseMusic(): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.pause(CodeSoundKeys.BG_MUSIC)
    }

    private playMusic(): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.play(CodeSoundKeys.BG_MUSIC)
    }

    private getMusicComponent(): Music | null {
        const music = find('Music')?.getComponent(Music)
        if (!music) {
            console.warn('SettingsScreen: Music not found')
            return null
        }

        return music
    }
}
