import { randomRange, tween } from 'cc'
import GeneralUtils from '../../../../utils/GeneralUtils'
import GameplayEvents from '../../../constants/GameEvents'
import { GridEvents } from '../../../constants/GridEvents'
import { getBestPath } from '../../../scripts/getBestPath'
import { Path, getValidPath } from '../../../scripts/getValidPath'
import Tile from '../../tile/Tile'
import Grid from '../Grid'
import { State } from '../GridMachine'
import Clear from './Clear'
import Idle from './Idle'

export default class Match extends State {
    private path: Path
    private match: [Tile, Tile]

    constructor(grid: Grid, tileDown: Tile, otherTile: Tile) {
        super(grid)
        this.path = getValidPath(grid, tileDown, otherTile)
        this.match = [tileDown, otherTile]
    }

    emitTileDown(_tile: Tile): void {
        // do nothing
    }

    awaitingInput(): boolean {
        return false
    }

    transition(): void {
        if (this.path.length === 0) {
            this.machine!.updateState(new Idle(this.grid))
            return
        }

        this.machine!.updateState(
            new Clear(this.grid, [
                {
                    tile1: this.match[0],
                    tile2: this.match[1],
                },
            ])
        )
    }

    public onEnter(): void {
        this.grid.node.emit(GridEvents.MATCH, this.match)

        if (this.path.length !== 0) {
            this.grid.setNormalMatches(this.grid.getNormalMatches() + 1)
        }

        if (this.path.length !== 0) {
            // GameManager.Instance.pathDrawManager.drawMatchPath(this.path)
            // GameManager.Instance.comboManager.addCombo()
            this.grid.node.scene.emit(GameplayEvents.GRID_MATCH, this.path, this.match)
            this.grid.node.scene.emit(GridEvents.MATCH_SUCCESS, this.match)
        } else {
            GeneralUtils.playSound('sounds/invalid_match')

            const path = getBestPath(this.grid, this.match[0], this.match[1])

            if (path.length > 0) {
                /*
                GameManager.Instance.pathDrawManager.drawErrorPath(path)
                */
            }

            this.shakeGrid()

            this.grid.node.emit(GridEvents.MATCH_FAIL, this.match)
        }

        this.transition()
    }

    public onExit(): void {
        // do nothing
    }

    private shakeGrid(): Promise<void> {
        // if (GameManager.Instance.playerDataManager.getSettings()[2] && navigator.vibrate)
        //     navigator.vibrate(100)

        const defaultPos = this.grid.node.position.clone()

        const shakeTween = tween(this.grid.node)
            .to(0.05, {
                position: defaultPos.clone().add3f(randomRange(5, 10), randomRange(5, 10), 0),
            })
            .to(0.05, {
                position: defaultPos.clone().subtract3f(randomRange(5, 10), randomRange(5, 10), 0),
            })

        return new Promise((resolve) => {
            tween(this.grid.node)
                .repeat(2, shakeTween)
                .to(
                    0.05,
                    {
                        position: defaultPos,
                    },
                    {
                        onComplete: () => {
                            resolve()
                        },
                    }
                )
                .start()
        })
    }
}
