import { CCFloat, Component, UIOpacity, _decorator, tween } from 'cc'
import DashboardScreens from '../../../../constants/DashboardScreens'
import CollectionSceneEvents from '../../../../gameplay/constants/CollectionSceneEvents'
import { NavManager } from './footer/nav-button/NavManager'
const { ccclass, property } = _decorator

@ccclass('DashboardUI')
export class DashboardUI extends Component {
    @property({ type: DashboardScreens })
    protected screenType: DashboardScreens = DashboardScreens.home

    @property({ type: CCFloat, min: 0 })
    protected fadeInDuration = 0.5

    @property({ type: CCFloat, min: 0 })
    protected fadeOutDuration = 0.5

    @property(UIOpacity)
    protected screenContainer!: UIOpacity

    @property(NavManager)
    protected navigation!: NavManager

    public __preload(): void {
        // called before onLoad, use for initialization
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    public start(): void {
        this.transitionIn()
    }

    public transitionIn(): void {
        tween(this.screenContainer)
            .call(() => (this.screenContainer.opacity = 0))
            .to(this.fadeInDuration, { opacity: 255 })
            .call(() => {
                this.node.scene.emit(CollectionSceneEvents.FADE_IN_COMPLETE)
            })
            .start()
    }

    public handleNavigation(screenType: NavigatePayload[0]) {
        if (screenType === this.screenType) {
            return
        }

        this.transitionOut()
    }

    public transitionOut(): void {
        tween(this.screenContainer).to(this.fadeOutDuration, { opacity: 0 }).start()
    }

    protected validateRequiredComponents() {
        if (!this.screenContainer) {
            throw new Error('DashboardUI: Screen container not found')
        }

        if (!this.navigation) {
            throw new Error('DashboardUI: Navigation not found')
        }
    }
}
