import { TileType } from '../../../constants/TileType'
import StateMachine from '../../grid/StateMachine'
import Tile from '../Tile'
import BasicState from './BasicState'
import SpecialState from './SpecialState'

export default class SpecialStateMachine extends StateMachine<SpecialState> {
    constructor(tile: Tile) {
        super()
        this.currentState = new BasicState(tile)
    }

    accept(other: Tile): boolean {
        return this.currentState.accept(other)
    }

    get tile(): Tile {
        return this.currentState.tile
    }

    set tile(value: Tile) {
        this.currentState.tile = value
    }

    clear(other?: Tile): Promise<[Tile, Tile][]> {
        return this.currentState.clear(other)
    }

    onEnter(): void {
        return this.currentState.onEnter()
    }

    update(dt: number): void {
        return this.currentState.update(dt)
    }

    onExit(): void {
        return this.currentState.onExit()
    }

    getType(): TileType {
        return this.currentState.getType()
    }
}
