import Tile from '../../tile/Tile'
import Grid from '../Grid'
import { State } from '../GridMachine'
import Gravity from './Gravity'
import Shuffle from './Shuffle'

export default class Spawn extends State {
    private spawned: boolean

    constructor(grid: Grid) {
        super(grid)
        this.spawned = false
    }

    emitTileDown(_tile: Tile): void {
        // do nothing
    }

    awaitingInput(): boolean {
        return false
    }

    transition(): void {
        if (this.spawned) {
            this.machine!.updateState(new Gravity(this.grid, this.grid.gravityList))
        } else {
            this.machine!.updateState(new Shuffle(this.grid))
        }
    }

    public onEnter(): void {
        if (this.grid.getNormalMatches() < 5) {
            this.spawned = false
            return this.transition()
        }

        const timesSpawnedAlready = Math.ceil((this.grid.getNormalMatches() - 5) / 2)
        // TODO: Hard code right here
        const addRandomTilesPerMove = 0
        // const { addRandomTilesPerMove } = this.grid.levelManager.getLevelData().rules

        if (timesSpawnedAlready >= addRandomTilesPerMove) {
            this.spawned = false
            return this.transition()
        }

        if (this.grid.getNormalMatches() % 2 === 0) {
            this.spawned = false
            return this.transition()
        }

        const frame = this.grid.frames[Math.floor(Math.random() * this.grid.frames.length)]
        const randomPair = this.grid.getRandomInactivePair()

        if (!randomPair) {
            this.spawned = false
            return this.transition()
        }
        // AudioSystem.Instance.playSound(AudioKeys.EXTRA_TILE_SPAWN)
        this.spawned = true
        Promise.all([randomPair[0].restore(frame), randomPair[1].restore(frame)]).then(() => {
            this.transition()
        })
    }

    public onExit(): void {
        // do nothing
    }
}
