import { Vec2 } from 'cc'
import Grid from '../game-objects/grid/Grid'
import Tile from '../game-objects/tile/Tile'

type IGrid = Grid
type ITile = Tile

export type Line = { a: Vec2Like; b: Vec2Like }
export type Path = Line[]

class PaddingTile implements IBaseTile {
    coord: Vec2
    width!: number
    height!: number

    constructor(x: number, y: number) {
        this.coord = new Vec2(x, y)
    }

    getCoord(): Vec2 {
        return this.coord
    }

    setCoord(x: number, y: number): void {
        this.coord.set(x, y)
    }

    isTraversable(): boolean {
        return true
    }
}

export function getValidPath(grid: IGrid, tile1: ITile, tile2: ITile): Path {
    const startCoord = tile1.getCoord().clone().add2f(1, 1)
    const endCoord = tile2.getCoord().clone().add2f(1, 1)

    const nodeGrid: IBaseTile[][] = grid.getTileGrid().map((row) => row.map((tile) => tile))

    const matrix = pad2DArray(nodeGrid)

    const exceptionTiles = [tile1, tile2]

    if (
        (startCoord.x === endCoord.x &&
            checkLineX(matrix, exceptionTiles, startCoord.y, endCoord.y, startCoord.x)) ||
        (startCoord.y === endCoord.y &&
            checkLineY(matrix, exceptionTiles, startCoord.x, endCoord.x, startCoord.y))
    ) {
        return pathOffset([{ a: startCoord, b: endCoord }], { x: -1, y: -1 })
    }

    // Check in a rectangle with x-axis or y-axis
    let t

    const pMinY = startCoord.y <= endCoord.y ? startCoord : endCoord
    const pMaxY = startCoord.y <= endCoord.y ? endCoord : startCoord
    const pMinX = startCoord.x <= endCoord.x ? startCoord : endCoord
    const pMaxX = startCoord.x <= endCoord.x ? endCoord : startCoord

    let moreLinePath: Path = []
    let rectPath: Path = []

    // Check more right or more left
    if (
        (t = checkMoreLineX(matrix, exceptionTiles, 1, pMinY, pMaxY)) !== -1 ||
        (t = checkMoreLineX(matrix, exceptionTiles, -1, pMinY, pMaxY)) !== -1
    ) {
        moreLinePath = [
            { a: startCoord, b: { x: startCoord.x, y: t } },
            { a: { x: startCoord.x, y: t }, b: { x: endCoord.x, y: t } },
            { a: { x: endCoord.x, y: t }, b: endCoord },
        ]
    }

    // Check more down or more up
    if (
        (t = checkMoreLineY(matrix, exceptionTiles, 1, pMinX, pMaxX)) !== -1 ||
        (t = checkMoreLineY(matrix, exceptionTiles, -1, pMinX, pMaxX)) !== -1
    ) {
        moreLinePath = [
            { a: startCoord, b: { x: t, y: startCoord.y } },
            { a: { x: t, y: startCoord.y }, b: { x: t, y: endCoord.y } },
            { a: { x: t, y: endCoord.y }, b: endCoord },
        ]
    }

    // Check in a rectangle with x-axis and y-axis
    if ((t = checkRectX(matrix, exceptionTiles, pMinY, pMaxY)) !== -1) {
        rectPath = [
            { a: startCoord, b: { x: startCoord.x, y: t } },
            { a: { x: startCoord.x, y: t }, b: { x: endCoord.x, y: t } },
            { a: { x: endCoord.x, y: t }, b: endCoord },
        ]
    } else if ((t = checkRectY(matrix, exceptionTiles, pMinX, pMaxX)) !== -1) {
        rectPath = [
            { a: startCoord, b: { x: t, y: startCoord.y } },
            { a: { x: t, y: startCoord.y }, b: { x: t, y: endCoord.y } },
            { a: { x: t, y: endCoord.y }, b: endCoord },
        ]
    }

    if (moreLinePath.length > 0 && rectPath.length > 0) {
        return pathOffset(
            getPathLength(moreLinePath) <= getPathLength(rectPath) ? moreLinePath : rectPath,
            { x: -1, y: -1 }
        )
    } else if (moreLinePath.length > 0) {
        return pathOffset(moreLinePath, { x: -1, y: -1 })
    } else if (rectPath.length > 0) {
        return pathOffset(rectPath, { x: -1, y: -1 })
    }

    return []
}

function getPathLength(path: Path): number {
    let length = 0

    for (const line of path) {
        length += getLineLength(line)
    }

    return length
}

function getLineLength(line: Line): number {
    if (line.a.x === line.b.x) {
        return Math.abs(line.a.y - line.b.y)
    } else if (line.a.y === line.b.y) {
        return Math.abs(line.a.x - line.b.x)
    }

    return 0
}

function isTraversable(tile: IBaseTile, exceptionTiles: ITile[] = []): boolean {
    return tile.isTraversable() || exceptionTiles.some((exceptionTile) => exceptionTile === tile)
}

function checkLineX(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    y1: number,
    y2: number,
    x: number
): boolean {
    const min = Math.min(y1, y2)
    const max = Math.max(y1, y2)
    // run column
    for (let y = min; y <= max; y++) {
        const tile = matrix[y][x]
        if (!isTraversable(tile, exceptionTiles)) {
            return false
        }
    }
    return true
}

function checkLineY(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    x1: number,
    x2: number,
    y: number
): boolean {
    const min = Math.min(x1, x2)
    const max = Math.max(x1, x2)
    for (let x = min; x <= max; x++) {
        const tile = matrix[y][x]
        if (!isTraversable(tile, exceptionTiles)) {
            return false
        }
    }
    return true
}

function checkRectX(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    pMinY: Vec2Like,
    pMaxY: Vec2Like
): number {
    for (let y = pMinY.y + 1; y < pMaxY.y; y++) {
        // check three line
        if (
            checkLineX(matrix, exceptionTiles, pMinY.y, y, pMinY.x) &&
            checkLineY(matrix, exceptionTiles, pMinY.x, pMaxY.x, y) &&
            checkLineX(matrix, exceptionTiles, y, pMaxY.y, pMaxY.x)
        ) {
            return y
        }
    }
    // have a line in three line not true then return -1
    return -1
}

function checkRectY(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    pMinX: Vec2Like,
    pMaxX: Vec2Like
) {
    // find line and y begin
    for (let x = pMinX.x + 1; x < pMaxX.x; x++) {
        if (
            checkLineY(matrix, exceptionTiles, pMinX.x, x, pMinX.y) &&
            checkLineX(matrix, exceptionTiles, pMinX.y, pMaxX.y, x) &&
            checkLineY(matrix, exceptionTiles, x, pMaxX.x, pMaxX.y)
        ) {
            return x
        }
    }
    return -1
}

function checkMoreLineX(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    type: number,
    pMinY: Vec2Like,
    pMaxY: Vec2Like
): number {
    // find line and y begin
    let y = type === 1 ? pMaxY.y : pMinY.y
    const row = type === 1 ? pMinY.x : pMaxY.x
    // check more
    if (checkLineX(matrix, exceptionTiles, pMinY.y, pMaxY.y, row)) {
        while (
            y >= 0 &&
            y < matrix.length &&
            isTraversable(matrix[y][pMinY.x], exceptionTiles) &&
            isTraversable(matrix[y][pMaxY.x], exceptionTiles)
        ) {
            if (checkLineY(matrix, exceptionTiles, pMinY.x, pMaxY.x, y)) {
                return y
            }
            y += type
        }
    }
    return -1
}

function checkMoreLineY(
    matrix: IBaseTile[][],
    exceptionTiles: ITile[],
    type: number,
    pMinX: Vec2Like,
    pMaxX: Vec2Like
): number {
    let x = type === 1 ? pMaxX.x : pMinX.x
    const col = type === 1 ? pMinX.y : pMaxX.y

    if (checkLineY(matrix, exceptionTiles, pMinX.x, pMaxX.x, col)) {
        while (
            x >= 0 &&
            x < matrix[0].length &&
            isTraversable(matrix[pMinX.y][x], exceptionTiles) &&
            isTraversable(matrix[pMaxX.y][x], exceptionTiles)
        ) {
            if (checkLineX(matrix, exceptionTiles, pMinX.y, pMaxX.y, x)) {
                return x
            }
            x += type
        }
    }
    return -1
}

export function pathOffset(path: Path, by: Vec2Like | Vec2): Path {
    return path.map((line) => {
        return {
            a: {
                x: line.a.x + by.x,
                y: line.a.y + by.y,
            },
            b: {
                x: line.b.x + by.x,
                y: line.b.y + by.y,
            },
        }
    })
}

function pad2DArray(array: IBaseTile[][]): IBaseTile[][] {
    const paddedArray: IBaseTile[][] = []

    // Calculate the new dimensions after padding
    const numRows = array.length + 2
    const numCols = array[0].length + 2

    // Fill the new array with padding elements
    for (let i = 0; i < numRows; i++) {
        paddedArray[i] = []
        for (let j = 0; j < numCols; j++) {
            paddedArray[i][j] = new PaddingTile(j - 1, i - 1)
        }
    }

    // Copy the original array into the center of the padded array
    for (let i = 0; i < array.length; i++) {
        for (let j = 0; j < array[i].length; j++) {
            paddedArray[i + 1][j + 1] = array[i][j]
        }
    }

    return paddedArray
}
