import { Component, SpriteFrame, _decorator, math, resources } from 'cc'
import GameplayEvents from '../constants/GameEvents'
import LevelData from '../constants/LevelData'
import LevelThemes from '../constants/LevelThemes'
import ResourcePaths from '../constants/ResourcePaths'

const { player } = globalThis.game
const { ccclass, property } = _decorator

@ccclass('LevelManager')
class LevelManager extends Component {
    @property([SpriteFrame])
    private defaultTheme: SpriteFrame[] = []

    public themeName!: string

    private levelData!: ILevelData
    private firstLoad!: boolean

    public __preload(): void {
        this.validateRequiredComponents()

        if (this.firstLoad) return

        this.firstLoad = true
        for (const theme of LevelThemes) {
            const themePath = `${ResourcePaths.LEVEL_THEME_FOLDER}/${theme}`
            resources.loadDir(themePath, SpriteFrame)
        }
    }

    private validateRequiredComponents() {
        if (this.defaultTheme.length === 0) {
            throw new Error('<LevelManager>: defaultTheme not found')
        }
    }

    public async loadLevel(level?: number, theme?: string): Promise<void> {
        level ? player.setGameData({ level: level }) : (level = 1)

        this.levelData = LevelData[level - 1]

        const frames = await this.requestTheme(theme)

        this.node.scene.emit(GameplayEvents.LEVEL_THEME_LOADED, frames)
    }

    private async requestTheme(theme?: string): Promise<SpriteFrame[]> {
        this.themeName = this.levelData.theme[math.randomRangeInt(0, this.levelData.theme.length)]

        if (theme) {
            this.themeName = theme
        }

        const themePath = `${ResourcePaths.LEVEL_THEME_FOLDER}/${this.themeName}`
        const assetsInfo = resources.getDirWithPath(themePath, SpriteFrame)
        if (assetsInfo.length > 0 && resources.get(assetsInfo[0].path, SpriteFrame) !== null) {
            const assets: SpriteFrame[] = []
            assetsInfo.forEach((assetInfo) => {
                const asset = resources.get(assetInfo.path, SpriteFrame)
                if (asset instanceof SpriteFrame) {
                    assets.push(asset)
                }
            })
            return Promise.resolve(assets)
        }

        return new Promise((resolve) => {
            resources.loadDir(
                `${ResourcePaths.LEVEL_THEME_FOLDER}/${this.themeName}`,
                SpriteFrame,
                (err: Error | null, assets: SpriteFrame[]) => {
                    if (err || assets.length === 0) {
                        resolve(this.defaultTheme)
                        return
                    }

                    resolve(assets)
                }
            )
        })
    }

    public getLevelData(): ILevelData {
        return this.levelData
    }
}

export default LevelManager
