import { EventTarget } from 'cc'

import GameplayEvents from '../../../constants/GameEvents'
import { GridEvents } from '../../../constants/GridEvents'
import Tile, { ClearFX } from '../../tile/Tile'
import Grid from '../Grid'
import { State } from '../GridMachine'
import Gravity from './Gravity'

export const ClearEvent = {
    CLEAR_START: 'CLEAR_START',
    CLEAR_PAIR: 'CLEAR_PAIR',
    CLEAR_END: 'CLEAR_END',
} as const

export type ClearEvent = typeof ClearEvent[keyof typeof ClearEvent]

type ClearPair = {
    tile1: Tile
    tile2: Tile
    fx?: ClearFX
    propogateFx?: boolean
}

type ClearConfig = ClearPair[]

export default class Clear extends State {
    private configs: ClearConfig
    readonly eventEmitter: EventTarget

    constructor(grid: Grid, configs: ClearConfig) {
        super(grid)
        this.configs = configs
        this.eventEmitter = new EventTarget()
    }

    emitTileDown(_tile: Tile): void {
        // do nothing
    }

    awaitingInput(): boolean {
        return false
    }

    transition(): void {
        this.machine!.updateState(new Gravity(this.grid, this.grid.gravityList))
    }

    public onEnter(): void {
        this.grid.node.emit(GridEvents.CLEAR, this.configs)
        this.eventEmitter.emit(ClearEvent.CLEAR_START)

        this.clearQueue().then(() => {
            const uncleared = this.grid.getUncleared()

            if (uncleared.length === 0) {
                this.grid.node.emit(GridEvents.CLEAR_ALL, this.grid)
                this.grid.node.scene.emit(GameplayEvents.GAME_FINISH)
            }

            this.eventEmitter.emit(ClearEvent.CLEAR_END)
            this.transition()
        })
    }

    public onExit(): void {
        // do nothing
    }

    private async clearQueue(): Promise<void> {
        while (this.configs.length > 0) {
            const {
                tile1,
                tile2,
                fx = 'clear',
                propogateFx: propogateAnimation = false,
            } = this.configs.shift()!

            const pairs = await Promise.all([
                tile1.setEmpty(tile2, false, fx),
                tile2.setEmpty(tile1, false, fx),
            ])

            this.configs.push(
                ...pairs[0].map((pair) => ({
                    tile1: pair[0],
                    tile2: pair[1],
                    animation: propogateAnimation ? fx : 'clear',
                    propogateAnimation,
                }))
            )

            this.configs.push(
                ...pairs[1].map((pair) => ({
                    tile1: pair[0],
                    tile2: pair[1],
                    animation: propogateAnimation ? fx : 'clear',
                    propogateAnimation,
                }))
            )

            this.grid.node.emit(GridEvents.CLEAR, tile1, tile2)
            this.eventEmitter.emit(ClearEvent.CLEAR_PAIR, tile1, tile2)
        }
    }
}
