import { Component, _decorator } from 'cc'
import { SceneManager } from '../../components/SceneManager'
import ScreenKeys from '../../constants/ScreenKeys'

const { ccclass, disallowMultiple, requireComponent } = _decorator

const { Events } = GameCore
const { event } = globalThis.game

@ccclass('TestScene')
@disallowMultiple
@requireComponent(SceneManager)
export class TestScene extends Component {
    private scene!: SceneManager

    public __preload() {
        this.scene = this.getComponent(SceneManager)!

        this.validateRequiredComponents()

        // ? Listen events by plugin Event of GameCore
        this.scene.listenSwitchSceneEvent(this.handleSwitchSceneEvent)
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('GameScene: SceneManager not found')
        }
    }

    public onLoad() {
        this.listenScreenEvents()

        // TODO: add demo play sound in buttons, screen,..
    }

    private listenScreenEvents() {
        event.once(Events.CLOSED_SCREEN, (payload: SwitchScreenPayload) => {
            const { screenName } = payload
            if (screenName !== ScreenKeys.LOADING_SCREEN) return

            // event.emit(Events.OPEN_SCREEN, {
            //     screenName: ScreenKeys.NOTIFICATION_SCREEN,
            //     screenData: {
            //         message: 'Test screen events pass!',
            //         duration: 2,
            //         isCloseButtonVisible: false,
            //     },
            // })
        })
    }

    public start() {
        console.log('TestScene.start')

        // event.emit(Events.OPEN_SCREEN, {
        //     screenName: ScreenKeys.LOADING_SCREEN,
        //     screenData: { duration: 0.5 },
        // })
    }

    private handleSwitchSceneEvent = (payload: SwitchScenePayload) => {
        const { sceneName, sceneData } = payload
        this.scene?.switchScene(sceneName, sceneData)
    }
}
