import { Component, Mask, View, _decorator, find } from 'cc'
import { SceneManager } from '../../components/SceneManager'
import Ads from '../../constants/Ads'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
import CocosUtils from '../../utils/CocosUtils'
import GeneralUtils from '../../utils/GeneralUtils'

const { ccclass, disallowMultiple, requireComponent } = _decorator

const { audio, ads } = globalThis.game
const { Layout, DesignSize } = GameWorld
const { Portrait } = DesignSize

@ccclass('GameScene')
@disallowMultiple
@requireComponent([SceneManager])
export class GameScene extends Component {
    private scene!: SceneManager

    public __preload() {
        this.scene = this.getComponent(SceneManager)!

        this.validateRequiredComponents()

        this.listenGameEvents()
        this.scene.listenSwitchSceneEvent(this.handleSwitchSceneEvent)

        GeneralUtils.showBannerAds()
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('GameScene: SceneManager not found')
        }
    }

    private listenGameEvents() {
        this.node.scene.on(GameEvents.LAYOUT_CHANGED, this.handleGameMaskWhenLayoutChanged)
        this.node.scene.on(GameEvents.VIEW_RESIZE, this.handleAdsWhenResize)
    }

    private handleSwitchSceneEvent = (payload: SwitchScenePayload) => {
        const { sceneName, sceneData } = payload
        this.scene.switchScene(sceneName, sceneData)
    }

    private handleGameMaskWhenLayoutChanged = (layout: string) => {
        const gameObjectsNode = find('Canvas/Game/GameObjects')
        if (!gameObjectsNode) return

        const maskComponent = gameObjectsNode.getComponent(Mask)
        if (!maskComponent) return

        maskComponent.enabled = layout === Layout.Landscape
    }

    private handleAdsWhenResize = () => {
        this.checkBottomBannerAdSize()
        this.setMaxSizeSideBannerAd()
    }

    private checkBottomBannerAdSize() {
        const worldGameSize = View.instance.getVisibleSize()
        const bannerRemainSizeCss = CocosUtils.worldSizeToCssSize(
            worldGameSize.height * Ads.MAX_BOTTOM_BANNER_RATIO
        )

        const { BannerDisplayAdOptions } = GameCore.Configs.Ads

        BannerDisplayAdOptions.forEach(({ PlacementId, Position, BannerHeight }) => {
            if (Position !== 'bottom') return

            if (BannerHeight > bannerRemainSizeCss) {
                ads.hideBannerAdAsync(PlacementId).catch((_) => {
                    // Safe to ignore
                })
            } else {
                ads.showBannerAdAsync(PlacementId).catch((_) => {
                    // Safe to ignore
                })
            }
        })
    }

    private setMaxSizeSideBannerAd() {
        const { extra } = GameSDK
        if (!('setSideBannerAdRemainSize' in extra)) return

        const worldGameSize = View.instance.getVisibleSize()
        const contentWorldWidth = Portrait.WIDTH + 12 * 2 // 12 is block line
        const bannerRemainSize = (worldGameSize.width - contentWorldWidth) / 2
        const bannerRemainSizeCss = CocosUtils.worldSizeToCssSize(bannerRemainSize)
        extra.setSideBannerAdRemainSize(bannerRemainSizeCss)
    }

    public start() {
        // ? Show subscribe bot popup of facebook instant game
        this.scheduleOnce(this.handleSubscribeBot, 0.5)

        this.updateVolumeBackgroundMusic()
    }

    private updateVolumeBackgroundMusic() {
        // ? Channel 'global_music' has been created in 'LoadScene' as a persistent node for playing background music across all scenes.
        audio.setVolume(1, 'global_music')
    }

    private handleSubscribeBot = async () => {
        if (!('subscribeBotAsync' in GameSDK.extra)) return

        // TODO: need to confirm canbe call subscribeBotAsync before startGameAsync
        await GameSDK.extra.subscribeBotAsync()
    }
}
