import { CCString, Component, director, _decorator } from 'cc'
import AudioChannel from './AudioChannel'
const { ccclass, property } = _decorator

const { audio, player } = globalThis.game

@ccclass('Music')
export class Music extends Component {
    @property(CCString)
    public channelId = 'global_music'

    private channel: AudioChannel | null = null

    public __preload() {
        // * Only call new a class (not component) in _preload method
        this.channel = new AudioChannel(this.node, this.channelId)
        audio.addChannel(this.channel)
    }

    // ? Sometime music background must be play in all scenes
    public makePersistent() {
        director.addPersistRootNode(this.node)
    }

    public removePersistent() {
        director.removePersistRootNode(this.node)
    }

    public play(
        key: GameCore.Plugins.Audio.SoundKeys,
        options?: GameCore.Plugins.Audio.PlayConfig
    ): void {
        if (!this.isMusicEnable()) return

        if (!options || !options.loop) {
            options = {
                ...options,
                loop: true,
            }
        }

        audio.play(key, options, this.channelId)
    }

    private isMusicEnable(): boolean {
        return !!player.getPlayerSetting('music')
    }

    public setVolume(volume: number) {
        audio.setVolume(volume, this.channelId)
    }

    public pause(key: GameCore.Plugins.Audio.SoundKeys) {
        audio.pause(key, this.channelId)
    }

    public resume(key: GameCore.Plugins.Audio.SoundKeys) {
        if (!this.isMusicEnable()) return

        audio.resume(key, this.channelId)
    }

    public stop(key: GameCore.Plugins.Audio.SoundKeys) {
        audio.stop(key, this.channelId)
    }

    public mute(isMute: boolean) {
        if (isMute) {
            audio.mute(this.channelId)
        } else {
            audio.unmute(this.channelId)
        }
    }
}
