import { Node as CCNode, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
import GeneralUtils from '../../../utils/GeneralUtils'
import { Gameplay } from '../../Gameplay'
import GameplayEvents from '../../constants/GameEvents'
import { GridEvents } from '../../constants/GridEvents'
import ItemKeys from '../../constants/ItemKeys'
import { UIManager } from '../../manager/UIManager'
import findFirstMatch from '../../scripts/findFirstMatch'
import Grid from '../grid/Grid'
import { GridMachineEvent } from '../grid/GridMachine'
import { PathDraw } from '../path-draw/PathDraw'
import { SpecialItem } from './SpecialItem'

const { ccclass } = _decorator
const { player, event } = globalThis.game
const { Events } = GameCore

@ccclass('HintItem')
export default class HintItem extends SpecialItem {
    private gridInstance!: Grid
    private pathDrawer!: PathDraw
    private uiManager!: UIManager

    private isDrawingHint = false

    public override init(gameplay: Gameplay) {
        this.gridInstance = gameplay.grid
        this.pathDrawer = gameplay.pathDraw
        this.uiManager = gameplay.uiManager

        this.setQuantity(this.loadQuantity())

        this.node.scene.on(
            GameplayEvents.ADD_HINT_ITEMS,
            (quantity: number) => {
                this.setQuantity(quantity)
                this.uiManager.playEntranceAnimUI()
            },
            this
        )

        this.gridInstance.node.on(GridEvents.CREATE, this.applyActivationStatus, this)

        this.gridInstance.node.on(GridEvents.IDLE_EXIT, () => {
            // simply disable button. Animation will be too distracting
            this.button.interactable = false
        })

        this.gridInstance.node.on(GridEvents.IDLE_ENTER, () => {
            // simply disable button. Animation will be too distracting
            this.button.interactable = true
            this.applyActivationStatus()
        })
    }

    protected override onDestroy(): void {
        this.node.scene.targetOff(this)

        const gridNode = this.gridInstance.node as CCNode | null | undefined

        if (gridNode === null || gridNode === undefined) {
            super.onDestroy?.()
            return
        }

        this.gridInstance.node.targetOff(this)
        super.onDestroy?.()
    }

    protected override validateRequiredComponents(): void {
        super.validateRequiredComponents()
        if (!this.gridInstance) {
            throw new Error(`${this.node.name}: gridInstance not found`)
        }

        if (!this.pathDrawer) {
            throw new Error(`${this.node.name}: pathDrawer not found`)
        }
    }

    public override canActivate(): boolean {
        const uncleared = this.gridInstance.getUncleared()
        return uncleared.length > 0
    }

    public override async activate(): Promise<void> {
        if (this.isDrawingHint) return

        if (this.getQuantity() <= 0) {
            await super.activate()
            return
        }

        await super.activate()

        this.isDrawingHint = true

        this.node.scene.emit(GameplayEvents.USE_HINT_ITEM)

        const path = findFirstMatch(this.gridInstance)

        if (path) {
            GeneralUtils.playSound('sounds/hint_item')

            this.pathDrawer.drawHintPath(path)

            const start = path[0].a
            const end = path.slice(-1)[0].b

            const startTile = this.gridInstance.getTileAt(start.x, start.y)
            const endTile = this.gridInstance.getTileAt(end.x, end.y)

            if (!startTile || !endTile) throw new Error('Tile is null')

            startTile.setHint(true)
            endTile.setHint(true)
        }

        this.gridInstance.node.once(GridMachineEvent.STATE_CHANGE, this.hideHint)
        this.gridInstance.node.once(GridEvents.TILE_SELECT, this.hideHint)
        this.node.scene.once(GameplayEvents.USE_STORM_ITEM, this.hideHint)
        this.node.scene.once(GameplayEvents.USE_THUNDER_ITEM, this.hideHint)
        this.node.scene.once(GameplayEvents.USE_PLIERS_ITEM, this.hideHint)
    }

    public requestItem = () => {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.GET_ITEM_SCREEN,
            screenData: {
                item: ItemKeys.HINT,
                quantity: 1,
            },
        })
    }

    public loadQuantity = () => {
        const gameData = player.getGameData()
        return gameData ? gameData.items[ItemKeys.HINT] : 0
    }

    public saveQuantity = () => {
        const gameData = player.getGameData()
        if (!gameData) return

        player.setGameData({
            items: {
                ...gameData.items,
                [ItemKeys.HINT]: this.getQuantity(),
            },
        })
    }

    private hideHint = () => {
        this.pathDrawer.clearHintPath()
        this.isDrawingHint = false
    }
}
