import { Component, Label, _decorator } from 'cc'
import { SceneManager } from '../../components/SceneManager'
import DashboardScreens from '../../constants/DashboardScreens'
import ScreenKeys from '../../constants/ScreenKeys'
import { CoinManager } from '../../gameplay/manager/CoinManager'
import { ThemeManager } from '../../gameplay/manager/ThemeManager'
import { NavManager } from './components/ui/footer/nav-button/NavManager'
import { JourneyProgressAnimator } from './components/ui/journey-progress/JourneyProgressAnimator'
const { ccclass, disallowMultiple, requireComponent, property } = _decorator

const { language, match, event, audio, storage, player } = globalThis.game
const { Events } = GameCore

export interface SwitchScreenPayload {
    screenName: DashboardScreens
}

// SceneComponent
// Required components: SceneManager

let isFirstTimeGoToGameScene = true

@ccclass('DashboardScene')
@disallowMultiple
@requireComponent(SceneManager)
export class DashboardScene extends Component {
    private scene!: SceneManager

    @property(ThemeManager)
    private themeManager!: ThemeManager

    @property(CoinManager)
    private coinManager!: CoinManager

    @property(JourneyProgressAnimator)
    private journeyProgress!: JourneyProgressAnimator

    @property(NavManager)
    private navManager!: NavManager

    @property(Label)
    private playButtonLabel!: Label

    public __preload() {
        this.scene = this.getComponent(SceneManager)!

        this.scene.preloadScene('CollectionsScene')
        this.validateRequiredComponents()

        this.scene.listenSwitchSceneEvent(this.handleSwitchSceneEvent)
    }

    public start() {
        this.themeManager.init()
        this.coinManager.init()

        this.updateVolumeBackgroundMusic()
        this.updateMapUnlockedProgress()
        return
    }

    public handlePlayButtonPress() {
        const playerId = player.getPlayerId()

        match.single.start
            .processAsync({
                playerId,
            })
            .then(() => {
                const mapPayload = this.themeManager.getCurrentProgress()

                storage.setStorageData('dashboard', 'lastMapPayload', mapPayload)
                storage.setStorageData('dashboard', 'animateToCurrentLevel', false)

                //TODO: Remove this when we have another method
                if (isFirstTimeGoToGameScene) {
                    event.emit(GameCore.Events.OPEN_SCREEN, {
                        screenName: ScreenKeys.LOADING_SCREEN,
                    })
                    isFirstTimeGoToGameScene = false
                }

                event.emit(Events.SWITCH_SCENE, {
                    sceneName: 'GameScene',
                })
            })
    }

    private setProgressAndMap(payload: CurrentMapProgressPayload) {
        const { currentMap, nextMap, progress, max } = payload

        this.journeyProgress.setCurrentLevelAvatar(currentMap)

        // ! Handle case where nextMap is undefined. (i.e. last map)
        this.journeyProgress.setNextLevelAvatar(nextMap ?? currentMap)

        this.journeyProgress.setRange(progress, max)
    }

    private async setProgressAndAnimateMap(
        fromProgress: number,
        toPayload: CurrentMapProgressPayload
    ) {
        this.setProgressAndMap({
            ...toPayload,
            progress: fromProgress,
        })

        await this.journeyProgress.animateTo(toPayload.progress)
    }

    private async setProgressAndAnimateLevelUp(
        lastMapPayload: CurrentMapProgressPayload,
        nextMapPayload: CurrentMapProgressPayload
    ): Promise<void> {
        this.setProgressAndMap(lastMapPayload)

        const lastMap = lastMapPayload.nextMap ?? nextMapPayload.currentMap
        const nextMap = nextMapPayload.nextMap ?? nextMapPayload.currentMap

        await this.journeyProgress.animateToNextLevel(
            nextMapPayload.progress,
            nextMapPayload.max,
            lastMap,
            nextMap
        )
    }

    private updateLevelText(level: number) {
        const levelText = language.getText('level')
        this.playButtonLabel.string = levelText + ` ${level}`
    }

    private async updateMapUnlockedProgress() {
        const level = player.getGameData()?.level ?? 1
        this.updateLevelText(level)

        const dashboardStorage = storage.getStorage('dashboard')

        if (dashboardStorage === null || dashboardStorage === undefined) {
            throw new Error('DashboardScene: dashboardStorage not found')
        }

        const lastMap = dashboardStorage.lastMapPayload
        const currMap = this.themeManager.getCurrentProgress()

        if (!dashboardStorage.animateToCurrentLevel) {
            this.setProgressAndMap(currMap)
            this.updateCurrentMapInStorage(currMap)
            return
        }

        if (this.isSameMapProgress(lastMap, currMap)) {
            await this.setProgressAndAnimateMap(lastMap.progress, currMap)
            this.updateCurrentMapInStorage(currMap)
            return
        }

        await this.setProgressAndAnimateLevelUp(lastMap, currMap)
        this.updateCurrentMapInStorage(currMap)
        await this.navManager.selectByType(DashboardScreens.collections)
    }

    private updateCurrentMapInStorage(currMap: CurrentMapProgressPayload) {
        storage.setStorageData('dashboard', 'animateToCurrentLevel', false)
        storage.setStorageData('dashboard', 'lastMapPayload', currMap)
    }

    private isSameMapProgress(self: CurrentMapProgressPayload, other: CurrentMapProgressPayload) {
        return self.currentMap === other.currentMap && self.nextMap === other.nextMap
    }

    private updateVolumeBackgroundMusic() {
        // ? Channel 'global_music' has been created in 'LoadScene' as a persistent node for playing background music across all scenes.
        audio.setVolume(1, 'global_music')
    }

    private handleSwitchSceneEvent = (payload: SwitchScenePayload) => {
        const { sceneName, sceneData } = payload
        this.scene.switchScene(sceneName, sceneData)
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('DashboardScene: SceneManager not found')
        }

        if (!this.themeManager) {
            throw new Error('DashboardScene: ThemeManager not found')
        }

        if (!this.coinManager) {
            throw new Error('DashboardScene: CoinManager not found')
        }

        if (!this.journeyProgress) {
            throw new Error('DashboardScene: journeyProgress not found')
        }

        if (!this.playButtonLabel) {
            throw new Error('DashboardScene: playButtonLabel not found')
        }
    }
}
