import { EditBox, Enum, _decorator } from 'cc'
import { Gameplay } from '../../../Gameplay'
import GameplayEvents from '../../../constants/GameEvents'
import DevItem from '../DevItem'

const { ccclass, property } = _decorator

export enum LevelLoadingType {
    Next,
    Previous,
    Specific,
}

@ccclass
export default class LevelLoaderItem extends DevItem<number> {
    @property({ type: Enum(LevelLoadingType), displayName: 'Loading strategy' })
    public loadingStrategy: LevelLoadingType = LevelLoadingType.Next

    @property({
        type: EditBox,
        visible: function (this: LevelLoaderItem) {
            return this.loadingStrategy === LevelLoadingType.Specific
        },
    })
    public levelNumberInput: EditBox | null = null

    public activate(): Promise<number> {
        if (!this.gameplay) return Promise.reject(new Error('Gameplay is not injected'))

        const currentLevel = this.gameplay.getGameData()?.level

        if (currentLevel === undefined || currentLevel === null)
            return Promise.reject(new Error('Current level is not injected'))

        switch (this.loadingStrategy) {
            case LevelLoadingType.Next:
                return Promise.resolve(this.loadLevel(currentLevel + 1))

            case LevelLoadingType.Previous:
                return Promise.resolve(this.loadLevel(currentLevel - 1))

            case LevelLoadingType.Specific:
                if (!this.levelNumberInput)
                    return Promise.reject(new Error('Level number input is not injected'))

                if (this.levelNumberInput.node.active) {
                    const level = Number(this.levelNumberInput.string)

                    // hide level number input
                    this.levelNumberInput.node.active = false

                    return Promise.resolve(
                        level !== currentLevel ? this.loadLevel(level) : currentLevel
                    )
                }

                // show level number input
                this.levelNumberInput.node.active = true
                this.levelNumberInput.string = String(currentLevel)

                return Promise.resolve(currentLevel)

            default:
                return Promise.reject(new Error('Unknown loading strategy'))
        }
    }

    public override injectGameplay(gameplay: Gameplay) {
        super.injectGameplay(gameplay)

        // hide level number input
        if (this.levelNumberInput) this.levelNumberInput.node.active = false
    }

    private loadLevel(level: number): number {
        this.node.scene.emit(GameplayEvents.SELECT_LEVEL, { level })
        return level
    }
}
