import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

type JoinGroupPayload = GameCore.Plugins.Match.Concrete.Payload.Group.Join
type StartGroupPayload = GameCore.Plugins.Match.Concrete.Payload.Group.Start

const { match, player, event } = globalThis.game

@ccclass('FlowGroup')
export class FlowGroup extends Component {
    private playerId = ''

    public onLoad() {
        this.playerId = player.getPlayerId()
    }

    private showLoadingScreen(duration: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
            screenData: { duration },
        })
    }

    private closeLoadingScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    public async startGroup(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as StartGroupPayload

        try {
            this.showLoadingScreen(0)

            await match.group.start.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('startGroup', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async joinGroup(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as JoinGroupPayload

        try {
            this.showLoadingScreen(0)

            await match.group.join.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('joinGroup', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async finishGroup() {
        try {
            this.showLoadingScreen(0)

            player.setPlayerDataByName('score', Math.floor(Math.random() * 2))
            await match.group.finish.processAsync()
        } catch (error) {
            console.warn('finishGroup', error)

            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }
}
