import { Animation, AnimationClip, CCString, Component, SpriteFrame, _decorator } from 'cc'
import { JourneyLevelAvatar } from '../journey-level-avatar/JourneyLevelAvatar'
import { JourneyProgressBar } from '../journey-progress-bar/JourneyProgressBar'
const { ccclass, property } = _decorator

const { event } = globalThis.game

@ccclass('ProgressConfiguration')
export class ProgressConfiguration {
    @property(SpriteFrame)
    public avatar!: SpriteFrame

    @property(CCString)
    public themeId!: string
}

@ccclass('JourneyProgressAnimator')
export class JourneyProgressAnimator extends Component {
    @property(JourneyProgressBar)
    private progressBar!: JourneyProgressBar

    @property(Animation)
    private animator!: Animation

    @property(AnimationClip)
    private levelUpAnimation!: AnimationClip

    @property(AnimationClip)
    private resetAnimation!: AnimationClip

    @property(JourneyLevelAvatar)
    private currentLevelAvatar!: JourneyLevelAvatar

    @property(JourneyLevelAvatar)
    private nextLevelAvatar!: JourneyLevelAvatar

    @property(JourneyLevelAvatar)
    private nextLevelAvatarPlaceholder!: JourneyLevelAvatar

    @property(ProgressConfiguration)
    private progressConfigs: ProgressConfiguration[] = []

    // ! test level up animation
    private callback!: () => void

    private isAnimating = false

    public setRange(current: number, total: number): void {
        this.progressBar.setRange(current, total)
    }

    public async animateTo(current: number): Promise<number> {
        if (this.isAnimating) {
            console.warn('JourneyProgressAnimator: animation already in progress')
            return Promise.resolve(-1)
        }

        this.isAnimating = true
        const r = await this.progressBar.animateTo(current)

        this.isAnimating = false
        return r
    }

    public async animateToNextLevel(
        nextProgress: number,
        nextTotal: number,
        prevThemeId: string,
        nextThemeId: string
    ): Promise<number> {
        if (this.isAnimating) {
            console.warn('JourneyProgressAnimator: animation already in progress')
            return Promise.resolve(-1)
        }

        this.isAnimating = true
        this.setAvatar(nextThemeId, this.nextLevelAvatarPlaceholder)

        await this.progressBar.animateTo(this.progressBar.total)

        await this.playAnimation(this.levelUpAnimation)

        this.setCurrentLevelAvatar(prevThemeId)
        this.setNextLevelAvatar(nextThemeId)

        await this.playAnimation(this.resetAnimation)

        this.progressBar.setRange(nextTotal, nextTotal)

        await this.progressBar.animateTo(0)
        await this.progressBar.animateTo(nextProgress)

        this.isAnimating = false

        return nextProgress
    }

    private playAnimation(clip: AnimationClip): Promise<void> {
        return new Promise((resolve) => {
            this.animator.once(Animation.EventType.FINISHED, () => {
                resolve()
            })

            this.animator.play(clip.name)
        })
    }

    private getSpriteFrameByThemeId(themeId: string): SpriteFrame {
        const config = this.progressConfigs.find((config) => config.themeId === themeId)

        if (!config) {
            throw new Error(`JourneyProgressController: config not found for themeId ${themeId}`)
        }

        return config.avatar
    }

    private setAvatar(themeId: string, avatar: JourneyLevelAvatar) {
        const spriteFrame = this.getSpriteFrameByThemeId(themeId)
        avatar.avatarSprite = spriteFrame
    }

    public setCurrentLevelAvatar(themeId: string) {
        this.setAvatar(themeId, this.currentLevelAvatar)
    }

    public setNextLevelAvatar(themeId: string) {
        this.setAvatar(themeId, this.nextLevelAvatar)
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    public start(): void {
        // ! test level up animation
        // call `game.event.emit('DashboardScene-Journey-Progress-LevelUp')` on the browser console
        this.callback = this.testAnimation.bind(this)
        event.on('DashboardScene-Journey-Progress-LevelUp', this.callback)
    }

    public onDestroy(): void {
        // ! test level up animation
        event.off('DashboardScene-Journey-Progress-LevelUp', this.callback)
    }

    private testAnimation(): void {
        this.animateToNextLevel(0, 10, 'autumn', 'summer')
    }

    private validateRequiredComponents(): void {
        if (!this.currentLevelAvatar) {
            throw new Error('JourneyProgressController: currentLevelAvatar not found')
        }

        if (!this.nextLevelAvatar) {
            throw new Error('JourneyProgressController: nextLevelAvatar not found')
        }

        if (!this.progressBar) {
            throw new Error('JourneyProgressController: progressBar not found')
        }

        if (this.progressConfigs.length === 0) {
            throw new Error('JourneyProgressController: add some progressConfigs')
        }

        if (!this.animator) {
            throw new Error('JourneyProgressController: animator not found')
        }

        if (!this.levelUpAnimation) {
            throw new Error('JourneyProgressController: levelUpAnimation not found')
        }

        if (!this.resetAnimation) {
            throw new Error('JourneyProgressController: restAnimation not found')
        }

        const clips = this.animator.clips
        if (!clips.find((clip) => clip && clip.name === this.levelUpAnimation.name)) {
            throw new Error('JourneyProgressController: levelUpAnimation not found in animator')
        }

        if (!clips.find((clip) => clip && clip.name === this.resetAnimation.name)) {
            throw new Error('JourneyProgressController: restAnimation not found in animator')
        }
    }
}
