import { Button, CCBoolean, CCInteger, Component, Label, Node, _decorator } from 'cc'

import ScreenKeys from '../../../../constants/ScreenKeys'
import { GridEvents } from '../../../../gameplay/constants/GridEvents'
import ItemKeys from '../../../../gameplay/constants/ItemKeys'
import { SpecialItem } from '../../../../gameplay/game-objects/item/SpecialItem'
const { ccclass, property } = _decorator

export const ItemEvent = {
    ACTIVATE: 'ITEM-ACTIVATE',
    FINISH: 'ITEM-FINISH',
} as const

export type ItemEvent = typeof ItemEvent[keyof typeof ItemEvent]

const { event } = globalThis.game
const { Events } = GameCore

@ccclass('ItemButton')
export default class ItemButton extends Component {
    protected item!: SpecialItem

    @property(CCBoolean)
    public isDebug = false

    @property(Button)
    private button!: Button

    @property({ type: CCInteger })
    public quantity = 0

    @property(Label)
    public quantityLabel!: Label

    @property(Node)
    public plusNode!: Node

    @property(Node)
    public quantityNode!: Node

    protected __preload(): void {
        this.item = this.getComponent(SpecialItem)!

        this.validateRequiredComponents()
    }

    protected start(): void {
        this.quantity = this.item.loadQuantity()
        this.quantityLabel.string = `${this.quantity}`
        this.validateButton()

        this.node.scene.on(GridEvents.CREATE, this.validateButton, this)
        this.node.scene.on(GridEvents.MATCH_SUCCESS, this.validateButton, this)
        this.node.scene.on(GridEvents.CLEAR, this.validateButton, this)
    }

    protected validateRequiredComponents() {
        if (!this.item) {
            throw new Error('Item: BaseItem component not found')
        }

        if (!this.button) {
            throw new Error('Item: Button component not found')
        }

        if (!this.quantityLabel) {
            throw new Error('Item: Quantity label not found')
        }

        if (!this.plusNode) {
            throw new Error('Item: Plus node not found')
        }

        if (!this.quantityNode) {
            throw new Error('Item: Quantity node not found')
        }
    }

    private validateButton() {
        console.log('VALIDATE BUTTON')

        this.canActivate().then((_canActivate) => {
            this.button.interactable = true
            this.plusNode.active = this.quantity == 0
            this.quantityNode.active = this.quantity > 0
        })
    }

    // ? Use for button event
    public activate(): Promise<void> {
        return new Promise((resolve) => {
            if (this.quantity <= 0) {
                this.handleShowAddItemScreen()

                resolve()
                return
            }

            this.item.activate().then(() => {
                if (!this.isDebug) {
                    this.quantity--
                    this.quantityLabel.string = `${this.quantity}`
                    this.item.saveQuantity()
                }

                this.validateButton()
                resolve()
            })
        })
    }

    public canActivate(): Promise<boolean> {
        return Promise.resolve(true)
        // return new Promise((resolve) => {
        //     this.item.canActivate().then((canActivate) => {
        //         resolve(canActivate && this.quantity > 0)
        //     })
        // })
    }

    public addQuantity(quantity: number): void {
        this.quantity += quantity
        this.quantityLabel.string = `${this.quantity}`
        this.item.saveQuantity()
        this.validateButton()
    }

    public clearQuantity(): void {
        this.quantity = 0
        this.quantityLabel.string = `${this.quantity}`
        this.item.saveQuantity()
        this.validateButton()
    }

    public handleShowAddItemScreen(): void {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.GET_ITEM_SCREEN,
            screenData: {
                item: ItemKeys.HINT,
                quantity: 1,
            },
        })
    }
}
