import { Component, Node, UIOpacity, Vec3, _decorator, tween } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import AnimUtils from '../../utils/AnimUtils'
import GeneralUtils from '../../utils/GeneralUtils'
import { BaseScreen } from '../BaseScreen'
const { ccclass, property, disallowMultiple, requireComponent } = _decorator
const { Events } = GameCore
const { event } = globalThis.game

@ccclass('TimeUpScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class LoseScreen extends Component {
    private screen!: BaseScreen

    @property(Node)
    public adsContainer!: Node

    @property(UIOpacity)
    public adOpacity!: UIOpacity

    @property([Node])
    private animNodes: Node[] = []

    @property(Node)
    private bubbleAnimNode!: Node

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('LoseScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.enabled = false
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.adsContainer) {
            throw new Error('TimeUpScreen: adsContainer not found')
        }

        if (!this.adOpacity) {
            throw new Error('TimeUpScreen: adOpacity not found')
        }

        if (this.animNodes.length === 0) {
            throw new Error('TimeUpScreen: animNodes not found')
        }

        if (!this.bubbleAnimNode) {
            throw new Error('TimeUpScreen: bubbleAnimNode not found')
        }
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    public open() {
        this.resetState()
        this.runOpenAnimation()
    }

    public close() {
        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    private resetState() {
        this.adOpacity.opacity = 0
    }

    private async runOpenAnimation() {
        if (!this.screen.container) {
            console.warn('TimeUpScreen: screen not found')
            return
        }

        this.animNodes.forEach((target, index) => {
            this.runAppearTween(target, index * 0.1)
        })

        await this.runShowAdContainer()
    }

    private runAppearTween(target: Node, delay: number) {
        const t = tween(target)
        t.set({ scale: new Vec3(0, 0, 0) })
        t.delay(delay)
        t.to(
            1,
            { scale: new Vec3(1, 1, 1) },
            {
                progress: (start: number, end: number, _current: number, ratio: number): number => {
                    const newRatio = AnimUtils.elasticOut(ratio, 0.1, 0.45)
                    return start + (end - start) * newRatio
                },
            }
        )
        t.start()
    }

    private runShowAdContainer() {
        const t = tween(this.adOpacity)
        t.delay(1)
        t.to(0.5, { opacity: 255 })

        return AnimUtils.runTweenAsync(t)
    }

    // ? Callback for click event of PlayAgainButton
    public handlePlayAgain(): void {
        this.close()

        this.node.scene.emit(GameplayEvents.GAME_REPLAY, {
            level: this.screen.data['level'],
        })
    }

    // ? Callback for click event of GetButton
    public async handleRescueWithTime(): Promise<void> {
        await GeneralUtils.showRewardVideoAdAsync({
            onSucceedCallback: () => {
                this.node.scene.emit(GameplayEvents.RESCUE_START)
                this.close()
            },
        })
    }
}
