import {
    Animation,
    AnimationClip,
    Button,
    Component,
    Node,
    Sprite,
    SpriteFrame,
    UIOpacity,
    UITransform,
    Vec2,
    Vec3,
    _decorator as decor,
    tween,
    type TweenEasing,
} from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import GameplayEvents from '../../constants/GameEvents'
import { TileTypes } from '../../constants/TileType'
import BasicState from './special/BasicState'
import BombState from './special/BombState'
import SpecialStateMachine from './special/SpecialStateMachine'

const { ccclass, property, requireComponent } = decor

export type ClearFX = 'clear' | 'break'

@ccclass('Tile')
@requireComponent([UITransform])
@requireComponent([UIOpacity])
export default class Tile extends Component {
    @property(Sprite)
    public readonly icon!: Sprite

    @property(Sprite)
    public readonly bombComponent!: Sprite

    @property(Node)
    private background!: Node

    @property({ type: UITransform, displayName: "Background's UI Transform" })
    private bgUITransform!: UITransform

    @property({ type: UIOpacity, displayName: 'This UI Opacity' })
    private uiOpacity!: UIOpacity

    @property(UIOpacity)
    private shineOpacity!: UIOpacity

    @property(Button)
    private button!: Button

    public get buttonComponent(): Button {
        return this.button
    }

    private animationComponent!: Animation

    @property({ type: AnimationClip, group: 'Break FX', displayName: 'Break clip' })
    private breakClip!: AnimationClip

    @property({ type: AnimationClip, group: 'Clear FX', displayName: 'Clear clip' })
    private clearClip!: AnimationClip

    public id: SpriteFrame | null = null
    public special: SpecialStateMachine = new SpecialStateMachine(this)
    private isEmpty = false
    private isSynced = true
    private coord: Vec2 = new Vec2()

    isDirty(): boolean {
        return !this.isSynced
    }

    protected onLoad(): void {
        this.animationComponent = this.getComponent(Animation)!
    }

    protected start(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.icon) {
            throw new Error('An icon component must be assigned to the tile')
        }

        if (!this.bombComponent) {
            throw new Error('A bomb component must be assigned to the tile')
        }

        if (!this.background) {
            throw new Error('A background component must be assigned to the tile')
        }

        if (!this.bgUITransform) {
            throw new Error('A background UI transform component must be assigned to the tile')
        }

        if (!this.uiOpacity) {
            throw new Error('A UI opacity component must be assigned to the tile')
        }

        if (!this.shineOpacity) {
            throw new Error('A shine opacity component must be assigned to the tile')
        }

        if (!this.animationComponent) {
            throw new Error('An animation component must be assigned to the tile')
        }

        if (!this.button) {
            throw new Error('A button component must be assigned to the tile')
        }

        const clips = this.animationComponent.clips
            .map((clip) => clip?.name)
            .filter(
                (clip): clip is string =>
                    clip !== null && clip !== undefined && typeof clip === 'string'
            )

        if (!clips.includes(this.breakClip.name)) {
            this.animationComponent.addClip(this.breakClip)
        }
    }

    protected update(dt: number): void {
        this.special.update(dt)
    }

    public getCoord(): Vec2 {
        return this.coord
    }

    public setCoord(x: number, y: number, isSynced = false): void {
        this.coord.set(x, y)
        if (!isSynced) {
            this.isSynced = false
        }
    }

    public isSame(tile: Tile): boolean {
        return this.special.accept(tile) && tile.special.accept(this)
    }

    public isTraversable(): boolean {
        return this.isEmpty
    }

    public getSize(): [number, number] {
        return [this.bgUITransform.width, this.bgUITransform.height]
    }

    protected onClick(): void {
        this.node.scene.emit(GameplayEvents.TILE_CLICK, this)
        // GameManager.Instance.stateMachine.playing()
        // GameManager.Instance.grid.emitTileDown(this)
    }

    public setIdentity(frame: SpriteFrame): void {
        this.id = frame
        this.icon.spriteFrame = frame

        //? fit icon into 60% Tile
        const fillRatio = 0.7
        const [tileWidth, tileHeight] = this.getSize()

        const { width: iconW, height: iconH } = frame.originalSize
        const scale = Math.max(iconW / (tileWidth * fillRatio), iconH / (tileHeight * fillRatio))
        if (scale > 1) {
            this.icon.node.setScale(new Vec3(1 / scale, 1 / scale, 1))
        }
    }

    public setSpecialType(specialType: TileType = TileTypes.NORMAL): void {
        switch (specialType) {
            case TileTypes.NORMAL:
                this.special.updateState(new BasicState(this))
                break
            case TileTypes.BOMB:
                this.special.updateState(new BombState(this, 90))
                break
            case TileTypes.ROCKET:
                this.special.updateState(new BasicState(this))
                break
            default:
                throw new Error('Invalid special type')
        }
    }

    public setFocused(focused: boolean): void {
        tween(this.shineOpacity)
            .to(0.2, { opacity: focused ? 255 : 0 }, { easing: 'quadIn' })
            .start()

        this.shineOpacity.node.scale = focused ? Vec3.ZERO : Vec3.ONE
        tween(this.shineOpacity.node)
            .to(0.2, { scale: focused ? Vec3.ONE : Vec3.ZERO }, { easing: 'quadOut' })
            .start()
    }

    setHint(_hinting: boolean): void {
        // this.animController.setValue('isHinting', hinting)
    }

    public async setEmpty(
        other?: Tile,
        quietly = false,
        clearFx: ClearFX = 'clear'
    ): Promise<[Tile, Tile][]> {
        if (this.isEmpty) {
            return []
        }

        if (quietly) {
            this.isEmpty = true
            this.uiOpacity.opacity = 0
            this.special.updateState(new BasicState(this))
            return []
        }

        this.isEmpty = true

        if (clearFx === 'clear') {
            await this.runAnimation(this.clearClip.name)
        } else {
            await this.runAnimation(this.breakClip.name)
        }

        const pairs = await this.special.clear(other)
        this.special.updateState(new BasicState(this))
        return pairs
    }

    public restore(id?: SpriteFrame): Promise<void> {
        this.isEmpty = false

        if (id) {
            this.setIdentity(id)
        }

        this.uiOpacity.opacity = 255
        this.node.setScale(new Vec3(0, 0, 0))

        return new Promise((resolve) => {
            tween(this.node)
                .to(
                    0.3,
                    {
                        scale: new Vec3(1, 1, 1),
                    },
                    {
                        easing: 'backOut',
                        onComplete: () => {
                            if (id) {
                                // this.animController.setValue('reset', true)
                            }

                            resolve()
                        },
                    }
                )
                .start()
        })
    }

    public sync(config: SyncConfig): Promise<void> {
        if (this.isSynced && !config.ignoreDirtyStatus) {
            return Promise.resolve()
        }

        const newPosition = this.getTilePosition(config)

        const delay = config.stagger ?? 0
        const duration = config.duration ?? 0.3
        const easing: TweenEasing = config.easing ?? 'cubicOut'

        return new Promise((resolve) => {
            // tween to new position
            tween(this.node)
                .delay(delay)
                .to(duration, { position: newPosition }, { easing })
                .call(() => {
                    resolve()
                    this.isSynced = true
                })
                .start()
        })
    }

    public pad(config: PaddingConfig): Promise<void> {
        const newPosition = this.getTilePosition(config)

        const delay = config.stagger ?? 0
        const duration = config.duration ?? 0.3
        const easing: TweenEasing = config.easing ?? 'cubicOut'

        return new Promise((resolve) => {
            // tween to new position
            tween(this.node)
                .call(() => {
                    this.isSynced = false
                })
                .delay(delay)
                .to(duration, { position: newPosition }, { easing })
                .call(() => {
                    resolve()
                })
                .start()
        })
    }

    serialize(): SerializableTile {
        return {
            coord: {
                x: this.coord.x,
                y: this.coord.y,
            } as Vec2Like,
            specialType: this.special.getType(),
        }
    }

    deserialize(json: SerializableTile): void {
        this.setCoord(json.coord.x, json.coord.y)
        this.setSpecialType(json.specialType)
    }

    private runAnimation(name: string): Promise<void> {
        this.animationComponent.play(name)

        return new Promise((resolve) => {
            this.animationComponent.once(Animation.EventType.FINISHED, () => {
                resolve()
            })
        })
    }

    public async fly(config: PaddingConfig) {
        const MAX_HEIGHT = 300
        const distanceToCenter = Math.sqrt(this.node.position.x ** 2 + this.node.position.y ** 2)
        const height = Math.max(MAX_HEIGHT - distanceToCenter * 0.7, 0)
        const delay = distanceToCenter / 1000

        const curPosition = this.node.position.clone()
        const newPosition = this.getTilePosition(config)

        const spriteNode = this.background
        // const shadowNode = this.node.getChildByName('Shadow')!

        let shakeAmount = 2

        const duration = height * 0.0025

        let initOrbitSpeed = 1 //? PI/s
        let initSelfOrbitSpeed = 360 //? degree/s

        const phase1Duration = duration

        let phase2StartAngle = 0
        const phase2CenterPos = new Vec3(0, 0, 0)
        const phase2Duration = 0.75 - phase1Duration

        const tweenObject = { value: 0, prevElapsed: 0 }
        const t = tween(tweenObject)
        t.call(() => {
            this.isSynced = false
        })
        t.delay(delay)
        t.to(
            0.5,
            { value: 0 },
            {
                progress: (
                    _start: number,
                    _end: number,
                    current: number,
                    ratio: number
                ): number => {
                    shakeAmount *= -1

                    const deltaShake = shakeAmount * ratio

                    const newPos = curPosition.clone()
                    newPos.x += deltaShake
                    this.node.setPosition(newPos)

                    return current
                },
            }
        )
        t.to(
            phase1Duration,
            { value: 1 },
            {
                progress: (
                    _start: number,
                    _end: number,
                    current: number,
                    ratio: number
                ): number => {
                    const expoRatio = AnimUtils.easeOutExpo(ratio)
                    const deltaTime = phase1Duration * ratio - tweenObject.prevElapsed
                    const elapsedTime = phase1Duration * ratio

                    //? tween height
                    const newH = height * expoRatio
                    this.setHeight(newH, MAX_HEIGHT)

                    //? tween position
                    const currentPos = this.node.position.clone()
                    initOrbitSpeed += 0.1
                    Vec3.rotateZ(
                        currentPos,
                        currentPos,
                        new Vec3(0, 0, 0),
                        initOrbitSpeed * Math.PI * deltaTime
                    )

                    this.node.setPosition(new Vec3(currentPos.x, currentPos.y, currentPos.z))

                    //? tween angle
                    initSelfOrbitSpeed += 10
                    const addAngle = initSelfOrbitSpeed * deltaTime
                    spriteNode.angle += addAngle
                    // shadowNode.angle += addAngle

                    tweenObject.prevElapsed = elapsedTime

                    return current
                },
            }
        )
        t.call(() => {
            phase2CenterPos.set(
                (this.node.position.x + newPosition.x) / 2,
                (this.node.position.y + newPosition.y) / 2,
                0
            )
            tweenObject.prevElapsed = 0
            //? transform angle to negative value for easy tween
            spriteNode.angle %= 360
            spriteNode.angle -= 360
            // shadowNode.angle %= 360
            // shadowNode.angle -= 360
            phase2StartAngle = spriteNode.angle
        })
        t.to(
            phase2Duration,
            { value: 0 },
            {
                progress: (
                    _start: number,
                    _end: number,
                    current: number,
                    ratio: number
                ): number => {
                    const deltaTime = phase2Duration * ratio - tweenObject.prevElapsed
                    const elapsedTime = phase2Duration * ratio

                    //? tween height
                    const newH = height * (1 - ratio)
                    this.setHeight(newH, MAX_HEIGHT)

                    //? tween position
                    const currentPos = this.node.position.clone()
                    Vec3.rotateZ(
                        currentPos,
                        currentPos,
                        phase2CenterPos,
                        (Math.PI / phase2Duration) * deltaTime
                    )

                    this.node.setPosition(new Vec3(currentPos.x, currentPos.y, currentPos.z))

                    //? tween angle
                    spriteNode.angle += (-phase2StartAngle / phase2Duration) * deltaTime
                    // shadowNode.angle += (-phase2StartAngle / phase2Duration) * deltaTime

                    tweenObject.prevElapsed = elapsedTime

                    return current
                },
            }
        )
        t.call(() => {
            this.node.setPosition(newPosition)
            this.setHeight(0, MAX_HEIGHT)
            spriteNode.angle = 0
            // shadowNode.angle = 0
        })
        return AnimUtils.runTweenAsync(t)
    }

    private setHeight(height: number, maxHeight: number): void {
        const spriteNode = this.background
        // const shadowNode = this.node.getChildByName('Shadow')!

        spriteNode.setPosition(new Vec3(spriteNode.position.x, height, 0))
        // shadowNode.setPosition(new Vec3(shadowNode.position.x, -4 - height / 10, 0))
        //? max: 0.3 (height), min: 0 (height 0)
        spriteNode.setScale(
            new Vec3(1 + (height / maxHeight) * 0.3, 1 + (height / maxHeight) * 0.3, 1)
        )
    }

    private getTilePosition(config: {
        tileSize: number[]
        gridW: number
        gridH: number
        padding?: number
    }) {
        const { tileSize, gridW, gridH, padding = 0 } = config
        const { x, y } = this.getCoord()

        return new Vec3(
            tileSize[0] * (x - (gridW - 1) / 2) + padding * (x - gridW / 2),
            tileSize[1] * (y - (gridH - 1) / 2) + padding * (y - gridH / 2),
            this.node.position.z
        )
    }

    public getSpecialType() {
        return this.special.getType()
    }
}
