import Tile from '../tile/Tile'
import Grid from './Grid'
import StateMachine from './StateMachine'
import BaseState from './states/Base'

export const GridMachineEvent = {
    STATE_CHANGE: 'CHANGE_STATE',
    STATE_ENTER: 'ENTER_STATE',
    STATE_EXIT: 'EXIT_STATE',
} as const

export type GridMachineEvent = typeof GridMachineEvent[keyof typeof GridMachineEvent]

export abstract class State extends BaseState {
    readonly grid: Grid
    public machine: GridMachine | null

    constructor(grid: Grid) {
        super()
        this.grid = grid
        this.machine = null
    }

    setMachine(machine: GridMachine): void {
        this.machine = machine
    }

    abstract emitTileDown(tile: Tile): void
    abstract awaitingInput(): boolean
    abstract transition(): void
}

export default class GridMachine extends StateMachine<State> {
    constructor(initial: State) {
        super()
        this.currentState = initial
        this.previousState = initial
        initial.setMachine(this)
    }

    public updateState(newState: State): void {
        const curr = this.currentState

        this.currentState.grid.node.emit(GridMachineEvent.STATE_EXIT, this.currentState)

        newState.setMachine(this)
        super.updateState(newState)

        this.currentState.grid.node.emit(GridMachineEvent.STATE_CHANGE, curr, newState)
        this.currentState.grid.node.emit(GridMachineEvent.STATE_ENTER, newState)
    }

    emitTileDown(tile: Tile): void {
        this.currentState.emitTileDown(tile)
    }

    awaitingInput(): boolean {
        return this.currentState.awaitingInput()
    }
}
