import { Component, ParticleSystem2D, UIOpacity, Vec3, _decorator, tween } from 'cc'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
const { ccclass, property } = _decorator

@ccclass('ComboEffect')
export class ComboEffect extends Component {
    @property(UIOpacity)
    private circleOpacity!: UIOpacity

    @property(ParticleSystem2D)
    private particle!: ParticleSystem2D

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.circleOpacity) {
            throw new Error('ComboEffect requires a circleOpacity component')
        }

        if (!this.particle) {
            throw new Error('ComboEffect requires a particle component')
        }
    }

    public start(): void {
        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.handleOnComboUpdated, this)
    }

    public handleOnComboUpdated(combo: number): void {
        if (combo > 1) {
            this.circleOpacity.node.scale = Vec3.ZERO
            this.circleOpacity.opacity = 127.5

            this.particle.resetSystem()

            tween(this.circleOpacity.node)
                .to(0.2, { scale: new Vec3(1.3, 1.3, 1.3) }, { easing: 'quadOut' })
                .start()

            tween(this.circleOpacity)
                .delay(0.2)
                .to(0.2, { opacity: 0 }, { easing: 'expoIn' })
                .start()
        }
    }
}
