import { Button, Component, _decorator } from 'cc'
import { EventBadge } from '../event-badge/EventBadge'
import { EventProgressBar } from '../event-progress-bar/EventProgressBar'
const { ccclass, property } = _decorator

@ccclass('EventButton')
export class EventButton extends Component {
    @property(Button)
    private buttonComponent!: Button

    @property(EventProgressBar)
    private progressBar!: EventProgressBar

    @property(EventBadge)
    private badge!: EventBadge

    public get progress(): EventProgressBar {
        return this.progressBar
    }

    public get button(): Button {
        return this.buttonComponent
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.buttonComponent) {
            throw new Error('EventButton: Missing required component Button')
        }

        if (!this.progressBar) {
            throw new Error('EventButton: Missing required component EventProgressBar')
        }

        if (!this.badge) {
            throw new Error('EventButton: Missing required component EventBadge')
        }
    }
}
