import { Label, _decorator } from 'cc'
import { JourneyProgressAnimator } from '../../ui/journey-progress/JourneyProgressAnimator'
import { DashboardScreen } from '../DashboardScreen'
const { ccclass, property } = _decorator

const { event, language, player, match } = globalThis.game
const { Events } = GameCore

@ccclass('HomeScreen')
export class HomeScreen extends DashboardScreen {
    @property(JourneyProgressAnimator)
    private journeyProgress!: JourneyProgressAnimator

    @property(Label)
    private playButtonLabel!: Label

    public setProgress(payload: CurrentMapProgressPayload) {
        const { currentMap: currentTheme, nextMap: nextTheme, progress, max } = payload

        this.journeyProgress.setCurrentLevelAvatar(currentTheme)

        // ! Handle case where nextTheme is undefined. (i.e. last theme)
        this.journeyProgress.setNextLevelAvatar(nextTheme ?? currentTheme)

        this.journeyProgress.setRange(progress, max)
    }

    public updateLevelText(level: number) {
        const levelText = language.getText('level')
        this.playButtonLabel.string = levelText + ` ${level}`
    }

    public handlePlayerButtonPress() {
        const playerId = player.getPlayerId()

        match.single.start
            .processAsync({
                playerId,
            })
            .then(() => {
                event.emit(Events.SWITCH_SCENE, {
                    sceneName: 'GameScene',
                    sceneData: {
                        isContextProcessing: true,
                    },
                })
            })
    }

    public onLoad() {
        super.onLoad()
        this.validateRequiredComponentsExtended()
    }

    private validateRequiredComponentsExtended() {
        if (!this.journeyProgress) {
            throw new Error('HomeScreen: journeyProgress not found')
        }

        if (!this.playButtonLabel) {
            throw new Error('HomeScreen: playButtonLabel not found')
        }
    }
}
