import { Color, Component, Input, Label, Sprite, __private, _decorator, profiler, sys } from 'cc'
const { ccclass, executionOrder, property } = _decorator

const { Utils } = GameCore

type Stats = __private._cocos_profiler_profiler__IProfilerState

const NORMAL_COLOR = Color.BLACK
const WARNING_COLOR = Color.MAGENTA
const DANGER_COLOR = Color.RED

@ccclass('GameStats')
@executionOrder(-1000)
export class GameStats extends Component {
    @property(Label)
    public fpsLabel!: Label

    @property(Label)
    public drawCountLabel!: Label

    @property(Label)
    public logicTimeLabel!: Label

    @property(Label)
    public physicTimeLabel!: Label

    @property(Label)
    public renderTimeLabel!: Label

    @property(Label)
    public objectMemoryUsageLabel!: Label

    @property(Label)
    public textureMemoryUsageLabel!: Label

    private bg!: Sprite

    private stats!: Stats
    private refreshInterval = 0.5

    public onLoad(): void {
        this.bg = this.getComponent(Sprite)!

        this.validateRequiredComponents()

        this.listenInputEvents()
    }

    private validateRequiredComponents() {
        if (!this.bg) throw new Error('Missing background sprite')
        if (!this.fpsLabel) throw new Error('Missing fpsLabel')
        if (!this.drawCountLabel) throw new Error('Missing drawCountLabel')
        if (!this.logicTimeLabel) throw new Error('Missing logicTimeLabel')
        if (!this.physicTimeLabel) throw new Error('Missing physicTimeLabel')
        if (!this.renderTimeLabel) throw new Error('Missing renderTimeLabel')
        if (!this.objectMemoryUsageLabel) throw new Error('Missing objectMemoryUsageLabel')
        if (!this.textureMemoryUsageLabel) throw new Error('Missing textureMemoryUsageLabel')
    }

    private listenInputEvents() {
        this.node.on(Input.EventType.TOUCH_START, this.handleMouseDown)
    }

    private handleMouseDown = () => {
        this.bg.enabled = !this.bg.enabled
    }

    public start(): void {
        this.bg.enabled = false
        this.node.active = false

        // console.warn({ profiler, isDebug: Utils.Valid.isDebugger() })
        if (!profiler) return

        if (!Utils.Valid.isDebugger()) {
            profiler.hideStats()
            return
        }

        profiler.showStats()

        this.schedule(this.waitForStats, 0.5, 20)
    }

    private waitForStats = () => {
        if (!profiler.stats) return
        this.unschedule(this.waitForStats)

        this.node.active = true

        this.stats = profiler.stats!

        this.hideCocosStats()
        this.startUpdateStats()
    }

    // ? Only hide the cocos stats UI
    private hideCocosStats() {
        if (!profiler) return
        // @ts-expect-error - private property
        profiler._showFPS = false
        // @ts-expect-error - private property
        const { _rootNode } = profiler

        if (_rootNode) {
            _rootNode.active = false
            if (_rootNode.isValid) {
                _rootNode.destroyAllChildren()
                _rootNode.removeAllChildren()
                _rootNode.destroy()
            }
        }
    }

    private startUpdateStats() {
        this.schedule(this.updateStats, this.refreshInterval)
    }

    private updateStats = () => {
        this.updateFps()
        this.updateDrawCalls()
        this.updateLogicTime()
        this.updatePhysicTime()
        this.updateRenderTime()
        this.updateObjectMemoryUsage()
        this.updateTextureMemoryUsage()
        this.hideCocosStats()
    }

    private updateFps() {
        const fps = this.getFps()
        this.updateFpsColor(fps)
        this.fpsLabel.string = `FPS: ${fps}`
    }

    // ? Good fps is 60, so 1 frame is 16.6ms, that mean renderTime + logicTime + physicTime
    // ? If renderTime, logicTime or physicTime is bigger than 5ms, it's not good
    private updateFpsColor(fps: number) {
        if (fps < 30) {
            this.fpsLabel.color = DANGER_COLOR
        } else if (fps < 48) {
            this.fpsLabel.color = WARNING_COLOR
        } else {
            this.fpsLabel.color = NORMAL_COLOR
        }
    }

    // ? The draw calls is the number of draw calls in the scene
    // ? If draw calls is bigger than 15, it's not good
    private updateDrawCalls() {
        const drawCount = this.getDrawCalls()
        this.updateDrawCallsColor(drawCount)
        this.drawCountLabel.string = `Draw Calls: ${drawCount}`
    }

    private updateDrawCallsColor(drawCount: number) {
        // * Special case: on iOS, drawCount higher will be ok.
        // ! But! only good to use with AutoAtlas and disable DynamicAtlas, if not, it's not good
        // ? Double check: setupDynamicAtlas in SystemManager.ts

        const isIOS = sys.os === sys.OS.IOS

        const dangerDrawCount = isIOS ? 80 : 25
        const warningDrawCount = isIOS ? 60 : 15

        if (drawCount > dangerDrawCount) {
            this.drawCountLabel.color = DANGER_COLOR
        } else if (drawCount > warningDrawCount) {
            this.drawCountLabel.color = WARNING_COLOR
        } else {
            this.drawCountLabel.color = NORMAL_COLOR
        }
    }

    private updateLogicTime() {
        const logicTime = this.getLogicTime()
        this.updateLogicTimeColor(logicTime)
        this.logicTimeLabel.string = `Logic Time: ${logicTime.toFixed(2)}ms`
    }

    private updateLogicTimeColor(logicTime: number) {
        if (logicTime > 10) {
            this.logicTimeLabel.color = DANGER_COLOR
        } else if (logicTime > 5) {
            this.logicTimeLabel.color = WARNING_COLOR
        } else {
            this.logicTimeLabel.color = NORMAL_COLOR
        }
    }

    private updatePhysicTime() {
        const physicTime = this.getPhysicTime()
        this.updatePhysicTimeColor(physicTime)
        this.physicTimeLabel.string = `Physic Time: ${physicTime.toFixed(2)}ms`
    }

    private updatePhysicTimeColor(physicTime: number) {
        if (physicTime > 10) {
            this.physicTimeLabel.color = DANGER_COLOR
        } else if (physicTime > 5) {
            this.physicTimeLabel.color = WARNING_COLOR
        } else {
            this.physicTimeLabel.color = NORMAL_COLOR
        }
    }

    private updateRenderTime() {
        const renderTime = this.getRenderTime()
        this.updateRenderTimeColor(renderTime)
        this.renderTimeLabel.string = `Render Time: ${renderTime.toFixed(2)}ms`
    }

    private updateRenderTimeColor(renderTime: number) {
        if (renderTime > 10) {
            this.renderTimeLabel.color = DANGER_COLOR
        } else if (renderTime > 5) {
            this.renderTimeLabel.color = WARNING_COLOR
        } else {
            this.renderTimeLabel.color = NORMAL_COLOR
        }
    }

    // ? The object memory usage is the memory used by the objects in the scene
    // ? If it's bigger than 40MB, it's not good
    private updateObjectMemoryUsage() {
        const objectMemoryUsage = this.getObjectMemoryUsage()
        this.updateObjectMemoryUsageColor(objectMemoryUsage)
        this.objectMemoryUsageLabel.string = `Object Mem: ${objectMemoryUsage.toFixed(2)}MB`
    }

    private updateObjectMemoryUsageColor(objectMemoryUsage: number) {
        if (objectMemoryUsage > 40) {
            this.objectMemoryUsageLabel.color = DANGER_COLOR
        } else if (objectMemoryUsage > 25) {
            this.objectMemoryUsageLabel.color = WARNING_COLOR
        } else {
            this.objectMemoryUsageLabel.color = NORMAL_COLOR
        }
    }

    // ? The texture memory usage is the memory used by the textures in the scene
    // ? If it's bigger than 50MB, it's not good
    private updateTextureMemoryUsage() {
        const textureMemoryUsage = this.getTextureMemoryUsage()
        this.updateTextureMemoryUsageColor(textureMemoryUsage)
        this.textureMemoryUsageLabel.string = `Texture Mem: ${textureMemoryUsage.toFixed(2)}MB`
    }

    private updateTextureMemoryUsageColor(textureMemoryUsage: number) {
        if (textureMemoryUsage > 50) {
            this.textureMemoryUsageLabel.color = DANGER_COLOR
        } else if (textureMemoryUsage > 35) {
            this.textureMemoryUsageLabel.color = WARNING_COLOR
        } else {
            this.textureMemoryUsageLabel.color = NORMAL_COLOR
        }
    }

    // Frames per second
    private getFps(): number {
        const { counter } = this.stats.fps
        return Math.floor(counter.value) || 0
    }

    private getDrawCalls(): number {
        // Draw calls per frame
        // * Maybe minus a draw call by GameStats
        const { counter } = this.stats.draws
        return counter.value || 0
    }

    private getLogicTime(): number {
        // Time spent in logic in ms
        const { counter } = this.stats.logic
        return Math.floor(counter.value * 1000) / 1000 || 0
    }

    private getPhysicTime(): number {
        // Time spent in physics in ms
        const { counter } = this.stats.physics
        return Math.floor(counter.value * 1000) / 1000 || 0
    }

    private getRenderTime(): number {
        // Time spent in rendering in ms
        const { counter } = this.stats.render
        return Math.floor(counter.value * 1000) / 1000 || 0
    }

    private getObjectMemoryUsage(): number {
        // Memory used by objects in MB
        const { counter } = this.stats.bufferMemory
        return Math.floor(counter.value * 1000) / 1000 || 0
    }

    private getTextureMemoryUsage(): number {
        // Memory used by textures in MB
        const { counter } = this.stats.textureMemory
        return Math.floor(counter.value * 1000) / 1000 || 0
    }
}
