import { find } from 'cc'
import { Music } from '../../components/audio/Music'
import ScreenKeys from '../../constants/ScreenKeys'
import GeneralUtils from '../../utils/GeneralUtils'
import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import InitializeState from './InitializeState'

const { match, analytics, event } = globalThis.game
const { Events } = GameCore

class FinishState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.processCrazyGamesEvent()

        this.turnUpBackgroundMusic()

        this.finishMatchAsync()
        this.logAnalytics()

        this.showLevelCompleteScreen()
    }

    private processCrazyGamesEvent(): void {
        if ('gameplayStop' in GameSDK) {
            GameSDK.gameplayStop()
        }
    }

    private turnUpBackgroundMusic(): void {
        find('Music')?.getComponent(Music)?.setVolume(0.8)
    }

    private async finishMatchAsync(): Promise<void> {
        try {
            if (!this.game) return
            await match.single.finish.processAsync()
        } catch (error) {
            console.warn('FinishState', error)
        }
    }

    private logAnalytics(): void {
        if (!this.game) return
        const { level = 0 } = this.data
        analytics.levelFail(level)
    }

    private async showLevelCompleteScreen(): Promise<void> {
        const { level } = this.data

        if (this.game) {
            await GeneralUtils.showInterstitialAdAsync({
                component: this.game,
                forLevel: level,
            })
        }

        GeneralUtils.playSound('sounds/level_complete')

        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LEVEL_COMPLETE_SCREEN,
            screenData: { level },
        })
    }

    public initialize(): void {
        const state = new InitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // Don't allow ready in finish state
    }

    public playing(): void {
        // Don't allow playing in finish state
    }

    public fail(): void {
        // Don't allow fail in finish state
    }

    public complete(): void {
        // Don't allow complete in finish state
    }

    public finish(): void {
        // This is finish state, do nothing
    }

    public update(): void {
        //
    }
}

export default FinishState
