import { Component, ScrollView, Vec2, _decorator, find } from 'cc'
import { SceneManager } from '../../components/SceneManager'
import CollectionSceneEvents from '../../gameplay/constants/CollectionSceneEvents'
import { ThemeManager } from '../../gameplay/manager/ThemeManager'
import { ThemeBlock } from './components/theme-tabs/ThemeBlock'

const { ccclass, disallowMultiple, requireComponent } = _decorator

const { audio } = globalThis.game

@ccclass('CollectionsScene')
@disallowMultiple
@requireComponent([SceneManager])
export class CollectionsScene extends Component {
    private scene!: SceneManager
    private themeManager!: ThemeManager

    private themeScrollView!: ScrollView

    public __preload() {
        this.scene = this.getComponent(SceneManager)!
        this.themeManager = this.getComponent(ThemeManager)!

        this.scene.preloadScene('DashboardScene')
        this.getThemeScrollView()
        this.validateRequiredComponents()

        this.listenGameEvents()
        this.scene.listenSwitchSceneEvent(this.handleSwitchSceneEvent)
    }

    private getThemeScrollView() {
        this.themeScrollView = find('Canvas/UI/ScreenContainer/ThemeScrollView')?.getComponent(
            ScrollView
        )!
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('CollectionsScene: SceneManager not found')
        }

        if (!this.themeManager) {
            throw new Error('CollectionsScene: ThemeManager not found')
        }

        if (!this.themeScrollView) {
            throw new Error('CollectionsScene: ThemeScrollView not found')
        }
    }

    private listenGameEvents() {
        this.node.scene.on(CollectionSceneEvents.FADE_IN_COMPLETE, this.handleFadeInComplete)
    }

    public start() {
        this.themeManager.init()

        this.updateVolumeBackgroundMusic()
        this.updateThemeUnlockedProgress()
    }

    private updateThemeUnlockedProgress() {
        const collectionsPayloads = this.themeManager.getCollectionUnlockProgress()

        if (collectionsPayloads.length === 0) return

        for (let i = 0; i < collectionsPayloads.length; i++) {
            const { theme, progress, max, isLocked } = collectionsPayloads[i]

            this.setProgressToThemeTab(theme, progress, max, isLocked)
        }
    }

    private getCurrentUnlockingTheme() {
        const collectionsPayloads = this.themeManager.getCollectionUnlockProgress()

        if (collectionsPayloads.length === 0) return null

        let currentUnlockingTheme = collectionsPayloads[0]

        for (let i = 0; i < collectionsPayloads.length; i++) {
            const { isLocked } = collectionsPayloads[i]

            if (!isLocked) {
                currentUnlockingTheme = collectionsPayloads[i]
            }
        }

        return currentUnlockingTheme
    }

    private handleFadeInComplete = () => {
        const currentUnlockingTheme = this.getCurrentUnlockingTheme()
        if (!currentUnlockingTheme) return

        this.scrollToCurrentUnlockingTheme(currentUnlockingTheme.theme)
    }

    private scrollToCurrentUnlockingTheme(theme: string) {
        const contents = find('Canvas/UI/ScreenContainer/ThemeScrollView/Contents')

        if (!contents) return

        let targetThemeTab: ThemeBlock | null = null

        for (let i = 0; i < contents.children.length; i++) {
            const child = contents.children[i]
            const themeTab = child.getComponent(ThemeBlock)

            if (!themeTab) continue

            if (themeTab.themeName !== theme) continue

            targetThemeTab = themeTab
            break
        }

        if (!targetThemeTab) return

        const { y } = targetThemeTab.node.position

        // ? 407.5 is the offset between first theme tab and the top of the scroll view
        this.themeScrollView.scrollToOffset(new Vec2(0, (y + 407.5) * -1), 2)
    }

    private setProgressToThemeTab(
        themeName: string,
        progress: number,
        max: number,
        isLocked: boolean
    ) {
        const contents = find('Canvas/UI/ScreenContainer/ThemeScrollView/Contents')

        if (!contents) return

        contents.children.forEach((child) => {
            const themeTab = child.getComponent(ThemeBlock)

            if (!themeTab) return

            if (themeTab.themeName !== themeName) return

            themeTab.setPayload({
                progress,
                max,
                isLocked,
            })
            themeTab.updateThemeBlockDisplay()
        })
    }

    private updateVolumeBackgroundMusic() {
        // ? Channel 'global_music' has been created in 'LoadScene' as a persistent node for playing background music across all scenes.
        audio.setVolume(1, 'global_music')
    }

    private handleSwitchSceneEvent = (payload: SwitchScenePayload) => {
        const { sceneName, sceneData } = payload
        this.scene.switchScene(sceneName, sceneData)
    }
}
