import {
    Animation,
    Component,
    Node,
    ParticleSystem2D,
    Prefab,
    UIOpacity,
    UITransform,
    Vec3,
    Widget,
    _decorator,
    find,
    instantiate,
    randomRange,
    tween,
} from 'cc'
import { Vibrate } from '../../components/vibrate/Vibrate'
import { BackgroundMask } from '../../screens/components/BackgroundMask'
import AnimUtils from '../../utils/AnimUtils'
import CocosUtils from '../../utils/CocosUtils'
import GeneralUtils from '../../utils/GeneralUtils'
import Grid from '../game-objects/grid/Grid'
import Tile from '../game-objects/tile/Tile'
import { AFPool } from '../utils/pool/AFPool'

const { ccclass, property } = _decorator

@ccclass('EffectManager')
export default class EffectManager extends Component {
    @property({ group: 'Thunder Effect', type: Node })
    private cloud!: Node

    @property({ group: 'Thunder Effect', type: Node })
    private lightnings: Node[] = []

    @property({ group: 'Mask Effect', type: BackgroundMask })
    private backgroundMask!: BackgroundMask

    @property({ group: 'Mask Effect', type: Node })
    private masks: Node[] = []

    @property({ group: 'Tornado Effect', type: Node })
    private tornado!: Node

    @property({ group: 'Bomb Effect', type: Prefab })
    private bombExplosion!: Prefab
    private bombExplosionPool!: AFPool<Node>

    @property({ group: 'Bomb Effect', type: Prefab })
    private defuseExplosion!: Prefab
    private defuseExplosionPool!: AFPool<Node>

    @property({ group: 'Bomb Effect', type: Node })
    private gridNode!: Node

    @property({ group: 'Clear Particle Effect', type: ParticleSystem2D, displayName: 'Tile 1' })
    private clearParticle1!: ParticleSystem2D

    @property({ group: 'Clear Particle Effect', type: ParticleSystem2D, displayName: 'Tile 2' })
    private clearParticle2!: ParticleSystem2D

    protected __preload(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.cloud) {
            throw new Error('EffectManager: cloud is not assigned!')
        }

        if (!this.lightnings || this.lightnings.length === 0) {
            throw new Error('EffectManager: lightning is not assigned!')
        }

        if (!this.backgroundMask) {
            throw new Error('EffectManager: backgroundMask is not assigned!')
        }

        if (!this.masks || this.masks.length === 0) {
            throw new Error('EffectManager: masks is not assigned!')
        }

        if (!this.tornado) {
            throw new Error('EffectManager: tornado is not assigned!')
        }

        if (!this.bombExplosion) {
            throw new Error('EffectManager: bombExplosion is not assigned!')
        }

        if (!this.defuseExplosion) {
            throw new Error('EffectManager: defuseExplosion is not assigned!')
        }
    }

    protected start(): void {
        this.bombExplosionPool = new AFPool<Node>(
            this.createBombExplosion.bind(this),
            1,
            BombExplosion.getBombExplosion,
            BombExplosion.returnBombExplosion,
            BombExplosion.destroyBombExplosion
        )

        this.defuseExplosionPool = new AFPool<Node>(
            this.createDefuseExplosion.bind(this),
            1,
            DefuseExplosion.getBombDefuseExplosion,
            DefuseExplosion.returnBombDefuseExplosion,
            DefuseExplosion.destroyBombDefuseExplosion
        )
    }

    public playClearFXAt(positions: [Vec3, Vec3]) {
        this.clearParticle1.node.setWorldPosition(new Vec3(positions[0].x, positions[0].y, 0))
        this.clearParticle2.node.setWorldPosition(new Vec3(positions[1].x, positions[1].y, 0))

        this.clearParticle1.resetSystem()
        this.clearParticle2.resetSystem()
    }

    public playTornadoAnimation() {
        this.tornado.active = true

        const t = tween(this.tornado)
        t.set({ scale: new Vec3(0, 0, 0) })
        t.delay(0.5)
        t.to(0.5, { scale: new Vec3(1, 1, 1) }, { easing: 'quadOut' })
        t.to(0.5, { scale: new Vec3(0, 0, 0) }, { easing: 'quadIn' })
        t.set({ active: false })

        return AnimUtils.runTweenAsync(t)
    }

    private async shakeGame(): Promise<void> {
        const defaultPos = this.node.position.clone()

        const shakeTween = tween(this.node)
            .to(0.05, {
                position: defaultPos.clone().add3f(randomRange(5, 10), randomRange(5, 10), 0),
            })
            .to(0.05, {
                position: defaultPos.clone().subtract3f(randomRange(5, 10), randomRange(5, 10), 0),
            })
            .to(0.05, {
                position: defaultPos,
            })

        await AnimUtils.runTweenAsync(shakeTween)
    }

    public async playThunderAnimation(pairs: [Tile, Tile][], grid: Grid): Promise<void> {
        this.cloud.active = true

        const screenWidth = CocosUtils.getScreenWidthCss()
        const screenHeight = CocosUtils.getScreenHeightCss()

        const direction = Math.floor(Math.random() * 4)
        let initialX, initialY

        switch (direction) {
            case 0: // Top
                initialX = Math.random() * screenWidth - screenWidth / 2
                initialY = screenHeight / 2 + 300
                break
            case 1: // Bottom
                initialX = Math.random() * screenWidth - screenWidth / 2
                initialY = -screenHeight / 2 - 300
                break
            case 2: // Left
                initialX = -screenWidth / 2 - 300
                initialY = Math.random() * screenHeight - screenHeight / 2
                break
            case 3: // Right
                initialX = screenWidth / 2 + 300
                initialY = Math.random() * screenHeight - screenHeight / 2
                break
            default:
                initialX = 0
                initialY = 0
                break
        }

        this.cloud.position = new Vec3(initialX, initialY)

        this.resetMasks()
        this.setMaskProperties(0, pairs[0][0].node.worldPosition, pairs[0][0].node.worldScale)
        this.setMaskProperties(1, pairs[0][1].node.worldPosition, pairs[0][1].node.worldScale)
        this.setMaskProperties(2, pairs[1][0].node.worldPosition, pairs[1][0].node.worldScale)
        this.setMaskProperties(3, pairs[1][1].node.worldPosition, pairs[1][1].node.worldScale)
        this.backgroundMask.node.active = true
        this.backgroundMask.getComponent(Widget)!.updateAlignment()

        await this.setBackgroundMaskOpacity(130)

        const cloudTween = tween(this.cloud).to(
            0.8,
            { position: new Vec3(0, 300) },
            { easing: 'quadOut' }
        )

        await AnimUtils.runTweenAsync(cloudTween)

        return new Promise((resolve) => {
            find('Vibrate')?.getComponent(Vibrate)?.vibrate(500)

            const tile1Position = pairs[0][0].node.worldPosition.clone()
            const tile2Position = pairs[0][1].node.worldPosition.clone()
            const tile4Position = pairs[1][1].node.worldPosition.clone()
            const tile3Position = pairs[1][0].node.worldPosition.clone()

            this.shakeGame()
            GeneralUtils.playSound('sounds/thunderbolt')
            this.playLightningAnimation(tile1Position, 0)
            this.playLightningAnimation(tile2Position, 1).then(() => {
                this.shakeGame()
                GeneralUtils.playSound('sounds/thunderbolt')
                find('Vibrate')?.getComponent(Vibrate)?.vibrate(500)
                this.playLightningAnimation(tile3Position, 0)
                this.playLightningAnimation(tile4Position, 1).then(() => {
                    this.cloud.active = false
                    this.setBackgroundMaskOpacity(0).then(() => {
                        this.backgroundMask.node.active = false
                        resolve()
                    })
                })
            })
            grid.clearTilesAt(pairs, 'break')
        })
    }

    private playLightningAnimation(position: Vec3, index: number): Promise<void> {
        const lightning = this.lightnings[index]
        const direction = position.subtract(lightning.worldPosition)
        const angle = Math.atan2(direction.y, direction.x) * (180 / Math.PI) + 180
        const distance = direction.length()

        lightning.getComponent(UITransform)!.width = distance
        lightning.angle = angle
        return new Promise((resolve) => {
            lightning.active = true
            this.scheduleOnce(() => {
                lightning.active = false
                resolve()
            }, 0.2)
        })
    }

    private setBackgroundMaskOpacity(opacity: number): Promise<void> {
        return new Promise((resolve) => {
            tween(this.backgroundMask.getComponent(UIOpacity)!)
                .to(
                    0.25,
                    { opacity },
                    {
                        onComplete: () => {
                            resolve()
                        },
                    }
                )
                .start()
        })
    }

    public resetMasks(): void {
        this.masks.forEach((mask) => {
            mask.worldPosition = new Vec3(-9999, -9999, 0)
        })
    }

    public setMaskProperties(index: number, position: Vec3, scale?: Vec3) {
        if (scale) {
            this.masks[index].worldScale = scale
        }
        this.masks[index].worldPosition = position
    }

    public createDefuseExplosionAt(position: Vec3): Promise<void> {
        const defuseExplosion = this.defuseExplosionPool.alloc()
        defuseExplosion.setPosition(position)

        const anim = defuseExplosion.getComponent(Animation)!

        return new Promise((resolve) => {
            anim.once(Animation.EventType.FINISHED, () => {
                this.defuseExplosionPool.free(defuseExplosion)
                resolve()
            })

            anim.play()
        })
    }

    public createBombExplosionAt(position: Vec3): Promise<void> {
        const bombExplosion = this.bombExplosionPool.alloc()
        bombExplosion.setPosition(position)

        const anim = bombExplosion.getComponent(Animation)!

        return new Promise((resolve) => {
            anim.once(Animation.EventType.FINISHED, () => {
                this.bombExplosionPool.free(bombExplosion)
                resolve()
            })

            anim.play()
        })
    }

    private createDefuseExplosion(): Node {
        return DefuseExplosion.createDefuseExplosion(this.defuseExplosion, this.gridNode)
    }

    private createBombExplosion(): Node {
        return BombExplosion.createBombExplosion(this.bombExplosion, this.gridNode)
    }
}

class DefuseExplosion {
    public static createDefuseExplosion(bombDefuseExplosionPrefab: Prefab, parent: Node): Node {
        const bombDefuseExplosion = instantiate(bombDefuseExplosionPrefab)
        parent.addChild(bombDefuseExplosion)

        bombDefuseExplosion.active = false

        return bombDefuseExplosion
    }

    public static destroyBombDefuseExplosion(node: Node) {
        node.destroy()
    }

    public static getBombDefuseExplosion(node: Node) {
        node.active = true
    }

    public static returnBombDefuseExplosion(node: Node) {
        node.active = false
    }
}

class BombExplosion {
    public static createBombExplosion(bombExplosionPrefab: Prefab, parent: Node): Node {
        const bombExplosion = instantiate(bombExplosionPrefab)
        parent.addChild(bombExplosion)

        bombExplosion.active = false

        return bombExplosion
    }

    public static destroyBombExplosion(node: Node) {
        node.destroy()
    }

    public static getBombExplosion(node: Node) {
        node.active = true
    }

    public static returnBombExplosion(node: Node) {
        node.active = false
    }
}
