import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

const { ads, event } = globalThis.game

@ccclass('RewardedAdsTest')
export class RewardedAdsTest extends Component {
    public show() {
        this.showRewardedAds()
    }

    private async showRewardedAds() {
        try {
            event.emit(GameCore.Events.OPEN_SCREEN, {
                screenName: ScreenKeys.LOADING_SCREEN,
                screenData: { message: 'Loading ads...' },
            })

            await ads.preloadRewardedVideoAsync()

            await ads.showRewardedVideoAsync()

            event.emit(GameCore.Events.CLOSE_SCREEN, { screenName: ScreenKeys.LOADING_SCREEN })

            event.emit(GameCore.Events.OPEN_SCREEN, {
                screenName: ScreenKeys.NOTIFICATION_SCREEN,
                screenData: { message: 'Ads finished.', duration: 2 },
            })
        } catch (error) {
            console.warn('showTips failed', error)

            if (
                typeof error === 'object' &&
                error &&
                'code' in error &&
                error.code === 'USER_INPUT'
            ) {
                event.emit(GameCore.Events.OPEN_SCREEN, {
                    screenName: ScreenKeys.NOTIFICATION_SCREEN,
                    screenData: {
                        message: 'Sorry, no rewards!\n You have cancelled the ad.',
                        duration: 2,
                    },
                })
                return
            }

            event.emit(GameCore.Events.OPEN_SCREEN, {
                screenName: ScreenKeys.NOTIFICATION_SCREEN,
                screenData: {
                    message: 'No ads to display.',
                    duration: 2,
                },
            })
        } finally {
            event.emit(GameCore.Events.CLOSE_SCREEN, { screenName: ScreenKeys.LOADING_SCREEN })
        }
    }

    public close() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }
}
