import { Animation, Component, Sprite, SpriteFrame, _decorator } from 'cc'
import GameplayEvents from '../../../../../gameplay/constants/GameEvents'
const { ccclass, property } = _decorator

@ccclass('ComboPopup')
export class ComboPopup extends Component {
    @property(Sprite)
    private textSprite!: Sprite
    @property(SpriteFrame)
    private comboSprites: SpriteFrame[] = []
    @property(Animation)
    private animation!: Animation

    onLoad() {
        this.validateRequiredComponents()
    }

    protected start() {
        this.node.scene.on(GameplayEvents.COMBO_UPDATED, this.handleOnComboUpdated, this)
    }

    private validateRequiredComponents() {
        if (!this.textSprite) {
            throw new Error('ComboPopup: textSprite not found')
        }

        if (this.comboSprites.length === 0) {
            throw new Error('ComboPopup: comboSprites not found')
        }

        if (!this.animation) {
            throw new Error('ComboPopup: animation not found')
        }
    }

    public handleOnComboUpdated(combo: number): void {
        if (combo < 2) return

        for (let i = this.comboSprites.length - 1; i >= 0; i--) {
            if (combo >= i + 2) {
                this.textSprite.spriteFrame = this.comboSprites[i]

                this.animation.play()

                break
            }
        }
    }
}
