import { Animation, CCInteger, CCString, Component, _decorator } from 'cc'
const { ccclass, requireComponent, property } = _decorator

@ccclass('BubbleAnimation')
@requireComponent([Animation])
export class BubbleAnimation extends Component {
    @property(Animation)
    private animation!: Animation

    @property(CCString)
    private clipName = 'Bubble'

    @property(CCInteger)
    private repeat = -1

    private currentRepeat = -1

    public onLoad(): void {
        this.animation = this.getComponent(Animation)!
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.animation) {
            throw new Error('BubbleAnimation: Animation component not found')
        }
    }

    public play(): void {
        if (!this.animation) return

        const animClip = this.animation.clips.find((clip) => clip?.name === this.clipName)

        if (!animClip) {
            console.warn(`BubbleAnimation: Animation component must have ${this.clipName} clip`)
            return
        }

        this.currentRepeat = -1
        this.animation.play(this.clipName)
    }

    /**
     * `bubble` anim event callback
     */
    public onBubbleAnimationLoop(): void {
        if (!this.animation) return

        if (this.repeat === -1) return

        this.currentRepeat++
        if (this.currentRepeat > this.repeat) {
            this.animation.stop()
        }
    }
}
