import { ccenum } from 'cc'

const path = 'sounds/'

export const CodeSoundKeys = {
    // TODO: replace BUTTON_CLICK
    BUTTON_CLICK: `${path}use_item_click`,

    BOMB_DEFUSE: `${path}bomb_defuse`,
    BOMB_EXPLODE_1: `${path}bomb_explode_1`,
    BOMB_EXPLODE_2: `${path}bomb_explode_2`,
    CLICKS: `${path}clicks`,
    CLOSE_POPUP_CLICK: `${path}close_popup_click`,
    COLLECT_STARS: `${path}collect_stars`,
    COMBO_1: `${path}combo_1`,
    COMBO_2: `${path}combo_2`,
    COMBO_3: `${path}combo_3`,
    COMBO_4: `${path}combo_4`,
    COMBO_5: `${path}combo_5`,
    DAILY_REWARD: `${path}daily_reward`,
    EXTRA_TIME_SPAWN: `${path}extra_time_spawn`,
    GIFT_CHARGE: `${path}gift_charge`,
    GIFT_EXPLODE: `${path}gift_explode`,
    GIFT_REVEAL: `${path}gift_reveal`,
    HINT_ITEM: `${path}hint_item`,
    INVALID_MATCH: `${path}invalid_match`,
    LEVEL_COMPLETE: `${path}level_complete`,
    LEVEL_FAIL: `${path}level_fail`,
    LEVEL_START: `${path}level_start`,
    PROGRESS_STAR: `${path}progress_star`,
    RESCUE_TIMER: `${path}rescue_timer`,
    ROCKET_FLY: `${path}rocket_fly`,
    SHUFFLE_ITEM: `${path}shuffle_item`,
    THREE_STARS: `${path}three_stars`,
    TICKING_BOMB_BEFORE_EXPLODE: `${path}ticking_bomb_before_explode`,
    TILE_SELECT: `${path}tile_select`,
    USE_ITEM_CLICK: `${path}use_item_click`,
    BG_MUSIC: `${path}bg_music`,
    PATCH_SHIFT: `${path}patch_shift`,
    THUNDERBOLT: `${path}thunderbolt`,
    WIND: `${path}wind`,
} as const

type ValueOf<T> = T[keyof T]
type TCodeSoundValue = ValueOf<typeof CodeSoundKeys>

const values = Object.values(CodeSoundKeys)
const SoundKeys = values.reduce((result, key, index) => {
    return { ...result, [key]: index }
}, {} as Record<TCodeSoundValue, number>)

ccenum(SoundKeys)

export default SoundKeys
export const SoundKeysMapping = values
