import { Component, Node, Vec3, View, _decorator, find } from 'cc'
import { SceneManager } from '../components/SceneManager'
import Ads from '../constants/Ads'
import ScreenKeys from '../constants/ScreenKeys'
import CocosUtils from '../utils/CocosUtils'
import GeneralUtils from '../utils/GeneralUtils'
import GameplayEvents from './constants/GameEvents'
import LevelData from './constants/LevelData'
import { InputController } from './controller/InputController'
import Grid from './game-objects/grid/Grid'
import { PathDraw } from './game-objects/path-draw/PathDraw'
import Tile from './game-objects/tile/Tile'
import { CoinManager } from './manager/CoinManager'
import { ComboManager } from './manager/ComboManager'
import EffectManager from './manager/EffectManager'
import { ItemManager } from './manager/ItemManager'
import LevelManager from './manager/LevelManager'
import { StateManager } from './manager/StateManager'
import { ThemeManager } from './manager/ThemeManager'
import { UIManager } from './manager/UIManager'
import { parseLevelGravity } from './scripts/applyGravity'
import { Path } from './scripts/getValidPath'

const { player, ads, event } = globalThis.game
const { Events, Utils } = GameCore
const { ccclass, property } = _decorator

@ccclass('Gameplay')
export class Gameplay extends Component {
    // ? Why Managers and Controllers are not marked as @property?
    // * Because they are partial of Gameplay (system) and not a component of Gameplay (node)

    // ? Why use non-null assertion operator (!) for Managers, Controllers and Components?
    // * Because they are required components of Gameplay (node)

    // Managers (System)
    public uiManager!: UIManager
    public levelManager!: LevelManager
    public themeManager!: ThemeManager
    private stateManager!: StateManager
    public coinManager!: CoinManager
    public effectManager!: EffectManager
    private itemManager!: ItemManager
    private comboManager!: ComboManager

    // Controllers (System)
    public inputController!: InputController

    // Misc
    public inputBlocker!: Node

    // Components
    @property(Grid)
    public grid!: Grid

    @property(PathDraw)
    public pathDraw!: PathDraw

    private gameData: GameData | null = null

    private defaultData: GameData | null = null

    private rescueTiles: Tile[] = []

    public onLoad(): void {
        this.initComponents()

        this.validateRequiredComponents()

        this.listenCoreEvents()
        this.listenInputEvents()

        this.showBannerAds()
    }

    private initComponents() {
        this.initUIManager()
        this.initLevelManager()
        this.initThemeManager()
        this.initStateManager()
        this.initCoinManager()
        this.initEffectManager()
        this.initItemManager()
        this.initComboManager()
        this.initInputController()
        this.initInputBlocker()
    }

    private initUIManager(): void {
        this.uiManager = this.node.parent?.getComponentInChildren(UIManager)!

        this.uiManager.initDevTools(this)
    }

    private initLevelManager(): void {
        this.levelManager = this.getComponent(LevelManager)!
    }

    private initThemeManager(): void {
        this.themeManager = this.getComponent(ThemeManager)!
    }

    private initStateManager(): void {
        this.stateManager = this.getComponent(StateManager)!
    }

    private initCoinManager(): void {
        this.coinManager = this.getComponent(CoinManager)!
    }

    private initInputController(): void {
        this.inputController = this.getComponent(InputController)!
    }

    private initEffectManager(): void {
        this.effectManager = this.getComponent(EffectManager)!
    }

    private initItemManager(): void {
        this.itemManager = this.getComponent(ItemManager)!
        this.itemManager.syncItems()
        this.itemManager.items.forEach((item) => {
            item.init(this)
        })
    }

    private initComboManager(): void {
        this.comboManager = this.getComponent(ComboManager)!
    }

    private initInputBlocker(): void {
        this.inputBlocker = find('Canvas/InputBlocker')!
    }

    private validateRequiredComponents(): void {
        if (!this.grid) {
            throw new Error('Missing required Grid component')
        }

        if (!this.pathDraw) {
            throw new Error('Missing required PathDrawManager component')
        }

        if (!this.uiManager) {
            throw new Error('Missing required UIManager component')
        }

        if (!this.levelManager) {
            throw new Error('Missing required LevelManager component')
        }

        if (!this.themeManager) {
            throw new Error('Missing required ThemeManager component')
        }

        if (!this.coinManager) {
            throw new Error('Missing required CoinManager component')
        }

        if (!this.stateManager) {
            throw new Error('Missing required StateManager component')
        }

        if (!this.effectManager) {
            throw new Error('Missing required EffectManager component')
        }

        if (!this.itemManager) {
            throw new Error('Missing required ItemManager component')
        }

        if (!this.comboManager) {
            throw new Error('Missing required ComboManager component')
        }

        if (!this.inputController) {
            throw new Error('Missing required InputController component')
        }

        if (!this.inputBlocker) {
            throw new Error('Missing required InputBlocker node')
        }
    }

    private showBannerAds(): void {
        const { BannerDisplayAdOptions } = GameCore.Configs.Ads

        const worldGameSize = View.instance.getVisibleSize()
        const bannerRemainSizeCss = CocosUtils.worldSizeToCssSize(
            worldGameSize.height * Ads.MAX_BOTTOM_BANNER_RATIO
        )

        BannerDisplayAdOptions.forEach(({ PlacementId, Position, BannerHeight }) => {
            if (Position === 'bottom' && BannerHeight > bannerRemainSizeCss) return

            ads.showBannerAdAsync(PlacementId).catch((_) => {
                // Safe to ignore
            })
        })
    }

    private listenCoreEvents(): void {
        event.clear(Events.OPENING_SCREEN)
        event.clear(Events.CLOSING_SCREEN)

        event.on(Events.OPENING_SCREEN, this.handleOpenScreen)
        event.on(Events.CLOSING_SCREEN, this.handleCloseScreen)
    }

    private handleOpenScreen = (payload: SwitchScreenPayload) => {
        const { screenName } = payload

        const screens = [
            ScreenKeys.START_LEVEL_SCREEN,
            ScreenKeys.TIME_UP_SCREEN,
            ScreenKeys.LEVEL_COMPLETE_SCREEN,
            ScreenKeys.SETTINGS_SCREEN,
            ScreenKeys.GET_ITEM_SCREEN,
        ]

        if (screens.includes(screenName)) {
            this.uiManager.disableHeader()
            this.uiManager.playExitAnimUI()
        }
    }

    private handleCloseScreen = (payload: SwitchScreenPayload) => {
        const { screenName } = payload

        const screens = [
            ScreenKeys.START_LEVEL_SCREEN,
            ScreenKeys.SETTINGS_SCREEN,
            ScreenKeys.TIME_UP_SCREEN,
            ScreenKeys.GET_ITEM_SCREEN,
        ]

        //? handle restart case, wait restart event confirm
        //? GameplayEvents.GAME_REPLAY fire after Events.CLOSING_SCREEN
        if (screenName === ScreenKeys.SETTINGS_SCREEN) {
            this.uiManager.enableHeader()
            this.scheduleOnce(this.playEntranceAnimUI, 0)
            return
        }

        if (screens.includes(screenName)) {
            this.uiManager.enableHeader()
            this.playEntranceAnimUI()
        }
    }

    private listenInputEvents(): void {
        // this.node.scene.on(GameplayEvents.GAME_START, this.handleStartGame)
        this.node.scene.on(GameplayEvents.GAME_PAUSE, this.handlePauseGame)
        this.node.scene.on(GameplayEvents.GAME_RESUME, this.handleResumeGame)
        this.node.scene.on(GameplayEvents.GAME_REPLAY, this.handleReplayGame)
        this.node.scene.on(GameplayEvents.GAME_FINISH, this.handleFinishGame)
        this.node.scene.on(GameplayEvents.NEXT_LEVEL, this.handleNextLevel)
        // this.node.scene.on(GameplayEvents.START_OVER, this.handleStartOver)
        // this.node.scene.on(GameplayEvents.SELECT_LEVEL, this.handleSelectLevel)
        this.node.scene.on(GameplayEvents.LEVEL_THEME_LOADED, this.handleThemeLoaded)
        this.node.scene.on(GameplayEvents.TILE_CLICK, this.handleTileClick)
        this.node.scene.on(GameplayEvents.GRID_MATCH, this.handleGridMatch)
        this.node.scene.on(GameplayEvents.APPLY_GRAVITY, this.handleApplyGravity)
        this.node.scene.on(GameplayEvents.TIME_OUT, this.handleClockTimeout)
        this.node.scene.on(GameplayEvents.RESCUE_START, this.handleStartRescue)
        this.node.scene.on(GameplayEvents.RESCUE_FINISHED, this.handleFinishRescue)
        this.node.scene.on(GameplayEvents.BOMB_EXPLODE, this.handleBombExplode)

        // this.node.scene.on(GameplayEvents.SKIP_LEVEL, this.handleSkipLevel)
        this.node.scene.on(GameplayEvents.USE_HINT_ITEM, this.handleUseItem)
        this.node.scene.on(GameplayEvents.USE_STORM_ITEM, this.handleUseItem)
        this.node.scene.on(GameplayEvents.USE_THUNDER_ITEM, this.handleUseItem)
        this.node.scene.on(GameplayEvents.USE_PLIERS_ITEM, this.handleUseItem)
    }

    public start(): void {
        this.initData()
        this.initGame()
        // this.initDevTools()
    }

    private initData(): void {
        // ! Debug only
        /* player.setGameData({
            level: 1,
            levelProgress: {},
            passAllLevels: false,
        }) */

        const { level = 1 } = player.getGameData() ?? {}

        const sceneData = this.getSceneData()
        const { isContextProcessing = false } = sceneData ?? {}

        this.defaultData = {
            level,
            levelData: LevelData[level - 1],
            isContextProcessing: false,
        }

        this.setGameData(this.defaultData)
        this.setGameData({ isContextProcessing })
    }

    private initGame(): void {
        if (!this.stateManager) {
            throw new Error('StateManager is not initialized')
        }

        this.stateManager.init()
        this.stateManager.initialize()

        this.themeManager.init()
        this.coinManager.init()
    }

    public setGameData(data: GameData): void {
        if (!data) return
        this.gameData = { ...this.gameData, ...data }
    }

    public getGameData(): GameData | null {
        return this.gameData
    }

    private getSceneData(): SceneData | null {
        const scene = this.node.scene.getComponent(SceneManager)
        return scene?.getSceneData(this.node.scene.name) ?? null
    }

    private handlePauseGame = (): void => {
        this.uiManager.pauseCountdown()
    }

    private handleResumeGame = (): void => {
        if (this.stateManager.isPlaying()) {
            this.uiManager.resumeCountdown()
        }

        this.processCrazyGamesGameplayStop()
    }

    private handleReplayGame = (gameEvent?: GameEvent): void => {
        const data = { ...this.defaultData, ...gameEvent }

        this.setGameData(data)
        this.stateManager.initialize()

        this.unschedule(this.playEntranceAnimUI)
    }

    private handleFinishGame = () => {
        this.stateManager.finish()
    }

    private handleNextLevel = (gameEvent: GameEvent): void => {
        if (!this.gameData) return

        if (!gameEvent) {
            throw new Error('handleNextLevel: GameEvent payload is not defined')
        }

        const { level: currentLevel } = this.gameData
        const { level = currentLevel } = gameEvent

        if (!Utils.Valid.isNumber(level)) {
            throw new Error('Level is not defined')
        }

        const nextLevel = level + 1

        player.setGameData({ level: nextLevel })

        const data = { ...this.defaultData, ...gameEvent, level: nextLevel }

        this.setGameData(data)
        this.stateManager.initialize()

        console.log('Next level', nextLevel)
    }

    private handleThemeLoaded = (frames: SpriteFrame[]) => {
        const levelData = this.levelManager.getLevelData()
        this.setGameData({ levelData })
        this.grid.create(frames, levelData).then(() => {
            this.stateManager.ready()
        })
    }

    private handleTileClick = (tile: Tile) => {
        this.stateManager.playing()
        this.grid.emitTileDown(tile)
    }

    private handleGridMatch = (path: Path, match: [Tile, Tile]) => {
        this.pathDraw.drawMatchPath(path)
        this.comboManager.addCombo()

        this.effectManager.playClearFXAt([match[0].node.worldPosition, match[1].node.worldPosition])
    }

    private handleApplyGravity = () => {
        if (!this.gameData) return
        if (!this.gameData.levelData) return

        this.grid.gravityList = parseLevelGravity(this.gameData.levelData.mode)
    }

    private handleClockTimeout = () => {
        this.stateManager.fail()
    }

    private handleStartRescue = () => {
        this.inputBlocker.active = true

        while (this.rescueTiles.length > 0) {
            const tile = this.rescueTiles.pop()
            tile?.restore()
        }
    }

    private handleFinishRescue = () => {
        this.inputBlocker.active = false
    }

    private handleBombExplode = (tile: Tile) => {
        this.rescueTiles.push(tile)

        GeneralUtils.playSound('sounds/bomb_explode_1')
        const position = this.grid.toWorldPosition(tile.getCoord())
        this.effectManager.createBombExplosionAt(new Vec3(position.x, position.y, 0))

        this.inputBlocker.active = true
        this.scheduleOnce(() => {
            this.inputBlocker.active = false
            this.handleClockTimeout()
        }, 0.8)
    }

    private handleUseItem = () => {
        this.stateManager.playing()
    }

    private processCrazyGamesGameplayStop(): void {
        if ('gameplayStop' in GameSDK) {
            GameSDK.gameplayStop()
        }
    }

    public disableRewardAdsItemForCrazyGames(): void {
        if (GameSDK.getSDKName() !== 'CrazyGames') return

        //TODO: implement for CG
    }

    private playEntranceAnimUI = (): void => {
        this.uiManager.playEntranceAnimUI()
    }
}
