import { Component, Sprite, _decorator } from 'cc'
import GameEvents from '../../constants/GameEvents'
import GameWorld from '../../constants/GameWorld'
const { ccclass } = _decorator

const { Layout } = GameWorld

@ccclass('Background')
export class Background extends Component {
    private blockLines!: Sprite[]
    private backgroundEdge!: Sprite

    public __preload(): void {
        this.backgroundEdge = this.getComponent(Sprite)!
        this.blockLines = this.getComponentsInChildren(Sprite)!

        this.validateRequiredComponents()

        this.listenGameEvents()
    }

    private validateRequiredComponents() {
        if (!this.backgroundEdge) {
            throw new Error('Background: BG Edge not found')
        }

        if (!this.blockLines) {
            throw new Error('Background: Block lines not found')
        }
    }

    private listenGameEvents() {
        this.node.scene.on(GameEvents.LAYOUT_CHANGED, this.handleSpriteTypeWhenLayoutChanged)
    }

    private handleSpriteTypeWhenLayoutChanged = (layout: string) => {
        if (layout === Layout.Landscape) {
            this.setBackgroundEdgeType(Sprite.Type.SLICED)
            this.setActiveBlockLines(true)
            this.setBackgroundEdgeEnabled(true)
        } else {
            this.setBackgroundEdgeType(Sprite.Type.SIMPLE)
            this.setActiveBlockLines(false)
            this.setBackgroundEdgeEnabled(false)
        }
    }

    private setBackgroundEdgeEnabled(enabled: boolean) {
        this.backgroundEdge.enabled = enabled
    }

    private setBackgroundEdgeType(type: number) {
        this.backgroundEdge.type = type
    }

    private setActiveBlockLines(enable: boolean) {
        this.blockLines.forEach((component) => {
            if (['BlockLineLeft', 'BlockLineRight'].includes(component.node.name)) {
                component.node.active = enable
            }
        })
    }
}
