import { Component, Scene, SceneAsset, _decorator, director } from 'cc'
const { ccclass, disallowMultiple } = _decorator

const { event } = globalThis.game
const { Events, Utils } = GameCore

@ccclass('SceneManager')
@disallowMultiple
export class SceneManager extends Component {
    private scene: Scene | null = null
    private scenes: Scenes = {}

    public onLoad() {
        this.scene = this.node.scene

        if (Utils.Valid.isDebugger()) {
            GameSDK.startGameAsync().catch(console.warn)
        }
    }

    public listenSwitchSceneEvent(callback: SwitchSceneCallback) {
        event.clear(Events.SWITCH_SCENE)
        event.on(Events.SWITCH_SCENE, callback)
    }

    public getScene(sceneName: string): Scene {
        this.createSceneList()
        return this.scenes[sceneName]
    }

    public startScene(sceneName: string, sceneData?: SceneData) {
        console.log('SceneManager.startScene', { sceneName, sceneData })
        director.loadScene(sceneName, this.onSceneLaunched(sceneData))
    }

    public runScene(sceneName: string, sceneData?: SceneData) {
        console.log('SceneManager.runScene', { sceneName, sceneData })

        this.setSceneData(sceneName, sceneData ?? {})
        const scene = this.getScene(sceneName)
        director.runScene(scene)
    }

    public switchScene = (sceneName: string, sceneData?: SceneData) => {
        console.log('SceneManager.switchScene', { sceneName, sceneData })

        if (!this.scene) {
            throw new Error('SceneManager.switchScene: scene is null')
        }

        if (this.scene.name === sceneName) return

        // ? Disable current scene
        this.scene.active = false

        if (!this.isSceneLoaded(sceneName)) {
            this.startScene(sceneName, sceneData)
            return
        }

        this.runScene(sceneName, sceneData)
    }

    public setSceneData(sceneName: string, sceneData: SceneData) {
        if (!sceneData) return

        const scene = this.getScene(sceneName)
        if (!scene) {
            console.warn('SceneManager.setSceneData', `Scene ${sceneName} not found`)
            return
        }

        if (!('data' in scene.scene)) return

        // Merge current data
        const currentData = scene.scene.data || {}
        scene.attr({ data: { ...currentData, ...sceneData } })

        // console.log('SceneManager.setSceneData', { sceneName, sceneData, currentData })
    }

    public getSceneData(sceneName: string): SceneData {
        const scene = this.getScene(sceneName)
        if (!scene) return {}

        if (!('data' in scene.scene)) return {}

        return scene.scene.data || {}
    }

    private createSceneList(): void {
        if (this.scenes) return
        this.scenes = {}
    }

    private isSceneLoaded(sceneName: string): boolean {
        this.createSceneList()
        return sceneName in this.scenes
    }

    public preloadScene(sceneName: string) {
        director.preloadScene(sceneName, this.onSceneLoaded)
    }

    private onSceneLoaded = (error: Error | null, sceneAsset?: SceneAsset) => {
        // console.log('SceneManager.onSceneLoaded', { error, sceneAsset })

        if (error) {
            console.warn('SceneManager.onSceneLoaded', error)
            return
        }

        if (!sceneAsset) return

        const sceneName = sceneAsset.name
        this.scenes[sceneName] = sceneAsset.scene
    }

    private onSceneLaunched = (sceneData?: SceneData) => (error: Error | null, scene?: Scene) => {
        // console.log('SceneManager.onSceneLaunched', { error, scene })

        if (error) {
            console.warn('SceneManager.onSceneLaunched', error)
            return
        }

        if (!scene) return

        const sceneName = scene.name

        this.createSceneList()
        this.scenes[sceneName] = scene

        this.setSceneData(sceneName, sceneData ?? {})
    }
}
