import {
    CCBoolean,
    CCFloat,
    Node as CCNode,
    Component,
    Enum,
    Tween,
    UIOpacity,
    UITransform,
    Vec3,
    _decorator,
    easing,
    tween,
} from 'cc'
import { EDITOR } from 'cc/env'
import DashboardScreens from '../../../../../../constants/DashboardScreens'
import GeneralUtils from '../../../../../../utils/GeneralUtils'
import { NavManager } from './NavManager'
const { ccclass, property, executeInEditMode } = _decorator

@ccclass('NavButton')
@executeInEditMode
export class NavButton extends Component {
    // ! Remove this when other screens are ready
    @property(CCBoolean)
    private wip = false

    public get isWIP(): boolean {
        return this.wip
    }

    @property({
        group: { name: 'Animation' },
        displayName: 'Is Selected',
        tooltip: 'This is for the editor only and will be ignored at runtime',
        editorOnly: true,
    })
    private isSelectedInEditor = false

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private animationDuration = 0.3

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedSpacing = 150

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedSpacing = 88

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedIconY = 20

    @property({ group: { name: 'Animation' } })
    private selectedIconScale = new Vec3(1, 1, 1)

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedIconY = 0

    @property({ group: { name: 'Animation' } })
    private deselectedIconScale = new Vec3(0.8, 0.8, 1)

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private selectedTextY = -10

    @property({ type: CCFloat, group: { name: 'Animation' } })
    private deselectedTextY = 0

    @property({ type: UITransform })
    private spacer!: UITransform

    @property(CCNode)
    private icon!: CCNode

    @property(UIOpacity)
    private text!: UIOpacity

    @property({ type: Enum(DashboardScreens) })
    private screenType!: DashboardScreens

    public get buttonType(): DashboardScreens {
        return this.screenType
    }

    @property(NavManager)
    private navManager!: NavManager

    private isSelected: boolean = false

    public update() {
        if (EDITOR) {
            this.onEditorUpdate()
        }
    }

    private onEditorUpdate() {
        if (this.icon === null || this.icon === undefined) {
            return
        }
        if (this.text === null || this.icon === undefined) {
            return
        }
        if (this.spacer === null || this.spacer === undefined) {
            return
        }

        if (this.isSelectedInEditor) {
            this.icon.setScale(this.selectedIconScale)
            this.icon.setPosition(this.icon.position.x, this.selectedIconY, 0)

            this.text.node.setPosition(this.text.node.position.x, this.selectedTextY, 0)

            this.spacer.width = this.selectedSpacing

            this.text.opacity = 255
        } else {
            this.icon.setScale(this.deselectedIconScale)
            this.icon.setPosition(this.icon.position.x, this.deselectedIconY, 0)

            this.text.node.setPosition(this.text.node.position.x, this.deselectedTextY, 0)

            this.spacer.width = this.deselectedSpacing

            this.text.opacity = 0
        }
    }

    public setSelected() {
        if (this.isSelected) {
            return
        }

        GeneralUtils.playSound('sounds/patch_shift')
        this.navManager.selectUsingSceneTransition(this)
    }

    public select(): Promise<void> {
        if (this.isSelected) {
            throw new Error('NavButton: Already selected')
        }

        this.isSelected = true
        return this.toPromise<void>(this.playSelectAnimation(), undefined)
    }

    public deselect(): Promise<void> {
        if (!this.isSelected) {
            throw new Error('NavButton: Already deselected')
        }

        this.isSelected = false
        return this.toPromise<void>(this.deselectAnimation(), undefined)
    }

    private playSelectAnimation() {
        return tween(this.spacer).to(
            this.animationDuration,
            {
                width: this.selectedSpacing,
            },
            {
                easing: 'linear',
                onUpdate: (_target, ratio) => {
                    if (ratio === undefined) return

                    const easedRatio = easing.quintInOut(ratio)
                    const backRatio = easing.backOut(ratio)
                    const linearRatio = easing.linear(ratio)
                    const halfRatio = ratio < 0.5 ? ratio * 2 : 1

                    const iconNextY =
                        this.deselectedIconY +
                        (this.selectedIconY - this.deselectedIconY) * backRatio

                    this.icon.setPosition(this.icon.position.x, iconNextY, 0)

                    const iconNextScale = this.deselectedIconScale
                        .clone()
                        .lerp(this.selectedIconScale, easedRatio)

                    this.icon.setScale(iconNextScale.x, iconNextScale.y, this.icon.scale.z)

                    const textNextOpacity = halfRatio * 255
                    const textNextY =
                        this.deselectedTextY +
                        (this.selectedTextY - this.deselectedTextY) * linearRatio

                    this.text.opacity = textNextOpacity
                    this.text.node.setPosition(this.text.node.position.x, textNextY, 0)
                },
            }
        )
    }

    private deselectAnimation() {
        return tween(this.spacer).to(
            this.animationDuration,
            {
                width: this.deselectedSpacing,
            },
            {
                easing: 'linear',
                onUpdate: (_target, ratio) => {
                    if (ratio === undefined) return

                    const easedRatio = easing.quintInOut(ratio)
                    const linearRatio = easing.linear(ratio)
                    const halfRatio = ratio < 0.5 ? ratio * 2 : 1

                    const iconNextY =
                        this.selectedIconY +
                        (this.deselectedIconY - this.selectedIconY) * linearRatio

                    this.icon.setPosition(this.icon.position.x, iconNextY, 0)

                    const iconNextScale = this.selectedIconScale
                        .clone()
                        .lerp(this.deselectedIconScale, easedRatio)

                    this.icon.setScale(iconNextScale.x, iconNextScale.y, this.icon.scale.z)

                    const textNextOpacity = 255 - halfRatio * 255
                    const textNextY =
                        this.selectedTextY +
                        (this.deselectedTextY - this.selectedTextY) * linearRatio

                    this.text.opacity = textNextOpacity
                    this.text.node.setPosition(this.text.node.position.x, textNextY, 0)
                },
            }
        )
    }

    private toPromise<T>(tw: Tween<unknown>, payload: T): Promise<T> {
        return new Promise((resolve) => {
            tw.call(() => resolve(payload)).start()
        })
    }

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.navManager) {
            throw new Error('NavButton: NavManager not found')
        }

        if (!this.spacer) {
            throw new Error('NavButton: Spacer not found')
        }

        if (!this.icon) {
            throw new Error('NavButton: Icon not found')
        }

        if (!this.text) {
            throw new Error('NavButton: Text not found')
        }
    }
}
