import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

const { match, player, event } = globalThis.game

@ccclass('FlowSingle')
export class FlowSingle extends Component {
    private playerId = ''

    public onLoad() {
        this.playerId = player.getPlayerId()
    }

    private showLoadingScreen(duration: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
            screenData: { duration },
        })
    }

    private closeLoadingScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    public async startSingle() {
        try {
            this.showLoadingScreen(0)

            await match.single.start.processAsync({ playerId: this.playerId })
        } catch (error) {
            console.warn('startSingle', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async finishSingle() {
        try {
            player.setPlayerDataByName('score', Math.floor(Math.random() * 2))
            await match.single.finish.processAsync()
        } catch (error) {
            console.warn('finishSingle', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }
}
