import { Component, _decorator } from 'cc'
import { BaseScreen } from '../screens/BaseScreen'
const { ccclass, disallowMultiple } = _decorator

const { event } = globalThis.game
const { Events } = GameCore

@ccclass('ScreenManager')
@disallowMultiple
export class ScreenManager extends Component {
    public screens: Screens = {}

    public __preload() {
        const baseScenes = this.getComponentsInChildren(BaseScreen)

        // ? Add all screens in this scene
        baseScenes.forEach((screen) => {
            this.addScreen(screen)
        })

        // ? Listen events by plugin Event of GameCore
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private listenScreenEvents() {
        event.on(Events.OPEN_SCREEN, this.handleOpenScreen)
        event.on(Events.CLOSE_SCREEN, this.handleCloseScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPEN_SCREEN, this.handleOpenScreen)
        event.off(Events.CLOSE_SCREEN, this.handleCloseScreen)
    }

    private handleOpenScreen = (payload: SwitchScreenPayload) => {
        const { screenName, screenData } = payload
        this.openScreen(screenName, screenData)
    }

    private handleCloseScreen = (payload: SwitchScreenPayload) => {
        const { screenName } = payload
        this.closeScreen(screenName)
    }

    public addScreen(screen: BaseScreen) {
        this.screens[screen.node.name] = screen
    }

    public getScreen(screenName: string): BaseScreen {
        return this.screens[screenName]
    }

    public openScreen(screenName: string, screenData?: ScreenData) {
        const screen = this.getScreen(screenName)
        if (!screen) {
            console.warn('ScreenManager.openScreen', `Screen ${screenName} not found`)
            return
        }

        // ? Active this screen before emit opening event
        screen.node.active = true

        const eventPayload = { screenName, screenData }

        event.emit(Events.OPENING_SCREEN, eventPayload)

        screen.open(screenData)

        event.emit(Events.OPENED_SCREEN, eventPayload)
    }

    public closeScreen(screenName: string) {
        const screen = this.getScreen(screenName)
        if (!screen) {
            console.warn('ScreenManager.closeScreen', `Screen ${screenName} not found`)
            return
        }

        if (!screen.node.active) return

        const eventPayload = { screenName }

        event.emit(Events.CLOSING_SCREEN, eventPayload)

        screen.close()
    }
}
