import { Component, Font, Label, _decorator } from 'cc'

const { ccclass, property, requireComponent, executeInEditMode, disallowMultiple } = _decorator

const { language } = globalThis.game

@ccclass('Localize')
@disallowMultiple
@requireComponent(Label)
@executeInEditMode(true)
export class Localize extends Component {
    private label!: Label
    private _key!: string

    @property({ visible: true })
    public set key(value: string) {
        if (!value) return
        this._key = value
        this.updateTransToNode()
    }

    public get key(): string {
        return this._key
    }

    @property({
        group: { name: 'Fonts', id: 'fonts', displayOrder: 1 },
        type: Font,
    })
    private enFont = null

    @property({
        group: { name: 'Fonts', id: 'fonts', displayOrder: 2 },
        type: Font,
    })
    private ruFont = null

    public onLoad() {
        this.label = this.getComponent(Label)!
        this.validateRequiredComponents()
        this.updateTransToNode()
    }

    private validateRequiredComponents() {
        if (!this.label) {
            throw new Error('Label component is not found')
        }
    }

    public start() {
        this.listenEvent()
    }

    public onDestroy() {
        this.node.scene.off('update-language', this.handleLanguageChanged)
    }

    private listenEvent() {
        this.node.scene.on('update-language', this.handleLanguageChanged)
    }

    private handleLanguageChanged = () => {
        this.updateTransToNode()
    }

    public updateTransToNode() {
        if (!this.node.active) return
        if (!this._key) return

        try {
            const trans = language.getText(this._key)

            // ? Update font
            const font = this.getFontByLang()
            this.label.font = font

            this.label.string = trans

            // ? Emit event use for other components
            this.node.emit('update-trans', trans)
        } catch (error) {
            console.warn(error)
        }
    }

    private getFontByLang(): Font {
        if (!this.enFont || !this.ruFont) {
            throw new Error('Font is not found')
        }

        const locale = language.getCurrentLocale()

        switch (locale) {
            case 'en':
                return this.enFont
            case 'ru':
                return this.ruFont
            default:
                return this.enFont
        }
    }

    /* public fontSize(size: number): number {
        // ? modSize is number modify for the font size, after update new font
        const modSize = this.getModSizeByLang()

        return size / modSize
    }

    private getModSizeByLang(): number {
        const locale = language.getCurrentLocale()

        switch (locale) {
            case 'en':
                return 1
            case 'ru':
                return 1.15
            default:
                return 1
        }
    } */
}
