import { Component, ERigidBody2DType, RigidBody2D, Sprite, Vec2, _decorator, sp } from 'cc'
const { ccclass } = _decorator

const MESSAGE_NOT_FOUND = 'not found'

@ccclass('BaseObject')
export class BaseObject extends Component {
    protected id = 0
    protected points: IVec2Like[] = []
    protected body: RigidBody2D | null = null
    protected bodyType: ERigidBody2DType = ERigidBody2DType.Static

    public sleep() {
        if (!this.body) {
            this.body = this.getComponent(RigidBody2D)
            if (!this.body) return

            this.bodyType = this.body.type
        }

        this.enabled = false
        this.body.sleep()
        this.body.type = ERigidBody2DType.Static
    }

    public wake() {
        this.enabled = true

        if (!this.body) {
            this.body = this.getComponent(RigidBody2D)
            if (!this.body) return

            this.bodyType = this.body.type
        } else {
            this.body.wakeUp()
            this.body.type = this.bodyType
        }
    }

    public hide() {
        this.enabled = false
        this.body?.sleep()

        const skeleton = this.getComponent(sp.Skeleton)
        if (skeleton) skeleton.enabled = false

        const sprite = this.getComponent(Sprite)
        if (sprite) sprite.enabled = false
    }

    public preHide() {
        this.enabled = false
        this.body?.sleep()
        this.scheduleOnce(this.hide, 0.2)
    }

    public appear() {
        this.enabled = true
        const skeleton = this.getComponent(sp.Skeleton)
        if (skeleton) skeleton.enabled = true
    }

    public setSpineAnimation(animationName: string, isLoop: boolean) {
        const skeleton = this.getComponent(sp.Skeleton)
        skeleton && skeleton.setAnimation(0, animationName, isLoop)
    }

    public getCurrentSpineAnimation(): string | null {
        const skeleton = this.getComponent(sp.Skeleton)
        if (!skeleton) return null

        return skeleton.animation
    }

    public setEnable(enabled: boolean) {
        this.enabled = enabled
    }

    public angle(x: number, y: number) {
        let angle = Math.atan2(y, x)

        if (angle < 0) {
            angle += 2 * Math.PI
        }

        return angle
    }

    public getVectorDataFromAToB(a: IVec2Like, b: IVec2Like): IVectorData {
        const dx = b.x - a.x
        const dy = b.y - a.y

        const vector = new Vec2(dx, dy).normalize()

        return { vector: vector, dx: dx, dy: dy }
    }

    public kill() {
        this.node.active = false
    }

    public revive() {
        this.node.active = true
    }

    protected throwMissingComponentException<C extends Object>(
        classConstructor: C,
        message = MESSAGE_NOT_FOUND
    ) {
        this.exception(classConstructor.constructor.name, message)
    }

    protected throwMissingPropsException(propName: string, message = MESSAGE_NOT_FOUND) {
        this.exception(propName, message)
    }

    protected exception(key: string, message: string) {
        throw new Error(`${this.constructor.name}: ${key} ${message}`)
    }

    protected lazyValidateCCProperties() {
        const ccPropNames = this.getCCPropNames()

        const requiredCCPropNames = ccPropNames.filter((propName) => !propName.startsWith('_'))
        for (const propName of requiredCCPropNames) {
            if (!this[propName as keyof this]) {
                this.throwMissingPropsException(propName)
            }
        }
    }

    private getCCPropNames(): string[] {
        const emptyArray: string[] = []
        if (!('__props__' in this.constructor)) return emptyArray

        const ccPropNames = this.constructor.__props__
        if (!Array.isArray(ccPropNames)) return emptyArray

        for (const propName of ccPropNames) {
            if (!GameCore.Utils.Valid.isString(propName)) {
                return emptyArray
            }
        }

        return ccPropNames
    }
}
