import { AudioSource } from 'cc'
type SoundKeys = GameCore.Plugins.Audio.SoundKeys

class AudioPlayer extends GameCore.Plugins.Audio.BaseAudioPlayer {
    private audioSource: AudioSource

    constructor(key: SoundKeys, audioSource: AudioSource) {
        super(key)

        this.audioSource = audioSource
    }

    public play(config?: GameCore.Plugins.Audio.PlayConfig): void {
        if (config) {
            const { volume = 1, loop = false } = config
            this.audioSource.volume = volume
            this.audioSource.loop = loop
        }
        this.audioSource.play()
    }

    public pause(): void {
        this.audioSource.pause()
    }

    public resume(): void {
        if (this.audioSource.state === AudioSource.AudioState.PAUSED) {
            this.audioSource.play()
        }
    }

    public stop(): void {
        this.audioSource.stop()
    }

    public setVolume(volume: number): void {
        this.audioSource.volume = volume
    }

    public getAudioSource(): AudioSource {
        return this.audioSource
    }
}

export default AudioPlayer
