import { Button, Component, EditBox, Node, _decorator } from 'cc'
import { Gameplay } from '../Gameplay'
import GameplayEvents from '../constants/GameEvents'
import DevItem from './item/DevItem'

const { ccclass, property } = _decorator

const { match } = globalThis.game

@ccclass('DevToolsContainer')
export class DevToolsContainer extends Component {
    @property(Node)
    public devToolsToggle!: Node

    @property({ type: DevItem, displayName: 'Dev items' })
    public devItems: DevItem[] = []

    public isActive = true
    public game: Gameplay | null = null

    public currentSpawnObjId = -1

    public start() {
        // this.devToolsToggle.on(Node.EventType.TOUCH_START, this.handleClickDevToolsButton)
        // this.prepareAllItems()
    }

    public injectGameplay(game: Gameplay) {
        this.game = game
    }

    private hideAllButtons() {
        this.getComponentsInChildren(Button).forEach((button) => {
            const { parent } = button.node
            if (!parent) return

            if (parent.name === this.devToolsToggle.name) return
            parent.active = false
        })
    }

    private showAllButtons() {
        this.getComponentsInChildren(Button).forEach((button) => {
            const { parent } = button.node
            if (!parent) return

            if (parent.name === this.devToolsToggle.name) return
            parent.active = true
        })
    }

    public handleClickDevToolsButton() {
        if (this.isActive) {
            this.hideAllButtons()
            this.isActive = false
        } else {
            this.showAllButtons()
            this.isActive = true
        }
    }

    // ? Callback for ResetDataButton
    public handleResetData() {
        //@ts-expect-error waiting update core
        game.player.setPlayerData(window.__defaultData)
    }

    public async handleNextLevel() {
        await this.finishMatch()
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL, {})
    }

    public async handlePrevLevel() {
        const level = (this.game?.getGameData()?.level || 2) - 2
        await this.finishMatch()
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL, { level })
    }

    // ? Callback for SelectLevelButton
    public async handleEnterSelectLevel(editBox: EditBox) {
        const level = Number(editBox.string)
        await this.finishMatch()
        this.node.scene.emit(GameplayEvents.NEXT_LEVEL, { level: level - 1 })
    }

    // ? Callback for button
    public handleClickWinLevelButton() {
        this.node.scene.emit(GameplayEvents.GAME_FINISH)
    }

    // ? Callback for button
    public handleClickTimeOutButton() {
        this.node.scene.emit(GameplayEvents.TIME_OUT)
    }

    // ? Callback for button
    public handleClickExplodeButton() {
        //TODO: check if in bomb level and make current time of the bomb to 1
    }

    // ? Callback for button
    public handleFillItem() {
        this.node.scene.emit(GameplayEvents.ADD_HINT_ITEMS, 2)
        this.node.scene.emit(GameplayEvents.ADD_STORM_ITEMS, 2)
        this.node.scene.emit(GameplayEvents.ADD_THUNDER_ITEMS, 2)
        this.node.scene.emit(GameplayEvents.ADD_PLIERS_ITEMS, 2)
    }

    private async finishMatch() {
        try {
            if (!this.game) return
            await match.single.finish.processAsync()
        } catch (error) {
            console.warn('FinishState', error)
        }
    }
}
