import { Component, Label, Node, ProgressBar, _decorator } from 'cc'
const { ccclass, property } = _decorator

@ccclass('ThemeProgressBar')
export class ThemeProgressBar extends Component {
    @property(Node)
    public progressBar!: Node

    @property(Node)
    public progressText!: Node

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.progressBar) {
            throw new Error('ThemeProgressBar: progressBar not found')
        }

        if (!this.progressText) {
            throw new Error('ThemeProgressBar: progressText not found')
        }
    }

    public setProgress(progress: number, max: number) {
        const progressBarComponent = this.progressBar.getComponent(ProgressBar)

        if (!progressBarComponent) {
            throw new Error('ThemeProgressBar: progressBarComponent not found')
        }

        progressBarComponent.progress = progress / max

        const progressTextComponent = this.progressText.getComponent(Label)

        if (!progressTextComponent) {
            throw new Error('ThemeProgressBar: progressTextComponent not found')
        }

        progressTextComponent.string = `${progress}/${max}`
    }
}
