import { Component, JsonAsset, _decorator, find, native, resources } from 'cc'
import { ANDROID } from 'cc/env'
import GameSettings from 'db://assets/constants/GameSettings'
import { SceneManager } from '../../components/SceneManager'
import { Music } from '../../components/audio/Music'
import { Sound } from '../../components/audio/Sound'
import SceneKeys from '../../constants/SceneKeys'
import ResourcePaths from '../../gameplay/constants/ResourcePaths'

import enData from 'db://assets/resources/locales/en.json'
import { CodeSoundKeys } from '../../constants/SoundKeys'

const { ccclass, disallowMultiple, requireComponent } = _decorator

const { Plugins, Utils, Events, Configs } = GameCore
const { ads, event, player, language, analytics } = globalThis.game

type Locale = GameCore.Plugins.Language.Locale

@ccclass('LoadScene')
@disallowMultiple
@requireComponent(SceneManager)
export class LoadScene extends Component {
    private scene!: SceneManager
    private isProgressContextData = false

    public __preload() {
        this.setupAndroidPlatformAds()
        this.processCrazyGamesLoadingStart()

        this.setupLanguage()

        this.loadLevelThemeData()

        this.scene = this.getComponent(SceneManager)!
        this.validateRequiredComponents()

        this.scene.listenSwitchSceneEvent(this.handleSwitchSceneEvent)
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('GameScene: SceneManager not found')
        }
    }

    public onLoad() {
        this.makeMusicComponentPersistent()

        this.processCrazyGamesLoadingStop()

        // const gameData = globalThis.game.player.getGameData()

        //Preload game level
        // if (gameData && gameData.level > 0) {
        //     const mapId = this.getMapByLevel(gameData.level)
        //     const levelStr = this.paddingZero(mapId.toString(), 3)

        //     resources.preload(`${ResourcePaths.MAP_JSON_PREFIX}${levelStr}`)
        // }

        this.preloadScenes()

        event.once(Events.CORE_READY, this.handleCoreReady)
    }

    private preloadScenes(): void {
        this.scene.preloadScene(SceneKeys.GAME_SCENE)
        this.scene.preloadScene(SceneKeys.DASHBOARD_SCENE)
        this.scene.preloadScene(SceneKeys.COLLECTIONS_SCENE)
    }

    public start() {
        analytics.event(Plugins.Analytics.Events.LOAD_COMPLETE)

        // Check GameCore has emit CORE_READY event
        const isGameCoreReady = event.getEventEmitCount(Events.CORE_READY) > 0
        if (isGameCoreReady && !this.isProgressContextData) {
            this.processWhenCoreReady()
        }
    }

    private handleCoreReady = () => {
        this.processWhenCoreReady()
    }

    private async processWhenCoreReady() {
        this.setupGameData()

        this.handlePauseAudio()

        await this.handleShowAdWhenLoadGame()

        this.playBackgroundMusic()

        this.preloadAds()
    }

    private async handleShowAdWhenLoadGame() {
        try {
            const { Enabled, ShowAdOnLoad } = Configs.Ads
            if (!Enabled) return

            const { Enabled: ShowAdOnLoadEnabled, DelaySeconds, PlacementId } = ShowAdOnLoad
            if (!ShowAdOnLoadEnabled) return

            if (DelaySeconds > 0) {
                await Utils.Time.sleepAsync(DelaySeconds * 1000)
            }

            const ad = ads.getAdByPlacementId(PlacementId)
            if (!ad) return

            await this.showAdWhenLoadGame(ad)
        } catch (error) {
            console.warn('handleShowAdWhenLoadGame failed', error)
        }
    }

    private loadLevelThemeData() {
        resources.load(`${ResourcePaths.LEVEL_THEME_DATA}`, JsonAsset)
    }

    private makeMusicComponentPersistent() {
        const music = this.getMusicComponent()
        if (!music) return

        music.makePersistent()
    }

    private playBackgroundMusic() {
        const music = this.getMusicComponent()
        if (!music) return

        music.play(CodeSoundKeys.BG_MUSIC, { volume: 0.5 })
    }

    /* private getMapByLevel(level: number) {
        const map = MAP.find((data) => data.id === level)
        if (map) return map.map
        return 1
    } */

    /* private paddingZero(str: string, length: number) {
        return (
            Array(length - str.length)
                .fill('0')
                .join('') + str
        )
    } */

    private handleSwitchSceneEvent = (payload: SwitchScenePayload) => {
        const { sceneName, sceneData } = payload

        // ? If your game don't have DashboardScene, ResultScene,.. you can handle it here

        this.scene.switchScene(sceneName, sceneData)
    }

    private setupAndroidPlatformAds() {
        if (!ANDROID) return

        const extra = GameSDK.extra
        if ('setStaticNativeAdMethod' in extra) {
            extra.setStaticNativeAdMethod(native.reflection.callStaticMethod)
        }
    }

    private setupGameData() {
        const gameData = player.getGameData()
        if (!gameData) return

        const { MaxLevel } = GameSettings
        const { level, totalLevel, passAllLevels, coins, levelVersion, items, themes } = gameData
        const correctTotalLevel = totalLevel === 0 ? MaxLevel : totalLevel

        // * Add default game data
        // ? Add interface from /types/game-core/player.d.ts
        player.setGameData({
            level,
            coins,
            totalLevel: correctTotalLevel,
            levelVersion,
            passAllLevels,
            items,
            themes,
        })
    }

    private setupLanguage() {
        language.add('en', enData)
        language.choose('en' as Locale)

        /* const locale = player.getPlayerSetting('language')
        if (!locale) return

        language.choose(locale as Locale) */
    }

    private async preloadAds() {
        const { Enabled, InterstitialAdOptions, RewardedVideoAdOptions } = Configs.Ads
        if (!Enabled) return

        const { INTERSTITIAL, REWARDED_VIDEO } = GameCore.Plugins.Ads.Types

        InterstitialAdOptions.forEach((option) => {
            const { PlacementId } = option
            ads.loadAdAsync(INTERSTITIAL, PlacementId).catch(() => {
                // Safe to ignore
            })
        })

        RewardedVideoAdOptions.forEach((option) => {
            const { PlacementId } = option
            ads.loadAdAsync(REWARDED_VIDEO, PlacementId).catch(() => {
                // Safe to ignore
            })
        })
    }

    private async showAdWhenLoadGame(ad: GameCore.Plugins.Ads.Ad) {
        try {
            const { type, placementId } = ad

            await ads.loadAdAsync(type, placementId)

            this.muteMusic(true)
            this.muteSound(true)

            await ads.showAdAsync(type, placementId)
        } catch (error) {
            console.warn('showAdWhenLoadGame failed', error)
        } finally {
            this.muteMusic(false)
            this.muteSound(false)
        }
    }

    private muteMusic(isMute = true): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.mute(isMute)
    }

    private muteSound(isMute = true): void {
        const sound = this.getSoundComponent()
        if (!sound) return

        sound.mute(isMute)
    }

    private getMusicComponent(): Music | null {
        const music = find('Music')?.getComponent(Music)
        if (!music) {
            console.warn('LoadScene: Music not found')
            return null
        }

        return music
    }

    private getSoundComponent(): Sound | null {
        const sound = find('Sound')?.getComponent(Sound)
        if (!sound) {
            console.warn('LoadScene: Sound not found')
            return null
        }

        return sound
    }

    private processCrazyGamesLoadingStart(): void {
        if ('gameLoadingStart' in GameSDK) {
            GameSDK.gameLoadingStart()
        }
    }

    private processCrazyGamesLoadingStop(): void {
        if ('gameLoadingStop' in GameSDK) {
            GameSDK.gameLoadingStop()
        }
    }

    private handlePauseAudio = () => {
        event.on(Events.VISIBILITY_HIDDEN, this.handleMuteAudio)
        event.on(Events.VISIBILITY_VISIBLE, this.handleUnMuteAudio)
    }

    private handleMuteAudio = () => {
        this.muteMusic()
        this.muteSound()
    }

    private handleUnMuteAudio = () => {
        // ? If ads is showing, don't unmute audio
        if (ads.isAdShowing()) return

        this.muteMusic(false)
        this.muteSound(false)
    }
}
