import { Component, JsonAsset, _decorator, resources } from 'cc'
import DefaultLevelThemeData from '../constants/DefaultLevelThemeData'
import ResourcePaths from '../constants/ResourcePaths'

const { ccclass } = _decorator
const { storage } = globalThis.game

@ccclass('ThemeManager')
export class ThemeManager extends Component {
    private levelThemeData: LevelThemeData = DefaultLevelThemeData
    public playerThemeProgress: PlayerThemeProgress | null = null

    public init() {
        this.updateLevelThemeData()
        this.updatePlayerThemeProgress()
        this.initDashboardSceneStorage()
    }

    private initDashboardSceneStorage() {
        const dashboardStorage = storage.getStorage('dashboard')
        if (dashboardStorage !== null && dashboardStorage !== undefined) {
            return
        }

        const currMap = this.getCurrentProgress()

        storage.addStorage('dashboard', {
            lastMapPayload: currMap,
            animateToCurrentLevel: false,
        })
    }

    private updateLevelThemeData() {
        const levelThemeData = resources.get(`${ResourcePaths.LEVEL_THEME_DATA}`, JsonAsset)

        if (levelThemeData) {
            this.levelThemeData = levelThemeData.json as LevelThemeData
        }
    }

    private updatePlayerThemeProgress() {
        const playerData = globalThis.game.player.getGameData()

        if (playerData?.themes && Object.keys(playerData.themes).length > 0) {
            this.playerThemeProgress = playerData.themes
            return
        }

        console.log('getPlayerThemeProgress: playerData not found, use data from level')

        const themeInfo = this.levelThemeData.levelOrder

        // init player theme progress
        this.playerThemeProgress = {} as PlayerThemeProgress

        for (const element of themeInfo) {
            this.playerThemeProgress[element.theme] = {
                currentProgress: 0,
                maxProgress: element.levelCount,
                isLocked: true,
            }
        }

        this.updatePlayerThemeProgressByLevel()
        console.log(this.playerThemeProgress)
    }

    private syncPlayerThemeProgress() {
        const gameData = globalThis.game.player.getGameData()

        globalThis.game.player.setGameData({
            ...gameData,
            themes: this.playerThemeProgress!,
        })
    }

    public getLevelTheme(level?: number): ThemeKeys {
        const playerLevel = level ?? 1

        const { levelOrder } = this.levelThemeData

        let count = 0

        for (const element of levelOrder) {
            const levelThemeInfo = element

            if (count + levelThemeInfo.levelCount >= playerLevel) {
                console.log('getLevelTheme', levelThemeInfo.theme)
                return levelThemeInfo.theme
            } else {
                count += levelThemeInfo.levelCount
            }
        }

        const lastLevelThemeInfo = levelOrder[levelOrder.length - 1]
        console.log('getLevelTheme', lastLevelThemeInfo.theme)
        return lastLevelThemeInfo.theme
    }

    public getUnlockedThemes(): string[] {
        if (this.playerThemeProgress === null || this.playerThemeProgress === undefined) {
            throw new Error(
                'getUnlockedThemes: playerThemeProgress not found. Have you called init()?'
            )
        }

        const themeKeys = Object.keys(this.playerThemeProgress) as ThemeKeys[]

        const unlockedThemes: string[] = []

        for (let i = 0; i < themeKeys.length; i++) {
            const theme = themeKeys[i]
            const { isLocked } = this.playerThemeProgress[theme]

            if (!isLocked) {
                unlockedThemes.push(theme)
            }
        }

        console.log('getUnlockedThemes', unlockedThemes)
        return unlockedThemes
    }

    public getLevelMap(level?: number): string {
        const playerLevel = level ?? 1

        const { levelOrder } = this.levelThemeData

        let count = 0

        for (const levelThemeInfo of levelOrder) {
            if (count + levelThemeInfo.levelCount >= playerLevel) {
                console.log('getLevelMap', levelThemeInfo.map)
                return levelThemeInfo.map
            } else {
                count += levelThemeInfo.levelCount
            }
        }

        const lastLevelThemeInfo = levelOrder[levelOrder.length - 1]
        console.log('getLevelMap', lastLevelThemeInfo.map)
        return lastLevelThemeInfo.map
    }

    private updatePlayerThemeProgressByLevel() {
        const gameData = globalThis.game.player.getGameData()
        const levelProgress = (gameData?.level ?? 1) - 1

        let count = levelProgress

        const { levelOrder } = this.levelThemeData

        for (const levelThemeInfo of levelOrder) {
            const { levelCount, theme } = levelThemeInfo

            if (count <= 0) continue

            const progress = count - levelCount >= 0 ? levelCount : count

            count -= progress

            console.log('getPlayerThemeProgressByLevel', theme, progress)
            this.increaseProgress(theme, progress, false)
        }
    }

    public getCollectionUnlockProgress() {
        if (this.playerThemeProgress === null) {
            throw new Error(
                'getCollectionUnlockProgress: playerThemeProgress not found. Have you called init()?'
            )
        }

        const themeKeys = Object.keys(this.playerThemeProgress) as ThemeKeys[]

        const payload: CollectionsPayload[] = []

        for (const theme of themeKeys) {
            const { currentProgress, maxProgress, isLocked } = this.playerThemeProgress[theme]

            payload.push({
                progress: currentProgress,
                max: maxProgress,
                isLocked,
                theme,
            })
        }

        console.log('getCollectionUnlockProgress', payload)
        return payload
    }

    public getCurrentProgress(): CurrentMapProgressPayload {
        if (this.playerThemeProgress === null) {
            throw new Error(
                'getCurrentProgress: playerThemeProgress not found. Have you called init()?'
            )
        }

        // get the furthest theme that is not completed. If all themes are completed, get the last theme
        const themeInfo = this.levelThemeData.levelOrder

        let currentMap = ''
        let nextMap = ''
        let progress = 0
        let max = 0

        for (let i = 0; i < themeInfo.length; i++) {
            const theme = this.playerThemeProgress[themeInfo[i].theme]

            // ! if theme is not found, use the first theme
            if (theme.currentProgress < theme.maxProgress) {
                currentMap = themeInfo[i].map
                nextMap = themeInfo[i + 1]?.map ?? currentMap
                progress = theme.currentProgress
                max = theme.maxProgress
                break
            }
        }

        if (currentMap === '') {
            const lastTheme = themeInfo[themeInfo.length - 1]
            currentMap = lastTheme.map
            nextMap = lastTheme.map
            progress = lastTheme.levelCount
            max = lastTheme.levelCount

            console.log('getCurrentProgress: all themes completed')

            return {
                currentMap: currentMap,
                nextMap: nextMap,
                progress,
                max,
            }
        }

        console.log('getCurrentProgress', currentMap, nextMap, progress, max)

        return {
            currentMap: currentMap,
            nextMap: nextMap,
            progress,
            max,
        }
    }

    public getThemeFromLevel(level: number): string {
        const { levelOrder } = this.levelThemeData

        let count = 0

        for (const levelThemeInfo of levelOrder) {
            if (count + levelThemeInfo.levelCount >= level) {
                console.log('getThemeFromLevel', levelThemeInfo.theme)
                return levelThemeInfo.theme
            } else {
                count += levelThemeInfo.levelCount
            }
        }

        const lastLevelThemeInfo = levelOrder[levelOrder.length - 1]
        console.log('getThemeFromLevel', lastLevelThemeInfo.theme)
        return lastLevelThemeInfo.theme
    }

    public increaseProgress(theme: ThemeKeys, progress: number, isSync: boolean) {
        if (this.playerThemeProgress === null) {
            throw new Error(
                'increaseProgress: playerThemeProgress not found. Have you called init()?'
            )
        }

        if (!this.playerThemeProgress[theme]) {
            console.warn('increaseProgress: theme not found', theme)
            return
        }

        const { currentProgress, maxProgress } = this.playerThemeProgress[theme]

        const newProgress =
            currentProgress + progress > maxProgress ? maxProgress : currentProgress + progress

        if (newProgress > 0) {
            // unlock current theme
            this.playerThemeProgress[theme].isLocked = false
        }

        if (newProgress === maxProgress) {
            // unlock next theme
            const themeInfo = this.levelThemeData.levelOrder
            const currThemeIndex = themeInfo.findIndex((element) => element.theme === theme)
            const nextTheme = themeInfo[currThemeIndex + 1]?.theme

            if (nextTheme) {
                this.unlockTheme(nextTheme, false)
            }
        }

        this.playerThemeProgress[theme].currentProgress = newProgress
        this.playerThemeProgress[theme].maxProgress = maxProgress

        if (isSync) this.syncPlayerThemeProgress()
    }

    public unlockTheme(theme: ThemeKeys, isSync: boolean) {
        if (this.playerThemeProgress === null) {
            throw new Error('unlockTheme: playerThemeProgress not found. Have you called init()?')
        }

        if (!this.playerThemeProgress[theme]) {
            console.warn('unlockTheme: theme not found', theme)
            return
        }

        this.playerThemeProgress[theme].isLocked = false

        if (isSync) this.syncPlayerThemeProgress()
    }
}
