import { CCBoolean, CCString, Component, Node, UIOpacity, _decorator } from 'cc'
import { ThemeProgressBar } from './common/ThemeProgressBar'
const { ccclass, property } = _decorator

@ccclass('ThemeBlock')
export class ThemeBlock extends Component {
    @property(Node)
    public mask!: Node

    @property(Node)
    public progressBar!: Node

    @property(Node)
    public lockIcon!: Node

    @property(CCString)
    public themeName!: string

    @property(UIOpacity)
    public backgroundUI!: UIOpacity

    @property(CCBoolean)
    public set isLocked(value: boolean) {
        this.payload.isLocked = value
        this.updateThemeBlockDisplay()
    }

    public get isLocked(): boolean {
        return this.payload.isLocked
    }

    private payload: ThemeBlockPayload = {
        progress: 0,
        max: 10,
        isLocked: true,
    }

    public onLoad() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.mask) {
            throw new Error('ThemeBlock: mask not found')
        }

        if (!this.progressBar) {
            throw new Error('ThemeBlock: progressBar not found')
        }

        if (!this.lockIcon) {
            throw new Error('ThemeBlock: lockIcon not found')
        }

        if (!this.themeName) {
            throw new Error('ThemeBlock: themeName not found')
        }

        if (!this.backgroundUI) {
            throw new Error('ThemeBlock: backgroundUI not found')
        }
    }

    public setPayload(payload: ThemeBlockPayload) {
        this.payload = payload
    }

    public updateThemeBlockDisplay() {
        const { progress, max, isLocked } = this.payload

        this.mask.active = isLocked
        this.progressBar.active = !isLocked
        this.lockIcon.active = isLocked

        if (isLocked) return

        const progressBarComponent = this.progressBar.getComponent(ThemeProgressBar)

        if (!progressBarComponent) {
            throw new Error('ThemeBlock: progressBarComponent not found')
        }

        progressBarComponent.setProgress(progress, max)
    }
}
