import { Component, _decorator } from 'cc'
import GameplayEvents from '../constants/GameEvents'
const { ccclass } = _decorator

const { player } = globalThis.game

const MAX_COINS = 999999999

@ccclass('CoinManager')
export class CoinManager extends Component {
    private coins: number = 0

    public onLoad(): void {
        this.validateRequiredComponents()
        this.getCoinsFromPlayerData()
        this.listenToCoinEvents()
    }

    public init(): void {
        this.updateCoins()
    }

    public listenToCoinEvents(): void {
        this.node.scene.on(GameplayEvents.ADD_COINS, this.addCoins, this)
        this.node.scene.on(GameplayEvents.REMOVE_COINS, this.removeCoins, this)
    }

    private validateRequiredComponents(): void {
        //
    }

    private getCoinsFromPlayerData(): void {
        const gameData = player.getGameData()

        if (!gameData) {
            this.coins = 0
            return
        }

        this.coins = gameData.coins
    }

    public getCoins(): number {
        return this.coins
    }

    public updateCoins(): void {
        if (this.coins > MAX_COINS) {
            this.coins = MAX_COINS
        }

        player.setGameData({ coins: this.coins })
        this.node.scene.emit(GameplayEvents.COIN_CHANGED, {
            coins: this.coins,
            displayString: this.getCoinDisplayString(),
        } satisfies CoinDataPayload)
    }

    public getCoinDisplayString(): string {
        if (this.coins < 1000) {
            return this.coins.toString()
        }

        let coins = this.coins
        let coinSuffixCount = 0

        while (coins >= 1000) {
            coins = Math.floor(coins / 1000)
            coinSuffixCount++
        }

        const coinPrefix = coins.toString()

        switch (coinSuffixCount) {
            case 1:
                return `${coinPrefix}K`

            default:
                return `${coinPrefix}M`

            // case 3:
            //     return `${coinPrefix}B`

            // case 4:
            //     return `${coinPrefix}T`

            // case 5:
            //     return `${coinPrefix}Q`
        }
    }

    public addCoins(coins: number): void {
        this.coins += coins

        this.updateCoins()
    }

    public removeCoins(coins: number): void {
        if (this.coins < coins) {
            this.node.scene.emit(GameplayEvents.REMOVE_COINS_FAILED)
            return
        }

        this.coins -= coins
        this.updateCoins()
        this.node.scene.emit(GameplayEvents.REMOVE_COINS_SUCCESS)
    }
}
