import {
    Animation,
    AnimationClip,
    Button,
    CCBoolean,
    Component,
    EventHandler,
    EventTouch,
    Input,
    Node,
    UITransform,
    _decorator,
    math,
} from 'cc'
import SoundKeys, { CodeSoundKeys, SoundKeysMapping } from '../../constants/SoundKeys'
import CocosUtils from '../../utils/CocosUtils'
import GeneralUtils from '../../utils/GeneralUtils'
const { ccclass, requireComponent, property, disallowMultiple, executionOrder } = _decorator

@ccclass('ButtonReleased')
@disallowMultiple
@requireComponent([UITransform, Button, Animation])
//? run after Button Component
@executionOrder(111)
export class ButtonReleased extends Component {
    @property(AnimationClip)
    private downClip!: AnimationClip

    @property(AnimationClip)
    private upClip!: AnimationClip

    @property(AnimationClip)
    private restClip!: AnimationClip

    @property(CCBoolean)
    public useSound = true

    @property({
        visible: function (this: ButtonReleased) {
            return this.useSound
        },
        type: SoundKeys,
    })
    private soundKey = SoundKeys[CodeSoundKeys.BUTTON_CLICK]

    @property(CCBoolean)
    public emitBeforeExit = true

    @property(EventHandler)
    public touchStartEvents: EventHandler[] = []

    @property(EventHandler)
    public touchEndEvents: EventHandler[] = []

    @property({
        tooltip: 'Add a delay after click to prevent spamming (not apply for cancel click)',
    })
    private disableSpamClick = false

    @property({
        visible: function (this: ButtonReleased) {
            return this.disableSpamClick
        },
        tooltip: 'Delay time in seconds',
    })
    private disableSpamDelay = 0.5

    private buttonComponent!: Button
    private animationComponent!: Animation

    /**
     * Handle anim scales the click area result in cancelling click
     */
    private currentClickBoundingBox: math.Rect | null = null

    /**
     * Handle double click case
     */
    private hasDown = false
    /**
     * Handle double click case
     */
    private hasUp = false

    public onLoad() {
        this.buttonComponent = this.getComponent(Button)!
        this.animationComponent = this.getComponent(Animation)!

        this.validateRequiredComponents()
    }

    protected start(): void {
        this.runAnimation(this.restClip.name)
    }

    private validateRequiredComponents() {
        if (!this.buttonComponent) {
            throw new Error('ButtonReleased: Button component not found')
        }

        if (!this.animationComponent) {
            throw new Error('ButtonReleased: Animation component not found')
        }

        const clips = this.animationComponent.clips
            .map((clip) => clip?.name)
            .filter(
                (clip): clip is string =>
                    clip !== null && clip !== undefined && typeof clip === 'string'
            )

        if (!clips.includes(this.downClip.name)) {
            this.animationComponent.addClip(this.downClip)
        }

        if (!clips.includes(this.upClip.name)) {
            this.animationComponent.addClip(this.upClip)
        }

        if (!clips.includes(this.restClip.name)) {
            this.animationComponent.addClip(this.restClip)
        }
    }

    public onEnable() {
        if (this.animationComponent.clips.length < 2) {
            console.warn(
                `ButtonAnimAddition: Animation component must have 2 clips in ${this.node.name}`
            )
            return
        }

        //? remove origin Button Event
        this.node.off(
            Input.EventType.TOUCH_END,
            //@ts-expect-error TS2339: Property '_onTouchEnded' does not exist on type 'Button'.
            this.buttonComponent._onTouchEnded,
            this.buttonComponent
        )

        //? add onClick custom handle
        this.node.on(Node.EventType.TOUCH_START, this.onTouchStart)
        this.node.on(Node.EventType.TOUCH_END, this.onTouchEnd)
        this.node.on(Node.EventType.TOUCH_CANCEL, this.onTouchCancel)
    }

    public onDisable() {
        this.node.off(Node.EventType.TOUCH_START, this.onTouchStart)
        this.node.off(Node.EventType.TOUCH_END, this.onTouchEnd)
        this.node.off(Node.EventType.TOUCH_CANCEL, this.onTouchCancel)

        this.unschedule(this.handleResetButtonState)
        this.handleResetButtonState()
    }

    private onTouchStart = (event: EventTouch) => {
        if (!this.buttonComponent.interactable) return
        if (this.hasDown) return

        this.currentClickBoundingBox = CocosUtils.getBoundingBoxToWorld(this.node)

        this.handleDown(event)
    }

    private handleDown(event: EventTouch) {
        this.hasDown = true

        this.runAnimation(this.downClip.name)

        if (this.useSound) {
            GeneralUtils.playSound(SoundKeysMapping[this.soundKey])
        }

        this.touchStartEvents.forEach((clickEvent) => {
            clickEvent.emit([event, clickEvent.customEventData, this])
        })
    }

    private onTouchEnd = (event: EventTouch) => {
        if (!this.buttonComponent.interactable) return
        if (!this.hasDown) return
        if (this.hasUp) return

        this.handleUp(event)
    }

    private onTouchCancel = (event: EventTouch) => {
        if (!this.buttonComponent.interactable) return
        if (!this.hasDown) return
        if (this.hasUp) return

        if (
            event instanceof EventTouch &&
            this.currentClickBoundingBox?.contains(event.getUILocation())
        ) {
            this.handleUp(event)
            return
        }

        this.hasUp = true

        this.runAnimation(this.upClip.name).then(() => {
            this.handleResetButtonState()
        })
    }

    private handleUp(event: EventTouch) {
        this.hasUp = true

        this.runAnimation(this.upClip.name).then(() => {
            if (this.disableSpamClick) {
                this.scheduleOnce(this.handleResetButtonState, this.disableSpamDelay)
            } else {
                this.handleResetButtonState()
            }

            if (!this.emitBeforeExit) {
                this.emitButtonEvents(event)
            }

            this.touchEndEvents.forEach((clickEvent) => {
                clickEvent.emit([event, clickEvent.customEventData, this])
            })
        })

        if (this.emitBeforeExit) {
            this.emitButtonEvents(event)
        }
    }

    private emitButtonEvents(event: EventTouch) {
        if (this.buttonComponent && !this.buttonComponent.interactable) return

        this.buttonComponent?.clickEvents.forEach((clickEvent) => {
            clickEvent.emit([event, clickEvent.customEventData])
        })
    }

    private handleResetButtonState = () => {
        this.hasDown = false
        this.hasUp = false

        if (!this.buttonComponent) return

        //Reset button scale to normal before animation
        this.runAnimation(this.restClip.name)
    }

    private runAnimation(name: string): Promise<void> {
        this.animationComponent.play(name)

        return new Promise((resolve) => {
            this.animationComponent.once(Animation.EventType.FINISHED, () => {
                resolve()
            })
        })
    }

    public debugLog() {
        console.log(`Clicked ${this.node.name}`)
    }
}
