import {
    Button,
    CCBoolean,
    CCInteger,
    Node as CCNode,
    Component,
    Label,
    UIOpacity,
    _decorator,
    tween,
} from 'cc'
import AnimUtils from '../../../utils/AnimUtils'
import { Gameplay } from '../../Gameplay'
const { ccclass, property } = _decorator

@ccclass('SpecialItem')
export class SpecialItem extends Component {
    @property({
        type: CCBoolean,
        editorOnly: true,
        tooltip: 'A debug button has infinite quantity',
    })
    protected isDevButton: boolean = false

    @property({ type: Button, tooltip: 'The button that activates the item' })
    protected button!: Button

    @property({
        type: CCInteger,
        group: 'Quantity',
        tooltip: 'The starting quantity of the item',
        displayName: 'Quantity',
    })
    private quantity: number = 0

    @property({
        type: Label,
        group: 'Quantity',
        tooltip: 'The label that shows the quantity',
        displayName: 'Label',
    })
    protected quantityLabel!: Label

    @property({
        type: CCNode,
        group: 'Quantity',
        tooltip: 'The node that shows the quantity of the item',
        displayName: 'Quantity node',
    })
    protected quantityNode!: CCNode

    @property({
        type: CCNode,
        group: 'Quantity',
        tooltip: 'The node that shows the plus icon when there is no item',
        displayName: 'Plus node',
    })
    protected plusNode!: CCNode

    protected onLoad(): void {
        this.validateRequiredComponents()
    }

    public init(_gameplay: Gameplay): void {
        // Override this method
    }

    protected start(): void {
        const quantity = this.loadQuantity()
        this.setQuantity(quantity)
    }

    protected validateRequiredComponents() {
        if (!this.button) {
            throw new Error('Item: Button component not found')
        }

        if (!this.quantityLabel) {
            throw new Error('Item: Quantity label not found')
        }

        if (!this.plusNode) {
            throw new Error('Item: Plus node not found')
        }

        if (!this.quantityNode) {
            throw new Error('Item: Quantity node not found')
        }

        if (this.requestItem === null) {
            throw new Error('Item: requestItem function not found')
        }

        if (this.loadQuantity === null) {
            throw new Error('Item: loadQuantity function not found')
        }

        if (this.saveQuantity === null) {
            throw new Error('Item: saveQuantity function not found')
        }
    }

    public async applyActivationStatus(): Promise<void> {
        const canActivate = this.canActivate()
        const uiOpacity = this.button.getComponent(UIOpacity)

        if (uiOpacity === null || uiOpacity === undefined) {
            this.button.interactable = canActivate
            return
        }

        await AnimUtils.runTweenAsync(
            tween(uiOpacity)
                .call(() => {
                    this.button.interactable = canActivate
                })
                .to(0.2, { opacity: canActivate ? 255 : 128 })
        )
    }

    public async activate(): Promise<void> {
        if (this.quantity <= 0) {
            this.requestItem()
            return
        }

        this.setQuantity(this.quantity - 1)
    }

    public canActivate(): boolean {
        return false
    }

    public setQuantity(quantity: number): void {
        if (this.isDevButton) {
            this.quantityLabel.string = 'i'
            this.quantityNode.active = true
            this.plusNode.active = false

            this.applyActivationStatus()

            return
        }

        if (quantity < 0) {
            quantity = 0
        }

        this.quantity = quantity
        this.quantityLabel.string = `${this.quantity}`

        if (quantity <= 0) {
            this.quantityNode.active = false
            this.plusNode.active = true
        } else {
            this.quantityNode.active = true
            this.plusNode.active = false
        }

        this.applyActivationStatus()
    }

    public getQuantity(): number {
        return this.quantity
    }

    public requestItem: () => void = null!
    public loadQuantity: () => number = null!
    public saveQuantity: () => void = null!
}
