import { Component, Label, _decorator } from 'cc'
import GameplayEvents from '../../../gameplay/constants/GameEvents'
const { ccclass } = _decorator

@ccclass('DemoCoinBar')
export class DemoCoinBar extends Component {
    public onLoad() {
        this.node.scene.on(GameplayEvents.COIN_CHANGED, this.updateCoinDisplay, this)
    }

    public updateCoinDisplay(payload: CoinDataPayload) {
        console.log('Update coin display', payload)

        const label = this.node.getComponent(Label)

        if (label) {
            label.string = `Current Coins: ${payload.displayString}`
        }
    }
}
