import { View, find } from 'cc'
import { Music } from '../components/audio/Music'
import { Sound } from '../components/audio/Sound'
import Ads from '../constants/Ads'
import ScreenKeys from '../constants/ScreenKeys'
import CocosUtils from './CocosUtils'

const { Events, Configs, Plugins } = GameCore
const { ads, language, event } = globalThis.game

type ShowInterAdOptions = {
    component: Component
    forLevel?: number
    excludeList?: SDKNames[]
    includeList?: SDKNames[]
    onFailedCallback?: () => void
    onSucceedCallback?: () => void
}

type ShowRewardedAdOptions = {
    excludeList?: SDKNames[]
    onFailedCallback?: () => void
    onSucceedCallback?: () => void
}

class GeneralUtils {
    public static showBannerAds(): void {
        const { BannerDisplayAdOptions } = GameCore.Configs.Ads

        const worldGameSize = View.instance.getVisibleSize()
        const bannerRemainSizeCss = CocosUtils.worldSizeToCssSize(
            worldGameSize.height * Ads.MAX_BOTTOM_BANNER_RATIO
        )

        BannerDisplayAdOptions.forEach(({ PlacementId, Position, BannerHeight }) => {
            if (Position === 'bottom' && BannerHeight > bannerRemainSizeCss) return

            ads.showBannerAdAsync(PlacementId).catch((_) => {
                // Safe to ignore
            })
        })
    }

    public static async showInterstitialAdAsync({
        component,
        forLevel,
        excludeList,
        includeList,
        onFailedCallback,
        onSucceedCallback,
    }: ShowInterAdOptions) {
        if (excludeList && excludeList.includes(GameSDK.getSDKName())) return
        if (includeList && !includeList.includes(GameSDK.getSDKName())) return

        if (forLevel !== undefined && Configs.Gameplay.StartAdAtLevel > forLevel) return

        const { INTERSTITIAL } = Plugins.Ads.Types

        if (!ads.canbeShowInterstitialAd()) return

        const beforeTime = Date.now()
        try {
            this.muteAudio(true)

            const loadingText = language.getText('loading')
            this.showNotificationScreen(loadingText)

            const adStatus = ads.getAdStatus(Plugins.Ads.Types.INTERSTITIAL)
            if (adStatus !== Plugins.Ads.Status.FILLED) {
                await ads.loadAdAsync(INTERSTITIAL)
            }

            await ads.showAdAsync(INTERSTITIAL)

            onSucceedCallback && onSucceedCallback()
        } catch (error) {
            console.error('showInterstitialAdAsync failed', error)

            onFailedCallback && onFailedCallback()
        } finally {
            this.muteAudio(false)

            const displayTime = (Date.now() - beforeTime) / 1000
            component.scheduleOnce(
                () => {
                    this.closeNotificationScreen()
                },
                displayTime > 0.5 ? 0 : 0.5
            )

            ads.loadAdAsync(INTERSTITIAL).catch(() => {
                // Safe to ignore
            })
        }
    }

    public static async showRewardVideoAdAsync({
        excludeList,
        onFailedCallback,
        onSucceedCallback,
    }: ShowRewardedAdOptions) {
        if (excludeList && excludeList.includes(GameSDK.getSDKName())) return

        const { REWARDED_VIDEO } = Plugins.Ads.Types

        try {
            this.muteAudio(true)

            const loadingText = language.getText('loading')
            this.showNotificationScreen(loadingText)

            const adStatus = ads.getAdStatus(Plugins.Ads.Types.REWARDED_VIDEO)
            if (adStatus !== Plugins.Ads.Status.FILLED) {
                await ads.loadAdAsync(REWARDED_VIDEO)
            }

            await ads.showAdAsync(REWARDED_VIDEO)

            this.closeNotificationScreen()

            onSucceedCallback && onSucceedCallback()
        } catch (error) {
            console.error('skip level failed', error)

            if (error instanceof Object && 'code' in error && error.code === 'USER_INPUT') {
                this.showNotificationScreen('Sorry, no rewards!\n You have cancelled the ad.', 2)
                return
            }

            this.showNotificationScreen('No ads to display.', 2)

            onFailedCallback && onFailedCallback()
        } finally {
            this.muteAudio(false)

            ads.loadAdAsync(REWARDED_VIDEO).catch(() => {
                // Safe to ignore
            })
        }
    }

    public static muteAudio(isMute: boolean): void {
        this.muteMusic(isMute)
        this.muteSound(isMute)
    }

    public static muteMusic(isMute: boolean): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.mute(isMute)
    }

    public static muteSound(isMute: boolean): void {
        const sound = this.getSoundComponent()
        if (!sound) return

        sound.mute(isMute)
    }

    public static playSound(name: GameCore.Plugins.Audio.SoundKeys) {
        const sound = this.getSoundComponent()
        if (!sound) return

        sound.play(name)
    }

    private static showNotificationScreen(message: string, duration?: number) {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message, duration },
        })
    }

    private static closeNotificationScreen() {
        event.emit(Events.CLOSE_SCREEN, { screenName: ScreenKeys.NOTIFICATION_SCREEN })
    }

    private static getSoundComponent(): Sound | null {
        const sound = find('Sound')?.getComponent(Sound)

        if (!sound) {
            console.warn('WinningScreen: Sound not found')
            return null
        }

        return sound
    }

    private static getMusicComponent(): Music | null {
        const music = find('Music')?.getComponent(Music)

        if (!music) {
            console.warn('WinningScreen: Music not found')
            return null
        }

        return music
    }
}

export default GeneralUtils
