import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import CompleteState from './CompleteState'
import FailState from './FailState'
import FinishState from './FinishState'
import InitializeState from './InitializeState'
import ReadyState from './ReadyState'

class PlayingState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.startCountdown()
    }

    private startCountdown(): void {
        this.game?.uiManager.startCountdown()
    }

    public initialize(): void {
        const state = new InitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        const state = new ReadyState(this.manager)
        this.manager.change(state)
    }

    public playing(): void {
        // This is playing state, do nothing
    }

    public fail(): void {
        const state = new FailState(this.manager)
        this.manager.change(state)
    }

    public complete(): void {
        const state = new CompleteState(this.manager)
        this.manager.change(state)
    }

    public finish(): void {
        const state = new FinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        // TODO: check data using
        this.updateStateData()
    }
}

export default PlayingState
