import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
import GameplayEvents from '../../../gameplay/constants/GameEvents'
const { ccclass } = _decorator

const { event } = globalThis.game
const { Events } = GameCore

@ccclass('CoinDemo')
export class CoinDemo extends Component {
    public onLoad() {
        this.node.on(GameplayEvents.REMOVE_COINS_FAILED, this.onRemoveCoinsFailed, this)
    }

    public add500Coins() {
        console.log('Add 500 coins')
        this.node.scene.emit(GameplayEvents.ADD_COINS, 500)
    }

    public remove500Coins() {
        console.log('Remove 500 coins')
        this.node.scene.emit(GameplayEvents.REMOVE_COINS, 500)
    }

    private onRemoveCoinsFailed() {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { duration: 1.5, message: 'Removing coins failed.' },
        })
    }
}
