import { Button, Component, _decorator, find } from 'cc'
import { Music } from '../../components/audio/Music'
import { Sound } from '../../components/audio/Sound'
import ScreenKeys from '../../constants/ScreenKeys'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import ItemKeys from '../../gameplay/constants/ItemKeys'
import { BaseScreen } from '../BaseScreen'
import { ItemIcon } from './components/ItemIcon'
const { ccclass, property } = _decorator

const { event, ads, language } = globalThis.game
const { Events } = GameCore

type EventPayload = GameCore.Plugins.Event.Payload

@ccclass('GetItemScreen')
export class GetItemScreen extends Component {
    private screen!: BaseScreen
    private payload!: EventPayload

    @property(ItemIcon)
    public itemIcon!: ItemIcon

    @property(Button)
    public closeButtonComponent!: Button

    @property(Button)
    public getNowButtonComponent!: Button

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('SettingsScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.itemIcon) {
            throw new Error('GetItemScreen: itemIcon not found')
        }

        if (!this.closeButtonComponent) {
            throw new Error('GetItemScreen: closeButton not found')
        }

        if (!this.getNowButtonComponent) {
            throw new Error('GetItemScreen: getNowButton not found')
        }
    }

    private listenScreenEvents() {
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: EventPayload) => {
        console.log('SettingsScreen.OPENING_SCREEN', payload)
        if (!this.validateEventPayload(payload)) return

        this.closeButtonComponent.interactable = true
        this.getNowButtonComponent.interactable = true
        this.updateStatus()
    }

    private handleOpenedScreen = (payload: EventPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: EventPayload) {
        if (!('screenName' in payload)) return false

        const { screenName } = payload
        if (screenName !== this.node.name) return false

        this.payload = payload
        return true
    }

    public open() {
        this.updateStatus()
        this.runPopUpAnimation()
    }

    public close() {
        this.closeButtonComponent.interactable = false
        this.getNowButtonComponent.interactable = false

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    private updateStatus() {
        this.updateItemIcon()
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('SettingsScreen: screen not found')
        }
    }

    private updateItemIcon() {
        const itemType = this.getItemType()

        if (itemType) {
            this.itemIcon.updateIcon(itemType)
        }

        const quantity = this.getQuantity()

        if (quantity) {
            this.itemIcon.updateQuantity(quantity)
        }
    }

    private getItemType(): string | null {
        if (!this.payload || !('screenData' in this.payload)) {
            return null
        }

        const { screenData } = this.payload

        if (!screenData) return null

        if ('item' in screenData && screenData.item && typeof screenData.item === 'string') {
            return screenData.item
        }

        return null
    }

    private getQuantity(): number {
        if (!this.payload || !('screenData' in this.payload)) {
            return 0
        }

        const { screenData } = this.payload

        if (!screenData) return 0

        if (
            'quantity' in screenData &&
            screenData.quantity &&
            typeof screenData.quantity === 'number'
        ) {
            return screenData.quantity
        }

        return 0
    }

    public handleAddItem() {
        const itemType = this.getItemType()
        const quantity = this.getQuantity()

        if (!itemType || !quantity) return

        switch (itemType) {
            case ItemKeys.HINT:
                this.node.scene.emit(GameplayEvents.ADD_HINT_ITEMS, quantity)
                break

            case ItemKeys.STORM:
                this.node.scene.emit(GameplayEvents.ADD_STORM_ITEMS, quantity)
                break

            case ItemKeys.THUNDER:
                this.node.scene.emit(GameplayEvents.ADD_THUNDER_ITEMS, quantity)
                break

            case ItemKeys.PLIERS:
                this.node.scene.emit(GameplayEvents.ADD_PLIERS_ITEMS, quantity)
                break
            default:
        }

        this.close()
    }

    // ? Callback for click event of GetNowButton
    public async handleGetNowButtonClicked(): Promise<void> {
        const { REWARDED_VIDEO } = GameCore.Plugins.Ads.Types

        const beforeTime = Date.now()

        try {
            this.stopAllSounds()
            this.muteAudio(true)

            const loadingText = language.getText('loading')
            this.showNotificationScreen(loadingText)

            await ads.showAdAsync(REWARDED_VIDEO)

            this.handleAddItem()
        } catch (error) {
            console.error('showRewardedAdAsync failed', error)
        } finally {
            this.muteAudio(false)

            const displayTime = (Date.now() - beforeTime) / 1000
            this.scheduleOnce(
                () => {
                    this.closeNotificationScreen()
                },
                displayTime > 0.5 ? 0 : 0.5
            )

            ads.loadAdAsync(REWARDED_VIDEO).catch(() => {
                // Safe to ignore
            })
        }
    }

    private muteAudio(isMute: boolean): void {
        this.muteMusic(isMute)
        this.muteSound(isMute)
    }

    private muteMusic(isMute: boolean): void {
        const music = this.getMusicComponent()
        if (!music) return

        music.mute(isMute)
    }

    private muteSound(isMute: boolean): void {
        const sound = this.getSoundComponent()
        if (!sound) return

        sound.mute(isMute)
    }

    private getMusicComponent(): Music | null {
        const music = find('Music')?.getComponent(Music)
        if (!music) {
            console.warn('LoseScreen: Music not found')
            return null
        }

        return music
    }

    private getSoundComponent(): Sound | null {
        const sound = find('Sound')?.getComponent(Sound)
        if (!sound) {
            console.warn('LoseScreen: Sound not found')
            return null
        }

        return sound
    }

    private stopAllSounds() {
        find('Sound')?.getComponent(Sound)?.stopAll()
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    private closeNotificationScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
        })
    }
}
