import { Button, _decorator, tween } from 'cc'
import { DashboardUI } from './DashboardUI'
const { ccclass, property } = _decorator

@ccclass('HomeUI')
export class HomeUI extends DashboardUI {
    @property(Button)
    private buttons: Button[] = []

    public override transitionIn(): void {
        tween(this.screenContainer)
            .call(() => {
                this.screenContainer.opacity = 0
                this.buttons.forEach((button) => (button.interactable = false))
            })
            .to(this.fadeInDuration, { opacity: 255 })
            .call(() => {
                this.buttons.forEach((button) => (button.interactable = true))
            })
            .start()
    }

    public override transitionOut(): void {
        tween(this.screenContainer)
            .call(() => {
                this.buttons.forEach((button) => (button.interactable = false))
            })
            .to(this.fadeOutDuration, { opacity: 0 })
            .start()
    }
}
