import { Component, _decorator } from 'cc'
import { Direction } from '../../constants/Directions'
import { Line, Path } from '../../scripts/getValidPath'
import Grid from '../grid/Grid'
import { HintDrawer } from './hint/HintDrawer'
import { MatchDrawer } from './match/MatchDrawer'
const { ccclass, property } = _decorator

@ccclass('PathDraw')
export class PathDraw extends Component {
    @property(Grid)
    private grid!: Grid

    @property({ group: { name: 'Path Drawer' }, type: MatchDrawer })
    private matchPath!: MatchDrawer

    // ! this is not working
    // @property({ group: { name: 'Path Drawer' }, type: ErrorDrawer })
    // private errorPath!: ErrorDrawer

    @property({ group: { name: 'Path Drawer' }, type: HintDrawer })
    private hintPath!: HintDrawer

    public drawMatchPath(path: Path): void {
        // clone path to avoid modifying the original path
        path = path.map((line) => ({ ...line }))

        for (const line of path) {
            line.a = this.grid.toWorldPosition(line.a)
            line.b = this.grid.toWorldPosition(line.b)
        }

        this.matchPath.drawPath(path)
    }

    public clearMatchPath(): void {
        this.matchPath.clearPath()
    }

    public drawHintPath(path: Path): void {
        // clone path to avoid modifying the original path
        path = path.map((line) => ({ ...line }))

        for (const line of path) {
            line.a = this.grid.toWorldPosition(line.a)
            line.b = this.grid.toWorldPosition(line.b)
        }

        this.hintPath.drawPath(path)
    }

    public drawErrorPath(path: Path): void {
        // clone path to avoid modifying the original path
        path = path.map((line) => ({ ...line }))

        for (const line of path) {
            line.a = this.grid.toWorldPosition(line.a)
            line.b = this.grid.toWorldPosition(line.b)
        }

        // ! this is not working
        // this.errorPath.drawPath(path)
    }

    public clearHintPath(): void {
        this.hintPath.clearPath()
    }

    private getDirection(line: Line): DirectionType | null {
        if (line.a === line.b) return null

        if (line.a.x === line.b.x) {
            return line.a.y < line.b.y ? Direction.Up : Direction.Down
        }

        if (line.a.y === line.b.y) {
            return line.a.x < line.b.x ? Direction.Right : Direction.Left
        }

        return null
    }
}
