import { Component, _decorator } from 'cc'
import GeneralUtils from '../../utils/GeneralUtils'
import GameplayEvents from '../constants/GameEvents'
const { ccclass, property } = _decorator

@ccclass('ComboManager')
export class ComboManager extends Component {
    private currentCombo!: number

    public start(): void {
        this.setCombo(0)

        this.node.scene.on(GameplayEvents.COMBO_ENDED, this.resetCombo, this)
        this.node.scene.on(GameplayEvents.GAME_FINISH, this.resetCombo, this)
        this.node.scene.on(GameplayEvents.GAME_REPLAY, this.resetCombo, this)
    }

    public addCombo(): void {
        this.setCombo(this.currentCombo + 1)
    }

    public resetCombo(): void {
        this.setCombo(0)
    }

    public getCombo(): number {
        return this.currentCombo
    }

    private setCombo(combo: number): void {
        this.currentCombo = combo

        if (combo > 0 && combo < 5) {
            GeneralUtils.playSound(`sounds/combo_${combo}` as GameCore.Plugins.Audio.SoundKeys)
        } else if (combo >= 5) {
            GeneralUtils.playSound('sounds/combo_5')
        }

        this.node.scene.emit(GameplayEvents.COMBO_UPDATED, combo)
    }
}
