import { UIOpacity, Vec3, _decorator, tween } from 'cc'
import { Path } from '../../../scripts/getValidPath'
import { Line } from '../Line'
import { PathDrawer } from '../PathDrawer'

const { ccclass } = _decorator

@ccclass('HintDrawer')
export class HintDrawer extends PathDrawer {
    public drawPath(path: Path): void {
        this.clearPath()
        this.createPath(path)
        this.showPath()
    }

    private createPath(path: Path): void {
        path.forEach((line) => {
            const lineNode = this.linePool.alloc()
            lineNode.getComponent(Line)?.setPoints(line.a, line.b)

            const pointNode = this.pointPool.alloc()
            pointNode.setPosition(new Vec3(line.a.x, line.a.y, 0))
        })

        const pointNode = this.pointPool.alloc()
        pointNode.setPosition(new Vec3(path[path.length - 1].b.x, path[path.length - 1].b.y, 0))
    }

    private showPath(): void {
        for (const lineNode of this.lineNodes) {
            const lineOpacity = lineNode.getComponent(UIOpacity)!
            lineOpacity.opacity = 0
            tween(lineOpacity).to(0.2, { opacity: 255 }, { easing: 'quadOut' }).start()
        }

        for (const pointNode of this.pointNodes) {
            const pointOpacity = pointNode.getComponent(UIOpacity)!
            pointOpacity.opacity = 0
            tween(pointOpacity).to(0.2, { opacity: 255 }, { easing: 'quadOut' }).start()
        }
    }

    public clearPath(): void {
        for (const lineNode of this.lineNodes) {
            const lineOpacity = lineNode.getComponent(UIOpacity)!
            tween(lineOpacity)
                .to(
                    0.15,
                    { opacity: 0 },
                    {
                        onComplete: () => {
                            lineOpacity.opacity = 255
                            this.linePool.free(lineNode)
                        },
                    }
                )
                .start()
        }

        for (const point of this.pointNodes) {
            const pointOpacity = point.getComponent(UIOpacity)!
            tween(pointOpacity)
                .to(
                    0.15,
                    { opacity: 0 },
                    {
                        onComplete: () => {
                            pointOpacity.opacity = 255
                            this.pointPool.free(point)
                        },
                    }
                )
                .start()
        }

        this.lineNodes = []
        this.pointNodes = []
    }
}
