import { Component, Label, Node, Vec3, _decorator, math, tween } from 'cc'
import GameEvents from 'db://assets/constants/GameEvents'
import GameplayEvents from 'db://assets/gameplay/constants/GameEvents'

const { ccclass, disallowMultiple, property } = _decorator

type GamePlayConfig = GameCore.Plugins.RemoteConfig.GamePlayConfig

@ccclass('Clock')
@disallowMultiple
export class Clock extends Component {
    @property(Label)
    public timeText!: Label

    @property(Node)
    public clockIcon!: Node

    private time = 0
    private remaining = 0
    private running = false

    public __preload(): void {
        this.listenEvents()
    }

    public start() {
        this.init()
    }

    public init() {
        this.running = false
    }

    private listenEvents() {
        this.node.scene.on(GameEvents.GAMEPLAY_UPDATE_CONFIG, this.handleUpdateConfig)
        this.node.scene.on(GameplayEvents.RESCUE_START, this.refillTime)
    }

    private handleUpdateConfig = (config: GamePlayConfig) => {
        if (!this.enabled) return

        console.log('Clock: handleUpdateConfig', config)
        // this.setTime(config.waitForPassSecs)
    }

    public setTime(time: number) {
        this.time = time
        this.timeText.string = this.convertSecondsToMmSs(time)
    }

    public startCountdown() {
        this.running = true
        this.remaining = this.time
    }

    public pause() {
        this.running = false
    }

    public resume() {
        this.running = true
    }

    private endCountDown() {
        this.running = false
        this.remaining = 0
        this.node.scene.emit(GameplayEvents.TIME_OUT)
    }

    public update(deltaTime: number) {
        if (!this.running) return

        if (this.remaining <= 0) {
            this.endCountDown()
            return
        }

        this.remaining -= deltaTime
        if (this.remaining >= 0) this.updateLabel()
    }

    private updateLabel() {
        this.timeText.string = this.convertSecondsToMmSs(this.remaining)
    }

    private convertSecondsToMmSs(seconds: number): string {
        const minutes: number = Math.floor(seconds / 60)
        const remainingSeconds: number = Math.floor(seconds % 60)

        const formattedMinutes: string = this.padZero(minutes)
        const formattedSeconds: string = this.padZero(remainingSeconds)

        return `${formattedMinutes}:${formattedSeconds}`
    }

    private padZero(number: number): string {
        return number < 10 ? `0${number}` : `${number}`
    }

    private refillTime = () => {
        const addedTime = 120

        const startedTime = Date.now()

        const scaleUpTween = tween(this.node).to(0.5, { scale: new Vec3(1.1, 1.1, 1) })

        const scaleDownTween = tween(this.node).to(0.5, { scale: new Vec3(1, 1, 1) })

        const clockIconTween = tween(this.clockIcon)
            .to(0.15, { angle: -15 }, { easing: 'cubicOut' })
            .to(0.15, { angle: 0 }, { easing: 'cubicIn' })
            .to(0.15, { angle: 15 }, { easing: 'cubicOut' })
            .to(0.15, { angle: 0 }, { easing: 'cubicIn' })

        const clockIconTweenLoop = tween(this.clockIcon).repeat(5, clockIconTween)

        const counterTween = tween(this.node)
            .to(
                3,
                { position: new Vec3(0, 0, 0) },
                {
                    onUpdate: () => {
                        const now = Date.now()
                        const progress =
                            (now - startedTime) / 3000 > 1 ? 1 : (now - startedTime) / 3000
                        const current = math.lerp(0, addedTime, progress)

                        this.setTime(current)
                    },
                }
            )
            .call(() => {
                this.setTime(addedTime)

                scaleDownTween.start()

                this.node.scene.emit(GameplayEvents.RESCUE_FINISHED)
            })

        tween(this.node).parallel(scaleUpTween, counterTween).start()
        clockIconTweenLoop.start()
    }
}
