import { Node as CCNode, Component, Enum, UITransform, _decorator } from 'cc'
import DashboardScreens from '../../../../constants/DashboardScreens'
const { ccclass, property, requireComponent } = _decorator

@ccclass('DashboardScreen')
@requireComponent(UITransform)
export class DashboardScreen extends Component {
    @property({ type: Enum(DashboardScreens) })
    private screenType!: DashboardScreens

    @property({ type: CCNode, displayName: 'Performance Intensive Nodes' })
    private perfIntensiveNodes: CCNode[] = []

    @property(CCNode)
    private container!: CCNode

    private uiTransform!: UITransform

    public enableContainer(): void {
        this.container.active = true
    }

    public disableContainer(): void {
        this.container.active = false
    }

    public getWidth(): number {
        return this.uiTransform.width
    }

    public getScreenType(): DashboardScreens {
        return this.screenType
    }

    public onNavigatedIn(): Promise<void> {
        this.perfIntensiveNodes.forEach((node) => {
            node.active = true
        })

        return Promise.resolve()
    }

    public onNavigatedOut(): Promise<void> {
        this.perfIntensiveNodes.forEach((node) => {
            node.active = false
        })

        return Promise.resolve()
    }

    public onLoad(): void {
        this.uiTransform = this.getComponent(UITransform)!

        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.uiTransform) {
            throw new Error('DashboardScreen requires UITransform component')
        }
    }
}
