import { Component, UIOpacity, _decorator } from 'cc'
import { GridEvents } from '../../constants/GridEvents'
import Grid from '../grid/Grid'
const { ccclass, property, requireComponent } = _decorator

@ccclass('PathHolder')
@requireComponent(UIOpacity)
export class PathHolder extends Component {
    private uiOpacity!: UIOpacity

    @property(Grid)
    private grid!: Grid

    public onLoad(): void {
        this.uiOpacity = this.getComponent(UIOpacity)!

        this.validateRequiredComponents()
    }

    public start(): void {
        this.grid.node.on(GridEvents.CREATE, () => {
            this.node.scale = this.grid.node.scale
        })

        // TODO: fix this
        /* this.node.scene.on('game-state-updated', (state: GameState) => {
            // if (state instanceof PauseState) {
            //     this.uiOpacity.opacity = 0
            // } else {
            //     this.uiOpacity.opacity = 255
            // }
        }) */
    }

    private validateRequiredComponents(): void {
        if (!this.grid) {
            throw new Error(`${this.node.name}: grid not found`)
        }

        if (!this.uiOpacity) {
            throw new Error(`${this.node.name}: UIOpacity not found`)
        }
    }
}
