import { EventTarget } from 'cc'
import { GridEvents } from '../../../constants/GridEvents'
import findFirstMatch from '../../../scripts/findFirstMatch'
import shuffleTiles, { ShuffleConfig } from '../../../scripts/shuffleTiles'
import Tile from '../../tile/Tile'
import Grid from '../Grid'
import { State } from '../GridMachine'
import Idle from './Idle'

export const ShuffleEvent = {
    SHUFFLE_START: 'SHUFFLE_START',
    SHUFFLE: 'SHUFFLE',
    SHUFFLE_COMPLETE: 'SHUFFLE_COMPLETE',
} as const

export type ShuffleEvent = typeof ShuffleEvent[keyof typeof ShuffleEvent]

export default class Shuffle extends State {
    private config: ShuffleConfig | undefined
    readonly eventEmitter: EventTarget
    private force: boolean

    constructor(grid: Grid, config?: ShuffleConfig, force = false) {
        super(grid)
        this.config = config
        this.eventEmitter = new EventTarget()
        this.force = force
    }
    emitTileDown(_tile: Tile): void {
        // do nothing
    }

    awaitingInput(): boolean {
        return false
    }

    transition(): void {
        this.machine!.updateState(new Idle(this.grid))
    }

    public onEnter(): void {
        this.eventEmitter.emit(ShuffleEvent.SHUFFLE_START)

        this.shuffleUntilPlayable().then(() => {
            this.eventEmitter.emit(ShuffleEvent.SHUFFLE_COMPLETE)
            this.grid.node.emit(GridEvents.SHUFFLE_COMPLETE, this.grid)

            this.transition()
        })
    }

    public onExit(): void {
        // do nothing
    }

    private async shuffleUntilPlayable(): Promise<void> {
        const uncleared = this.grid.getUncleared()
        let hasEmitted = false

        if (uncleared.length === 0 && !this.force) {
            this.transition()
            return
        }

        let path = findFirstMatch(this.grid)

        while (!path || this.force) {
            if (!hasEmitted) {
                // only emit shuffle event if the grid is not playable
                // only emit this once
                hasEmitted = true
                this.grid.node.emit(GridEvents.SHUFFLE, this.grid)
            }

            this.force = false

            await shuffleTiles({
                grid: this.grid,
                config: {
                    duration: 0.4,
                    easing: 'smooth',
                },
                includeTraversable: false,
                ...this.config,
            })

            this.eventEmitter.emit(ShuffleEvent.SHUFFLE, this.grid)

            path = findFirstMatch(this.grid)
        }
    }
}
