import { Animation, Component, Node, UIOpacity, _decorator } from 'cc'
const { ccclass, property, disallowMultiple } = _decorator

const { Events } = GameCore
const { event } = globalThis.game

@ccclass('BaseScreen')
@disallowMultiple
export class BaseScreen extends Component {
    @property(Node)
    public container!: Node

    @property(Node)
    public backgroundMask!: Node

    private animation!: Animation

    public data: ScreenData = {}

    public onLoad() {
        this.animation = this.getComponent(Animation)!

        this.validateRequiredComponents()

        this.initializeNodes()
    }

    private initializeNodes() {
        const maskOpacity = this.getMaskOpacity()
        const containerOpacity = this.getContainerOpacity()
        if (!maskOpacity || !containerOpacity) return

        maskOpacity.opacity = 0
        containerOpacity.opacity = 0
    }

    private validateRequiredComponents() {
        if (!this.animation) {
            throw new Error(`${this.node.name}: animation not found`)
        }

        if (!this.container) {
            throw new Error(`${this.node.name}: container not found`)
        }

        if (!this.backgroundMask) {
            throw new Error(`${this.node.name}: backgroundMask not found`)
        }
    }

    public open(data?: ScreenData) {
        if (data) {
            this.data = data
        }

        this.runAnimation('OpenScreen')

        this.logEventOpenScreen()
    }

    public close() {
        if (!this.node.active) return

        this.animation.once(Animation.EventType.FINISHED, () => {
            event.emit(Events.CLOSED_SCREEN, { screenName: this.node.name })
            this.node.active = false
        })

        this.runAnimation('CloseScreen')

        this.logEventCloseScreen()
    }

    private runAnimation(name: string) {
        if (!this.isAnimationExist(name)) {
            throw new Error(`${this.node.name}: Animation component must have ${name} clip`)
        }

        this.animation.play(name)
    }

    private isAnimationExist(name: string): boolean {
        return this.animation.clips.some((clip) => {
            if (clip && clip.name === name) return true
            return false
        })
    }

    private getMaskOpacity(): UIOpacity | null {
        const maskOpacity = this.backgroundMask.getComponent(UIOpacity)
        if (!maskOpacity) {
            console.warn('BaseScreen: maskOpacity not found')
            return null
        }

        return maskOpacity
    }

    private getContainerOpacity(): UIOpacity | null {
        const containerOpacity = this.container.getComponent(UIOpacity)
        if (!containerOpacity) {
            console.warn('BaseScreen: containerOpacity not found')
            return null
        }

        return containerOpacity
    }

    protected logEventOpenScreen() {
        const screenName = this.node.name
        this.logPageview(screenName)
    }

    protected logEventCloseScreen(nextPage?: string) {
        if (!nextPage) return
        this.logPageview(nextPage)
    }

    private logPageview(screenName: string) {
        const { analytics } = globalThis.game

        analytics.setPlacement(screenName)
        analytics.pageview(screenName)
    }

    // TODO: add closeable when click outside
}
