import { Component, Label, Sprite, _decorator } from 'cc'
import ItemKeys from '../../../gameplay/constants/ItemKeys'
const { ccclass, property } = _decorator

@ccclass('ItemIcon')
export class ItemIcon extends Component {
    @property(Sprite)
    private iconHint!: Sprite

    @property(Sprite)
    private iconStorm!: Sprite

    @property(Sprite)
    private iconThunder!: Sprite

    @property(Sprite)
    private iconPlier!: Sprite

    @property(Label)
    private quantityLabel!: Label

    public onLoad(): void {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents(): void {
        if (!this.iconHint) {
            throw new Error('ItemIcon: iconHint not found')
        }

        if (!this.iconStorm) {
            throw new Error('ItemIcon: iconStorm not found')
        }

        if (!this.iconThunder) {
            throw new Error('ItemIcon: iconThunder not found')
        }

        if (!this.iconPlier) {
            throw new Error('ItemIcon: iconPlier not found')
        }

        if (!this.quantityLabel) {
            throw new Error('ItemIcon: quantityLabel not found')
        }
    }

    public updateIcon(itemType: string): void {
        switch (itemType) {
            case ItemKeys.HINT:
                this.iconHint.node.active = true
                this.iconStorm.node.active = false
                this.iconThunder.node.active = false
                this.iconPlier.node.active = false
                break

            case ItemKeys.STORM:
                this.iconHint.node.active = false
                this.iconStorm.node.active = true
                this.iconThunder.node.active = false
                this.iconPlier.node.active = false
                break

            case ItemKeys.THUNDER:
                this.iconHint.node.active = false
                this.iconStorm.node.active = false
                this.iconThunder.node.active = true
                this.iconPlier.node.active = false
                break

            case ItemKeys.PLIERS:
                this.iconHint.node.active = false
                this.iconStorm.node.active = false
                this.iconThunder.node.active = false
                this.iconPlier.node.active = true
                break

            default:
                console.warn('ItemIcon: unknown item type', itemType)
                break
        }
    }

    public updateQuantity(quantity: number): void {
        this.quantityLabel.string = `x${quantity}`
    }
}
