import Item from '../../../../scenes/game-scene/components/game/Item'
import DevItem from '../DevItem'

import { CCInteger, _decorator } from 'cc'

const { ccclass, property } = _decorator

@ccclass
export default class AddQuantityItem extends DevItem<Item[]> {
    @property({ type: Item, displayName: 'Game item' })
    public items: Item[] = []

    @property({ type: CCInteger, displayName: 'Quantity to add', min: 1 })
    public addQuantity = 1

    public activate(): Promise<Item[]> {
        this.items.forEach((item) => {
            item.quantity += this.addQuantity
        })

        return Promise.resolve(this.items)
    }
}
