import { Component, director, _decorator } from 'cc'

const { ccclass, executeInEditMode, disallowMultiple } = _decorator

import { EDITOR } from 'cc/env'

const { Events } = GameCore
const { event } = globalThis.game

type Locale = GameCore.Plugins.Language.Locale
type EventPayload = GameCore.Plugins.Event.Payload

@ccclass('DraftComponent')
@disallowMultiple
@executeInEditMode(true)
export class DraftComponent extends Component {
    public locale: Locale = 'en'
    public eventPayload: EventPayload = {
        screenName: 'DraftScene',
    }

    public __preload(): void {
        console.log('DraftComponent', Events)
    }

    public onLoad(): void {
        console.log('DraftComponent', event)
    }

    public start(): void {
        console.log('DraftComponent', EDITOR)

        if (!EDITOR) {
            this.makePersistent()
        }
    }

    public makePersistent() {
        director.addPersistRootNode(this.node)
    }
}
