import { Component, _decorator, find } from 'cc'
import { Music } from '../../../components/audio/Music'
import { Sound } from '../../../components/audio/Sound'
import { CodeSoundKeys } from '../../../constants/SoundKeys'
const { ccclass } = _decorator

@ccclass('AudioDemo')
export class AudioDemo extends Component {
    private musicComp!: Music
    private isMusicMute = false

    private soundComp!: Sound
    private isSoundMute = false
    private listSounds: GameCore.Plugins.Audio.SoundKeys[] = []

    public onLoad() {
        this.musicComp = find('Music')?.getComponent(Music)!
        this.soundComp = find('Sound')?.getComponent(Sound)!

        this.validateRequiredComponents()

        this.listSounds = [
            CodeSoundKeys.BEE_FLYING,
            CodeSoundKeys.BEE_STING_DOGE,
            CodeSoundKeys.BG_MUSIC,
            CodeSoundKeys.BUTTON_CLICK,
            CodeSoundKeys.COLLECTING_COINS,
            CodeSoundKeys.DOGE_DIES,
            CodeSoundKeys.DOGE_HURT,
            CodeSoundKeys.END_DRAW,
            CodeSoundKeys.HAND_CLAP,
            CodeSoundKeys.LEVEL_FAIL,
            CodeSoundKeys.LEVEL_PASS,
            CodeSoundKeys.LOST_STAR_IN_GAMEPLAY,
            CodeSoundKeys.RAISE_STAR,
            CodeSoundKeys.SPAWN_COINS,
            CodeSoundKeys.STAR_RAISE,
            CodeSoundKeys.TOUCH_START,
            CodeSoundKeys.BEE_BULLET_EXPLODE,
        ]
    }

    private validateRequiredComponents() {
        if (!this.musicComp) {
            throw new Error('AudioDemo: Music not found')
        }

        if (!this.soundComp) {
            throw new Error('AudioDemo: Sound not found')
        }
    }

    public playBackgroundMusic() {
        this.musicComp.play(CodeSoundKeys.BG_MUSIC)
    }

    public pauseBackgroundMusic() {
        this.musicComp.pause(CodeSoundKeys.BG_MUSIC)
    }

    public stopBackgroundMusic() {
        this.musicComp.stop(CodeSoundKeys.BG_MUSIC)
    }

    public randomMusicChannelVolume() {
        const random = Math.random()
        this.musicComp.setVolume(random)
    }

    public toggleMuteMusicChannel() {
        if (this.isMusicMute) {
            this.musicComp.mute(false)
            this.isMusicMute = false
        } else {
            this.musicComp.mute(true)
            this.isMusicMute = true
        }
    }

    public playRandomSound() {
        this.soundComp.play(this.listSounds[Math.floor(Math.random() * this.listSounds.length)])
    }

    public toggleMuteSoundChannel() {
        if (this.isSoundMute) {
            this.soundComp.mute(false)
            this.isSoundMute = false
        } else {
            this.soundComp.mute(true)
            this.isSoundMute = true
        }
    }

    public randomSoundChannelVolume() {
        const random = Math.random()
        this.soundComp.setVolume(random)
    }
}
