import ScreenKeys from '../../constants/ScreenKeys'
import GeneralUtils from '../../utils/GeneralUtils'
import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import InitializeState from './InitializeState'

const { event, player } = globalThis.game
const { Events } = GameCore
class CompleteState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.attemptCallCreateShortcut()

        this.updatePlayerProgress()

        this.showLevelCompleteScreen()
    }

    private attemptCallCreateShortcut(): void {
        const { level = 0 } = this.data

        if (level < 2) return

        if (!('createShortcutAsync' in GameSDK.extra)) return

        GameSDK.extra.createShortcutAsync().catch((error: Error) => {
            console.warn('createShortcutAsync', error)
        })
    }

    private async showLevelCompleteScreen(): Promise<void> {
        if (this.game) {
            await GeneralUtils.showInterstitialAdAsync({
                component: this.game,
                forLevel: this.data.level,
            })
        }

        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LEVEL_COMPLETE_SCREEN,
            screenData: {
                level: this.data.level,
                coins: 40, //Default coin
            },
        })
    }

    private updatePlayerProgress(): void {
        if (!this.game) return

        const { level } = this.data
        if (!level) return

        const gameData = player.getGameData()
        if (!gameData) return

        const totalLevel = gameData.totalLevel

        const newGameData = { ...gameData }
        if (level >= totalLevel) {
            newGameData.passAllLevels = true
        } else if (level < gameData.level) {
            newGameData.level = level + 1
        }

        const currentTheme = this.game.themeManager.getLevelTheme(level)
        this.game.themeManager.increaseProgress(currentTheme, 1, false)

        if (this.game.themeManager.playerThemeProgress === null) {
            throw new Error(
                'playerThemeProgress has not been initialized. Have you called ThemeManager.init()?'
            )
        }

        newGameData.themes = this.game.themeManager.playerThemeProgress

        player.setGameData(newGameData)
    }

    public initialize(): void {
        const state = new InitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // Don't allow ready in complete state
    }

    public pause(): void {
        // Don't pause in complete state
    }

    public playing(): void {
        // Don't play again in complete state
    }

    public fail(): void {
        // Don't allow fail in complete state
    }

    public complete(): void {
        // This is complete state, do nothing
    }

    public finish(): void {
        // Don't allow finish in complete state
    }

    public update(): void {
        //
    }
}

export default CompleteState
