import { Component, _decorator, game } from 'cc'
import GameEvents from 'db://assets/constants/GameEvents'

const { ccclass, disallowMultiple, executionOrder } = _decorator

const { Events } = GameCore
const { event } = globalThis.game

type Data = GameCore.Plugins.RemoteConfig.Data

@ccclass('RemoteConfig')
@executionOrder(-200)
@disallowMultiple
export class RemoteConfig extends Component {
    public __preload() {
        this.listenEvents()
    }

    private listenEvents() {
        event.on(Events.REQUEST_HOT_RELOAD, this.handleHotReload)
        event.on(Events.REQUEST_UPDATE_CONFIG, this.handleUpdateConfig)
    }

    private handleHotReload = () => {
        if (!this.enabled) return
        game.restart()
    }

    private handleUpdateConfig = (data: Data) => {
        if (!this.enabled) return
        if (data.type !== 'Gameplay') return

        // ? Broadcast to all node that config is need to update
        this.node.scene.emit(GameEvents.GAMEPLAY_UPDATE_CONFIG, data.config)

        const { id, type } = data

        // ? Tell RemoteConfig plugin that config is updated
        event.emit(Events.REMOTE_CONFIG_UPDATED, { id, type })
    }
}
