import { GridEvents } from '../../../constants/GridEvents'
import LevelGravity from '../../../constants/LevelGravity'
import applyGravity, { canApplyGravity } from '../../../scripts/applyGravity'
import Tile from '../../tile/Tile'
import Grid from '../Grid'
import { State } from '../GridMachine'
import Shuffle from './Shuffle'
import Spawn from './Spawn'

export default class Gravity extends State {
    private gravityList: LevelGravity[] = []
    private minimumPairs: number

    constructor(grid: Grid, gravityList: LevelGravity[], minimumPairs = 7) {
        super(grid)
        this.gravityList = gravityList
        this.minimumPairs = minimumPairs
    }

    emitTileDown(_tile: Tile): void {
        // do nothing
    }

    awaitingInput(): boolean {
        return false
    }

    transition(): void {
        if (this.machine!.getPreviousState() instanceof Spawn) {
            return this.machine!.updateState(new Shuffle(this.grid))
        }

        this.machine!.updateState(new Spawn(this.grid))
    }

    public onEnter(): void {
        const pairCount = this.grid.getUncleared().length / 2

        // if there are less than 7 pairs, skip this state
        if (pairCount <= this.minimumPairs) {
            this.transition()
            return
        }

        this.awaitAllGravity().then(() => {
            this.grid.node.emit(GridEvents.GRAVITY_COMPLETE, this.grid)
            this.transition()
        })
    }

    public onExit(): void {
        // do nothing
    }

    private async awaitAllGravity(): Promise<void> {
        let hasEmitted = false
        for (const gravity of this.gravityList) {
            if (canApplyGravity(this.grid, gravity)) {
                // emit gravity event only if gravity is applied
                if (!hasEmitted) {
                    // only emit once
                    hasEmitted = true
                    this.grid.node.emit(GridEvents.GRAVITY, this.grid)
                }

                await applyGravity(this.grid, gravity)
            }
        }
    }
}
