import { AudioClip, AudioSource, Node, resources } from 'cc'
import AudioPlayer from './AudioPlayer'

type SoundKeys = GameCore.Plugins.Audio.SoundKeys
type PlayConfig = GameCore.Plugins.Audio.PlayConfig

type TrackingAudioClip = {
    audioPlayer: AudioPlayer
    key: string
}
type MultipleTrackingDict = {
    [key: string]: {
        currentPostFix: number
        list: TrackingAudioClip[]
    }
}

const { BaseChannelManager } = GameCore.Plugins.Audio

class AudioChannel extends BaseChannelManager {
    private node: Node

    private allowMultipleTracking: MultipleTrackingDict = {}

    constructor(node: Node, channelId: string) {
        super(channelId)
        this.node = node
    }

    protected async onLoadAudio(key: SoundKeys, _config?: PlayConfig): Promise<AudioPlayer> {
        return new Promise((resolve, reject) => {
            resources.load(key, AudioClip, (error: Error | null, clip: AudioClip) => {
                try {
                    if (error) {
                        throw error
                    }

                    const audioPlayer = this.handleLoadedAudio(key, clip)
                    resolve(audioPlayer)
                } catch (error) {
                    reject(error)
                }
            })
        })
    }

    private handleLoadedAudio = (key: SoundKeys, clip: AudioClip): AudioPlayer => {
        const audioSource = this.node.addComponent(AudioSource)
        audioSource.clip = clip
        audioSource.playOnAwake = false

        const audioPlayer = new AudioPlayer(key, audioSource)

        return audioPlayer
    }

    protected playWithRealConfig(audioPlayer: AudioPlayer, config?: PlayConfig): void {
        if (!this.isCanPlay(config)) return

        if (config) {
            const { volume = 1, allowMultiple = false } = config
            config.volume = this.getVolume() * volume

            if (allowMultiple) {
                const newAudioPlayer = this.requestDuplicateAudioPlayer(
                    audioPlayer.getKey(),
                    audioPlayer.getAudioSource().clip!,
                    config
                )
                newAudioPlayer.play(config)
                return
            }
        }

        audioPlayer.play(config)
    }

    private isCanPlay(config?: PlayConfig): boolean {
        if (config) {
            const { checkList = [] } = config

            const isPlayingExist = checkList.some((key) => {
                const audioClip = this.getAudioClip(key)

                if (audioClip) {
                    const audioPlayer = audioClip.audioPlayer as AudioPlayer
                    return audioPlayer.getAudioSource().state === AudioSource.AudioState.PLAYING
                }

                return false
            })

            if (isPlayingExist) {
                return false
            }
        }

        return true
    }

    private requestDuplicateAudioPlayer(
        key: SoundKeys,
        clip: AudioClip,
        config?: PlayConfig
    ): AudioPlayer {
        const existingAudioPlayer = this.getDuplicateAudioFromPool(key)
        if (existingAudioPlayer) {
            if (config) {
                const { key: dupKey } = existingAudioPlayer
                const { volume = 1 } = config
                this.setAudioClip(dupKey, {
                    volume: volume,
                })
            }
            return existingAudioPlayer.audioPlayer
        }

        const dupKey = `${key}_${this.allowMultipleTracking[key].currentPostFix}`
        const newAudioPlayer = this.handleLoadedAudio(dupKey as SoundKeys, clip)
        this.allowMultipleTracking[key].list.push({
            audioPlayer: newAudioPlayer,
            key: dupKey,
        })
        this.allowMultipleTracking[key].currentPostFix += 1

        this.setAudioClip(dupKey, {
            audioPlayer: newAudioPlayer,
            volume: 1,
        })

        return newAudioPlayer
    }

    private getDuplicateAudioFromPool(key: SoundKeys): TrackingAudioClip | null {
        if (this.allowMultipleTracking[key]) {
            const { list } = this.allowMultipleTracking[key]
            const audioClip = list.find((audioClip) => {
                return (
                    audioClip.audioPlayer.getAudioSource().state !== AudioSource.AudioState.PLAYING
                )
            })
            if (audioClip) {
                return audioClip
            }
        } else {
            this.allowMultipleTracking[key] = {
                currentPostFix: 1,
                list: [],
            }
        }

        return null
    }
}

export default AudioChannel
