import Grid from '../game-objects/grid/Grid'
import shuffleArray from '../utils/array/shuffle'

/**
 * Configuration for the shuffle animation
 * @param grid The grid to shuffle. The content of the grid will be modified.
 * @param config The configuration for the sync animation
 * @param includeTraversable Whether to include traversable tiles in the shuffle
 */
export type ShuffleConfig = {
    grid: Grid
    config?: Pick<SyncConfig, 'duration' | 'easing' | 'stagger'>
    includeTraversable?: boolean
}

/**
 * Shuffles the tiles in the grid
 * @param config The configuration for the shuffle
 * @returns A promise that resolves when the tiles have finished syncing
 */
export default async function shuffleTiles(config: ShuffleConfig): Promise<void> {
    const { grid, config: syncConfig, includeTraversable } = config

    const ogArray = includeTraversable
        ? grid.getTiles()
        : grid.getTiles().filter((tile) => !tile.isTraversable())

    //? if there are only 2 tiles, shuffle them manually
    if (ogArray.length === 2) {
        grid.swapTilesImmediate(ogArray[0], ogArray[1])

        await grid.padTiles({
            padding: 5,
            duration: 0.1,
            stagger: 0,
        })

        grid.sortTiles()

        await grid.syncTiles(syncConfig)

        return
    }

    await grid.padTiles({
        padding: 10,
        duration: 0.5,
        stagger: 0,
    })

    //? if there are more than 2 tiles
    const shuffledArray = shuffleArray(ogArray)

    const newPositions = ogArray.map((tile, index) => ({
        tile,
        other: shuffledArray[index],
    }))

    //? half the array to guarantee that our pairs don't swap twice
    if (newPositions.length <= 6) {
        newPositions.splice(newPositions.length / 2)
    }

    newPositions.forEach((pair) => {
        grid.swapTilesImmediate(pair.tile, pair.other)
    })

    grid.sortTilesForTornadoAnim()

    await grid.flyTiles({
        ...syncConfig,
        padding: 10,
        stagger: 0,
        duration: (syncConfig?.duration ?? 0) / 2,
    })

    grid.sortTiles()

    await grid.syncTiles({
        ...syncConfig,
        duration: (syncConfig?.duration ?? 0) / 2,
        stagger: 0,
    })
}
