import { find } from 'cc'
import { Sound } from '../../components/audio/Sound'
import { Vibrate } from '../../components/vibrate/Vibrate'
import ScreenKeys from '../../constants/ScreenKeys'
import GeneralUtils from '../../utils/GeneralUtils'
import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import FinishState from './FinishState'
import InitializeState from './InitializeState'
import PlayingState from './PlayingState'

const { Events } = GameCore
const { event } = globalThis.game

class FailState extends BaseState {
    constructor(manager: StateManager) {
        super(manager)

        this.stopAllSounds()
        this.vibrateDevice()
        this.pauseCountdown()

        this.showTimeUpScreen()
    }

    private stopAllSounds() {
        find('Sound')?.getComponent(Sound)?.stopAll()
    }

    private pauseCountdown(): void {
        this.game?.uiManager.pauseCountdown()
    }

    private vibrateDevice(): void {
        find('Vibrate')?.getComponent(Vibrate)?.vibrate(500)
    }

    private async showTimeUpScreen(): Promise<void> {
        const { level } = this.data

        if (this.game) {
            await GeneralUtils.showInterstitialAdAsync({
                component: this.game,
                forLevel: level,
            })
        }

        GeneralUtils.playSound('sounds/level_fail')

        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.TIME_UP_SCREEN,
            screenData: { level },
        })
    }

    public initialize(): void {
        const state = new InitializeState(this.manager)
        this.manager.change(state)
    }

    public ready(): void {
        // Don't allow ready in fail state
    }

    public playing(): void {
        const state = new PlayingState(this.manager)
        this.manager.change(state)
    }

    public fail(): void {
        // This is fail state, do nothing
    }

    public complete(): void {
        // Don't allow complete in fail state
    }

    public finish(): void {
        const state = new FinishState(this.manager)
        this.manager.change(state)
    }

    public update(): void {
        //
    }
}

export default FailState
