import { Button, CCBoolean, Component, Node, _decorator } from 'cc'
import SoundKeys, { CodeSoundKeys, SoundKeysMapping } from '../../constants/SoundKeys'
import GeneralUtils from '../../utils/GeneralUtils'
const { ccclass, property, requireComponent } = _decorator

const { analytics } = globalThis.game

@ccclass('SwitchButton')
@requireComponent([Button])
export class SwitchButton extends Component {
    @property(CCBoolean)
    private enable = true

    @property(Node)
    private nodeOn!: Node

    @property(Node)
    private nodeOff!: Node

    @property(CCBoolean)
    public useSound = true

    @property({
        visible: function (this: SwitchButton) {
            return this.useSound
        },
        type: SoundKeys,
    })
    private soundKey = SoundKeys[CodeSoundKeys.BUTTON_CLICK]

    public onLoad() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.nodeOn) {
            throw new Error('SwitchButton: nodeOn not found')
        }

        if (!this.nodeOff) {
            throw new Error('SwitchButton: nodeOff not found')
        }
    }

    public switch(): boolean {
        if (!this.node.active) return false

        // TODO: handle event from component button
        analytics.event(GameCore.Plugins.Analytics.Events.BUTTON_CLICK, {
            switch_name: this.node.name,
            enable: !this.enable,
        })

        if (this.useSound) {
            GeneralUtils.playSound(SoundKeysMapping[this.soundKey])
        }

        return this.updateStatus(!this.enable)
    }

    public setEnabled(enable: boolean): void {
        if (!this.node.active) return

        this.updateStatus(enable)
    }

    private updateStatus(enable: boolean): boolean {
        this.enable = enable

        this.nodeOn.active = enable
        this.nodeOff.active = !enable

        return enable
    }
}
