import { math, Node, UITransform, view } from 'cc'

class CocosUtils {
    /**
     * Fix `uiTransformComponent.getBoundingBoxToWorld()` work incorrectly if have children
     * @param node Node need to get BoundingBox, must have a UITransform component
     */
    public static getBoundingBoxToWorld(node: Node) {
        if (node.parent) {
            const m = node.parent.getWorldMatrix()
            return this.getBoundingBoxTo(m, node)
        }

        const uiTransformComponent = node.getComponent(UITransform)
        if (!uiTransformComponent) {
            throw new Error('Node must have UITransform component')
        }

        return uiTransformComponent.getBoundingBox()
    }

    private static getBoundingBoxTo(parentMat: math.Mat4, currentNode: Node) {
        const _matrix = math.mat4()
        math.Mat4.fromRTS(
            _matrix,
            currentNode.getRotation(),
            currentNode.getPosition(),
            currentNode.getScale()
        )

        const uiTransformComponent = currentNode.getComponent(UITransform)
        if (!uiTransformComponent) {
            throw new Error('Node must have UITransform component')
        }

        const width = uiTransformComponent.contentSize.width
        const height = uiTransformComponent.contentSize.height
        const rect = new math.Rect(
            -uiTransformComponent.anchorPoint.x * width,
            -uiTransformComponent.anchorPoint.y * height,
            width,
            height
        )

        const _worldMatrix = math.mat4()
        math.Mat4.multiply(_worldMatrix, parentMat, _matrix)
        rect.transformMat4(_worldMatrix)

        // query child's BoundingBox
        if (!currentNode.children || currentNode.children.length === 0) {
            return rect
        }

        const locChildren = currentNode.children
        for (const child of locChildren) {
            if (child && child.active) {
                const uiTransform = child.getComponent(UITransform)
                if (uiTransform) {
                    const childRect = this.getBoundingBoxTo(currentNode.getWorldMatrix(), child)
                    if (childRect) {
                        math.Rect.union(rect, rect, childRect)
                    }
                }
            }
        }

        return rect
    }

    public static worldSizeToCssSize(worldSize: number) {
        const worldGameSize = view.getVisibleSize()
        const cssSize = view.getFrameSize()
        const cssVsWorldGameRatio = cssSize.width / worldGameSize.width

        return worldSize * cssVsWorldGameRatio
    }

    public static getScreenWidthCss(): number {
        const { body, documentElement } = document
        const { clientWidth: bodyClientWidth } = body
        const { clientWidth: docClientWidth } = documentElement

        return Math.max(docClientWidth, bodyClientWidth) || 0
    }

    public static getScreenHeightCss(): number {
        const { body, documentElement } = document
        const { clientHeight: bodyClientHeight } = body
        const { clientHeight: docClientHeight } = documentElement
        return Math.max(docClientHeight, bodyClientHeight) || 0
    }
}

export default CocosUtils
