import { Color, Component, Label, _decorator, native } from 'cc'
import { ANDROID } from 'cc/env'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass, property } = _decorator

const { ads, event } = globalThis.game

@ccclass('JavaAds')
export class JavaAds extends Component {
    @property(Label)
    private interLabel!: Label
    @property(Label)
    private rewardedLabel!: Label

    public __preload(): void {
        // called before onLoad, use for initialization
        this.setupAds()
    }

    private setupAds() {
        if (!ANDROID) return

        const extra = GameSDK.extra
        if ('setStaticNativeAdMethod' in extra) {
            extra.setStaticNativeAdMethod(native.reflection.callStaticMethod)
        }
    }

    public onLoad(): void {
        // called once when the component first activated, before any start methods
    }

    public start(): void {
        // called once when the component enabled for first time
    }

    public showBannerAds(): void {
        // called when the attached node first activated
        ads.showBannerAdAsync().catch((error) => {
            console.log('%c error: ', 'font-size:12px;background-color: #FFDD4D;color:#fff;', error)
        })
    }

    public hideBannerAds(): void {
        // called when the attached node first activated
        ads.hideBannerAdAsync().catch((error) => {
            console.log('%c error: ', 'font-size:12px;background-color: #6EC1C2;color:#fff;', error)
        })
    }

    public async loadRewardedAds(): Promise<void> {
        try {
            this.showNotificationScreen('loading')

            await ads.preloadRewardedVideoAsync()
            this.closeNotificationScreen()
            this.rewardedLabel.color = new Color(0, 255, 0)
        } catch (error) {
            console.log('%c error: ', 'font-size:12px;background-color: #A8978E;color:#fff;', error)
            this.showNotificationScreen('Load ads fail.', 2)
        }
    }

    public async showRewardAds(): Promise<void> {
        try {
            this.showNotificationScreen('loading')

            await ads.showRewardedVideoAsync()

            this.closeNotificationScreen()
        } catch (error) {
            console.warn('showTips failed', error)

            if (error instanceof Object && 'code' in error && error.code === 'USER_INPUT') {
                this.showNotificationScreen('Sorry, no rewards!\n You have cancelled the ad.', 2)
                return
            }

            this.showNotificationScreen('No ads to display.', 2)
        } finally {
            this.rewardedLabel.color = new Color(0, 0, 0)
        }
    }

    public async loadInterAds(): Promise<void> {
        try {
            this.showNotificationScreen('loading')

            await ads.preloadInterstitialAdAsync()
            this.closeNotificationScreen()
            this.interLabel.color = new Color(0, 255, 0)
        } catch (error) {
            console.log('%c error: ', 'font-size:12px;background-color: #BAAC9A;color:#fff;', error)
            this.showNotificationScreen('Load ads fail.', 2)
        }
    }

    public async showInterAds(): Promise<void> {
        try {
            this.showNotificationScreen('loading')

            await ads.showInterstitialAdAsync()

            this.closeNotificationScreen()
        } catch (error) {
            console.warn('showTips failed', error)

            if (error instanceof Object && 'code' in error && error.code === 'USER_INPUT') {
                this.showNotificationScreen('Sorry, no rewards!\n You have cancelled the ad.', 2)
                return
            }

            this.showNotificationScreen('No ads to display.', 2)
        } finally {
            this.interLabel.color = new Color(0, 0, 0)
        }
    }

    private showNotificationScreen(message: string, duration?: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message, duration },
        })
    }

    private closeNotificationScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
        })
    }
}
