import { Node as CCNode, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
import { Gameplay } from '../../Gameplay'
import GameplayEvents from '../../constants/GameEvents'
import { GridEvents } from '../../constants/GridEvents'
import ItemKeys from '../../constants/ItemKeys'
import EffectManager from '../../manager/EffectManager'
import { UIManager } from '../../manager/UIManager'
import Grid from '../grid/Grid'
import { SpecialItem } from './SpecialItem'

const { ccclass } = _decorator
const { player, event } = globalThis.game
const { Events } = GameCore

@ccclass('ThunderItem')
export default class ThunderItem extends SpecialItem {
    private gridInstance!: Grid
    private effectManager!: EffectManager
    private uiManager!: UIManager

    public override init(gameplay: Gameplay) {
        this.gridInstance = gameplay.grid
        this.effectManager = gameplay.effectManager
        this.uiManager = gameplay.uiManager

        this.node.scene.on(
            GameplayEvents.ADD_THUNDER_ITEMS,
            (quantity: number) => {
                this.setQuantity(quantity)
                this.uiManager.playEntranceAnimUI()
            },
            this
        )

        this.gridInstance.node.on(GridEvents.CREATE, this.applyActivationStatus, this)

        this.gridInstance.node.on(GridEvents.IDLE_EXIT, () => {
            // simply disable button. Animation will be too distracting
            this.button.interactable = false
        })

        this.gridInstance.node.on(GridEvents.IDLE_ENTER, () => {
            // simply disable button. Animation will be too distracting
            this.button.interactable = true
            this.applyActivationStatus()
        })
    }

    protected override onDestroy(): void {
        this.node.scene.targetOff(this)

        const gridNode = this.gridInstance.node as CCNode | null | undefined

        if (gridNode === null || gridNode === undefined) {
            super.onDestroy?.()
            return
        }

        this.gridInstance.node.targetOff(this)
        super.onDestroy?.()
    }

    protected override validateRequiredComponents(): void {
        super.validateRequiredComponents()
        if (!this.gridInstance) {
            throw new Error(`${this.node.name}: gridInstance not found`)
        }

        if (!this.effectManager) {
            throw new Error(`${this.node.name}: effectManager not found`)
        }

        if (!this.uiManager) {
            throw new Error(`${this.node.name}: uiManager not found`)
        }
    }

    public override canActivate(): boolean {
        const uncleared = this.gridInstance.getUncleared()
        return uncleared.length >= 4
    }

    public async activate(): Promise<void> {
        if (this.getQuantity() <= 0) {
            await super.activate()
            return
        }

        await super.activate()

        this.node.scene.emit(GameplayEvents.USE_THUNDER_ITEM)

        this.uiManager.playExitAnimUI()
        this.uiManager.setInteractableItemButtons(false)

        const unclearedTiles = this.gridInstance.getUncleared()

        if (unclearedTiles.length < 4) {
            throw new Error('Not enough uncleared tiles')
        }

        // pick 2 random pairs
        const pair1 = this.gridInstance.getRandomActivePair()!
        let pair2 = this.gridInstance.getRandomActivePair()!

        while (
            pair1[0] === pair2[0] ||
            pair1[1] === pair2[1] ||
            pair1[0] === pair2[1] ||
            pair1[1] === pair2[0]
        ) {
            pair2 = this.gridInstance.getRandomActivePair()!
        }

        // disable tiles' button
        this.gridInstance.setTileIgnoreInput(this.gridInstance.getTiles())

        const pairs = [pair1, pair2]

        await this.effectManager.playThunderAnimation(pairs, this.gridInstance)

        this.uiManager.playEntranceAnimUI()
        this.uiManager.setInteractableItemButtons(true)
    }

    public requestItem = () => {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.GET_ITEM_SCREEN,
            screenData: {
                item: ItemKeys.THUNDER,
                quantity: 1,
            },
        })
    }

    public loadQuantity = () => {
        const gameData = player.getGameData()
        return gameData ? gameData.items[ItemKeys.THUNDER] : 0
    }

    public saveQuantity = () => {
        const gameData = player.getGameData()
        if (!gameData) return

        player.setGameData({
            items: {
                ...gameData.items,
                [ItemKeys.THUNDER]: this.getQuantity(),
            },
        })
    }
}
