import { Component, _decorator, director } from 'cc'
import { EDITOR } from 'cc/env'

import enData from 'db://assets/resources/locales/en.json'
import ruData from 'db://assets/resources/locales/ru.json'

const { ccclass, executeInEditMode, disallowMultiple, executionOrder, property } = _decorator

const { Events } = GameCore
const { event, language } = globalThis.game

type Locale = GameCore.Plugins.Language.Locale

@ccclass('Localization')
@executionOrder(-100)
@disallowMultiple
@executeInEditMode(true)
export class Localization extends Component {
    public static _key: Locale = 'en'

    @property({
        readonly: true,
    })
    public set locale(value: Locale) {
        Localization._key = value
    }

    public get locale(): Locale {
        return Localization._key
    }

    public __preload() {
        this.addLanguage('en')
        this.addLanguage('ru')

        if (EDITOR) {
            this.setLanguage(this.locale)
        }
    }

    public start() {
        this.listenEvent()

        if (!EDITOR) {
            this.makePersistent()
        }
    }

    // ? Localization must be exist in all scenes
    public makePersistent() {
        director.addPersistRootNode(this.node)
    }

    public onDestroy() {
        event.off(Events.LANGUAGE_CHANGED, this.handleLanguageChanged)
    }

    private listenEvent() {
        event.on(Events.LANGUAGE_CHANGED, this.handleLanguageChanged)
    }

    private handleLanguageChanged = async (eventPayload: EventLanguagePayload) => {
        const { locale } = eventPayload
        this.node.scene.emit('update-language', locale)
        this.locale = locale as Locale

        // * Update component in editor
        if (EDITOR) {
            Editor.Message.broadcast('selection:unselect', 'node', this.node.uuid)
            Editor.Message.broadcast('selection:activated', 'node', this.node.uuid)
        }
    }

    private addLanguage(lang: Locale) {
        const langData = this.getLangData(lang)
        language.add(lang, langData)
    }

    private setLanguage(lang: Locale) {
        language.choose(lang)
    }

    private getLangData(lang: Locale) {
        switch (lang) {
            case 'en':
                return enData
            case 'ru':
                return ruData
            default:
                throw new Error(`Language ${lang} not supported`)
        }
    }
}
