import { Component, EventHandler, Node, Vec3, _decorator, easing, tween } from 'cc'
import DashboardScreens from '../../../../../../constants/DashboardScreens'
import SceneKeys from '../../../../../../constants/SceneKeys'
import ScreenKeys from '../../../../../../constants/ScreenKeys'
import type { NavButton } from './NavButton'
const { ccclass, property } = _decorator

const { event } = globalThis.game

let isFirstTimeGoToCollection = true

@ccclass('NavManager')
export class NavManager extends Component {
    @property(Node)
    private selectorNode!: Node

    @property(Node)
    public selectedButtonNode!: Node

    private selectedButton!: NavButton

    private navButtons: NavButton[] = []

    public get currentButton(): NavButton {
        return this.selectedButton
    }

    @property(EventHandler)
    public onNavigate: EventHandler[] = []

    private isNavigating = false

    public onLoad(): void {
        this.navButtons = this.node.getComponentsInChildren('NavButton') as NavButton[]
        this.selectedButton = this.selectedButtonNode?.getComponent('NavButton')! as NavButton

        this.validateRequiredComponents()
    }

    public start(): void {
        this.initCurrentButton()
    }

    private validateRequiredComponents() {
        if (!this.selectedButtonNode) {
            throw new Error('NavManager: Selected button node not found')
        }

        if (!this.selectedButton) {
            throw new Error('NavManager: Selected button not found')
        }

        if (!this.selectorNode) {
            throw new Error('NavManager: Selector node not found')
        }
    }

    public async select(button: NavButton): Promise<void> {
        if (this.isNavigating) {
            return
        }

        if (!this.navButtons.includes(button)) {
            throw new Error('NavManager: Button not found')
        }

        if (button === this.selectedButton) {
            return
        }

        if (button.isWIP) {
            this.handleWIPButton()
            return
        }

        this.isNavigating = true

        this.onNavigate.forEach((handler) =>
            handler.emit([button.buttonType, handler.customEventData])
        )

        const deselectAnim = this.selectedButton.deselect()

        this.selectedButton = button

        const selectorAnim = this.playSelectorAnimation()

        const selectAnim = button.select()

        await Promise.all([deselectAnim, selectAnim, selectorAnim])

        this.isNavigating = false
    }

    public async selectUsingSceneTransition(button: NavButton): Promise<void> {
        if (this.isNavigating) {
            return
        }

        let targetScene: string
        switch (button.buttonType) {
            case DashboardScreens.home:
                targetScene = SceneKeys.DASHBOARD_SCENE
                break

            case DashboardScreens.collections:
                targetScene = SceneKeys.COLLECTIONS_SCENE
                break

            default:
                this.handleWIPButton()
                return
        }

        //TODO: Remove this when we have another method
        if (isFirstTimeGoToCollection) {
            event.emit(GameCore.Events.OPEN_SCREEN, {
                screenName: ScreenKeys.LOADING_SCREEN,
                screenData: { useBlockInputWithoutBg: true },
            })
            isFirstTimeGoToCollection = false
        }
        await this.select(button)
        event.emit(GameCore.Events.SWITCH_SCENE, {
            sceneName: targetScene,
        })
    }

    public async selectByType(screenType: DashboardScreens): Promise<void> {
        const button = this.navButtons.find((button) => button.buttonType === screenType)

        if (!button) {
            throw new Error('NavManager: Button not found')
        }

        await this.selectUsingSceneTransition(button)
    }

    private handleWIPButton(): void {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message: 'Coming soon!' },
        })
    }

    private async initCurrentButton(): Promise<void> {
        if (!this.currentButton || !this.navButtons.includes(this.currentButton)) {
            throw new Error('NavManager: Button not found')
        }

        this.onNavigate.forEach((handler) =>
            handler.emit([
                this.currentButton.buttonType,
                handler.customEventData,
            ] as NavigatePayload)
        )

        await Promise.all([this.playSelectorAnimation(true), this.currentButton.select()])
    }

    public playSelectorAnimation(setScaleBeforeAnim = false): Promise<void> {
        const originalScale = new Vec3(1, 1, 1)
        const originalPosition = this.selectorNode.position.clone()

        const squashX = 1.2
        const squashY = 0.8
        const squashedScale = new Vec3(squashX * originalScale.x, squashY * originalScale.y, 1)

        if (setScaleBeforeAnim) {
            this.selectorNode.scale = new Vec3(0, 0, 1)
        }

        // ? This is a hack to manually update the position of the selector node
        // Without this, the selector node will follow the button node's position **before** it's rearranged by the layout which is delayed
        const it = {
            position: 0,
        }

        return new Promise((resolve) => {
            tween(it)
                .to(
                    0.5,
                    { position: 1 },
                    {
                        onUpdate: (_target, ratio) => {
                            if (ratio === null || ratio === undefined) {
                                return
                            }

                            const calcRatio = ratio < 0.5 ? ratio * 2 : 1 - (ratio - 0.5) * 2
                            const refScale = squashedScale.clone()
                            const nextScale = originalScale.clone().lerp(refScale, calcRatio)

                            this.selectorNode.scale = nextScale

                            const easedValue = easing.quintInOut(ratio)

                            const targetX = this.selectedButton.node.position.x
                            const nextX =
                                originalPosition.x + (targetX - originalPosition.x) * easedValue

                            this.selectorNode.position = new Vec3(nextX, originalPosition.y, 0)
                        },
                    }
                )
                .call(resolve)
                .start()
        })
    }
}
