import { Tween } from 'cc'

type TweenObj<T> = {
    tween: Tween<T>
}

class AnimUtils {
    /**
     * Elastic ease-out.
     *
     * @function Phaser.Math.Easing.Elastic.Out
     * @since 3.0.0
     *
     * @param {number} v - The value to be tweened.
     * @param {number} [amplitude=0.1] - The amplitude of the elastic ease.
     * @param {number} [period=0.1] - Sets how tight the sine-wave is, where smaller values are tighter waves, which result in more cycles.
     *
     * @return {number} The tweened value.
     */
    public static elasticOut(v: number, amplitude: number, period: number) {
        if (amplitude === undefined) {
            amplitude = 0.1
        }
        if (period === undefined) {
            period = 0.1
        }

        if (v === 0) {
            return 0
        } else if (v === 1) {
            return 1
        } else {
            let s = period / 4

            if (amplitude < 1) {
                amplitude = 1
            } else {
                s = (period * Math.asin(1 / amplitude)) / (2 * Math.PI)
            }

            return (
                amplitude * Math.pow(2, -10 * v) * Math.sin(((v - s) * (2 * Math.PI)) / period) + 1
            )
        }
    }

    public static easeOutExpo(progress: number): number {
        return progress === 1 ? 1 : 1 - Math.pow(2, -10 * progress)
    }

    public static easeInExpo(x: number): number {
        return x === 0 ? 0 : Math.pow(2, 10 * x - 10)
    }

    public static runTweenAsync<T>(tween: Tween<T>): Promise<TweenObj<T>> {
        return new Promise((resolve) => {
            tween
                .call(() => {
                    resolve({ tween })
                })
                .start()
        })
    }
}

export default AnimUtils
