import { Button, Component, Label, Node, _decorator } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { BaseScreen } from '../BaseScreen'

const { ccclass, property, requireComponent } = _decorator

const { Events, Utils } = GameCore
const { event, player } = globalThis.game

@ccclass('LevelCompleteScreen')
@requireComponent(BaseScreen)
export class LevelCompleteScreen extends Component {
    private screen!: BaseScreen
    private levelId: number = 1
    private coins: number = 40

    @property(Node)
    public playButton!: Node

    @property(Node)
    public coinText!: Node

    @property(Node)
    public levelText!: Node

    @property(Node)
    public nextLevelText!: Node

    @property(Button)
    private playButtonComponent!: Button

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('LevelCompleteScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.playButton) {
            throw new Error('LevelCompleteScreen: playButton not found')
        }

        if (!this.levelText) {
            throw new Error('LevelCompleteScreen: levelText not found')
        }

        if (!this.coinText) {
            throw new Error('LevelCompleteScreen: missionText not found')
        }

        if (!this.nextLevelText) {
            throw new Error('LevelCompleteScreen: nextLevelText not found')
        }
    }

    private listenScreenEvents() {
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = () => {
        this.playButtonComponent.interactable = true
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName, screenData } = payload ?? {}
        if (screenName !== this.node.name) return false

        if (!Utils.Valid.isObject(screenData)) return false

        if (Utils.Valid.isNumber(screenData.level)) {
            this.levelId = screenData.level
        }

        if (Utils.Valid.isNumber(screenData.coins)) {
            this.coins = screenData.coins
        }

        return true
    }

    public open() {
        this.updateComponents()
        this.runPopUpAnimation()
    }

    public close() {
        this.handleNextLevel()

        this.playButtonComponent.interactable = false

        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    public handleNextLevel() {
        const level = this.screen.data['level']
        if (!level) {
            console.warn('LevelCompleteScreen: level not found')
            return
        }

        const gameData = player.getGameData()

        if (!gameData) {
            console.warn('LevelCompleteScreen: gameData not found')
            return
        }

        const isLevelEnd = level >= gameData.totalLevel

        if (isLevelEnd) {
            // TODO: Need Congratulations screen when final level completed.
        } else {
            this.node.scene.emit(GameplayEvents.NEXT_LEVEL, { level })
        }
    }

    private updateComponents() {
        this.updateLevelText()
        this.updateCoinString()
    }

    private updateLevelText() {
        const gameData = player.getGameData()

        if (!gameData) {
            console.warn('LevelCompleteScreen: gameData not found')
            return
        }

        this.levelText.getComponent(Label)!.string = `Level ${this.levelId}`
        console.log('LevelCompleteScreen: updateLevelText', this.levelId)

        const nextLevel =
            this.levelId + 1 >= gameData.totalLevel ? 'Complete' : `Level ${this.levelId + 1}`
        this.nextLevelText.getComponent(Label)!.string = nextLevel
    }

    private updateCoinString() {
        this.coinText.getComponent(Label)!.string = `x${this.coins}`
        console.log('LevelCompleteScreen: updateCoinString', this.coins)
    }

    private runPopUpAnimation(): void {
        if (!this.screen.container) {
            console.warn('LevelCompleteScreen: screen not found')
            return
        }
    }

    // ? Callback for click event of PlayButton
    public handleClickedPlay() {
        this.node.scene.emit(GameplayEvents.ADD_COINS, this.coins)

        this.close()
    }
}
