import { Button, Component, Director, Node, Vec3, _decorator, director, tween } from 'cc'
import DevTools from '../../constants/DevTools'
import { Clock } from '../../scenes/game-scene/components/ui/clock/Clock'
import { Header } from '../../scenes/game-scene/components/ui/header/Header'
import { Gameplay } from '../Gameplay'
import { DevToolsContainer } from '../dev-tools/DevTools'
import { SpecialItem } from '../game-objects/item/SpecialItem'

const { ccclass, property } = _decorator
const { Utils } = GameCore

@ccclass('UIManager')
export class UIManager extends Component {
    @property(Clock)
    public clock!: Clock

    @property(Header)
    public header!: Header

    @property(DevToolsContainer)
    public devTools: DevToolsContainer | null = null

    @property([Node])
    private topAnimNodes: Node[] = []
    private originTopNodesPos: Vec3[] = []

    @property([Node])
    private botAnimNodes: Node[] = []
    private originBotNodesPos: Vec3[] = []

    public onLoad(): void {
        this.validateRequiredComponents()
        this.listensDevToolsEvents()

        //? wait LayoutComponent finish layout (not dirty)
        director.once(Director.EVENT_AFTER_UPDATE, () => {
            this.saveOriginPos()
            this.initUIPos()
        })
    }

    private validateRequiredComponents(): void {
        if (!this.clock) {
            throw new Error(`${this.node.name}: clock not found`)
        }

        if (!this.header) {
            throw new Error(`${this.node.name}: header not found`)
        }

        if (this.topAnimNodes.length === 0) {
            throw new Error(`${this.node.name}: animNodes not found`)
        }

        if (this.botAnimNodes.length === 0) {
            throw new Error(`${this.node.name}: animNodes not found`)
        }
    }

    private saveOriginPos(): void {
        this.topAnimNodes.forEach((node) => {
            this.originTopNodesPos.push(node.position.clone())
        })

        this.botAnimNodes.forEach((node) => {
            this.originBotNodesPos.push(node.position.clone())
        })
    }

    private initUIPos(): void {
        this.topAnimNodes.forEach((node, idx) => {
            const startPos = this.originTopNodesPos[idx].clone()
            startPos.y += 200
            node.setPosition(startPos)
        })
        this.botAnimNodes.forEach((node, idx) => {
            const startPos = this.originBotNodesPos[idx].clone()
            startPos.y -= 200
            node.setPosition(startPos)
        })
    }

    private listensDevToolsEvents() {
        this.node.scene.on(DevTools.Events.UNLOCK_DEV_TOOLS, this.showDevTools)
    }

    public initDevTools(gameplay: Gameplay): void {
        if (!this.devTools) return

        this.devTools.node.active = Utils.Valid.isDebugger()
        this.devTools.injectGameplay(gameplay)
    }

    private showDevTools = () => {
        if (!this.devTools) return
        this.devTools.node.active = true
    }

    public setLevelText(level: number): void {
        this.header.setLevelText(level)
    }

    public initCountdown(): void {
        this.clock.init()
    }

    public setCountdownTime(time: number): void {
        this.clock.setTime(time)
    }

    public startCountdown(): void {
        this.clock.startCountdown()
    }

    public pauseCountdown(): void {
        this.clock.pause()
    }

    public resumeCountdown(): void {
        this.clock.resume()
    }

    public setProgressBarPercentage(percentage: number): void {
        this.header.setProgress(percentage)
    }

    public setProgressStars(stars: number): void {
        this.header.setProgressStars(stars)
    }

    public startCountDownHeaderHintBubbleAnim(): void {
        this.header.startCountDownHintBubble()
    }

    public stopCountDownHeaderHintBubbleAnim(): void {
        this.header.stopCountDownHintBubble()
    }

    public disableHeader(): void {
        this.header.setButtonsSelectable(false)
    }

    public enableHeader(): void {
        this.header.setButtonsSelectable()
    }

    public playExitAnimUI(): void {
        this.topAnimNodes.forEach((node, idx) => {
            const des = this.originTopNodesPos[idx].clone()
            des.y += 200
            const t = tween(node)
            t.to(0.17, { position: des }, { easing: 'backIn' }).start()
        })

        this.botAnimNodes.forEach((node, idx) => {
            const des = this.originBotNodesPos[idx].clone()
            des.y -= 200
            const t = tween(node)
            t.to(0.17, { position: des }, { easing: 'backIn' })
            t.start()
        })
    }

    public playEntranceAnimUI(): void {
        this.topAnimNodes.forEach((node, idx) => {
            const t = tween(node)
            t.to(0.2, { position: this.originTopNodesPos[idx] }, { easing: 'backOut' })
            t.start()
        })

        this.botAnimNodes.forEach((node, idx) => {
            const t = tween(node)
            t.to(0.2, { position: this.originBotNodesPos[idx] }, { easing: 'backOut' })
            t.start()
        })
    }

    public setInteractableItemButtons(interactable = true): void {
        this.botAnimNodes.forEach((node) => {
            const button = node.getComponent(Button)
            const item = node.getComponent(SpecialItem)

            if (button && item) {
                if (!interactable) {
                    button.interactable = false
                    return
                }

                if (item.canActivate()) {
                    button.interactable = true
                }
            }
        })
    }
}
