import { _decorator, ProgressBar, Sprite } from 'cc'
const { ccclass, property, executeInEditMode } = _decorator

@ccclass('TapperedProgressBar')
@executeInEditMode
export class TapperedProgressBar extends ProgressBar {
    @property({
        type: Sprite,
        displayName: 'Left Cap',
        tooltip: 'If assigned, will be displayed when progress is larger than 0%',
    })
    public leftCap: Sprite | null = null

    @property({
        type: Sprite,
        displayName: 'Right Cap',
        tooltip: 'If assigned, will be displayed when progress is at 100%',
    })
    public rightCap: Sprite | null = null

    /**
     * @en
     * The current progress of the bar sprite. The valid value is between 0-1.
     *
     * @zh
     * 当前进度值，该数值的区间是 0-1 之间。
     */
    @property({
        range: [0, 1, 0.01],
        slide: true,
        tooltip: 'i18n:progress.progress',
        override: true,
    })
    public override get progress(): number {
        return super.progress
    }

    public override set progress(value: number) {
        super.progress = value
        this.updateCap()
    }

    private updateCap(): void {
        if (this.leftCap) {
            this.leftCap.enabled = this.progress > 0
        }
        if (this.rightCap) {
            this.rightCap.enabled = this.progress > 0
        }
    }

    public start(): void {
        this.updateCap()
    }
}
