import { find } from 'cc'
import { Music } from '../../components/audio/Music'
import { Sound } from '../../components/audio/Sound'
import GeneralUtils from '../../utils/GeneralUtils'
import { StateManager } from '../manager/StateManager'
import BaseState from './BaseState'
import ReadyState from './ReadyState'

const { player, match, analytics } = globalThis.game

class InitializeState extends BaseState {
    public static didGameStart = false

    constructor(manager: StateManager) {
        super(manager)

        if (GameSDK.getSDKName() === 'CrazyGames') {
            this.processCrazyGamesRequirements()
        }

        this.enableUI()

        this.clearHint()
        this.clearBoard()

        this.loadLevel()
        this.initCountdown()
        this.updateLevelText()

        this.startMatchAsync().then(() => {
            this.logAnalytics()
        })
    }

    private processCrazyGamesRequirements(): void {
        if ('gameplayStart' in GameSDK) {
            GameSDK.gameplayStart()
        }

        this.showInterAdsCrazyGames()
    }

    private async showInterAdsCrazyGames(): Promise<void> {
        this.stopAllSounds()
        const game = this.game
        if (game) {
            await GeneralUtils.showInterstitialAdAsync({
                component: game,
                includeList: ['CrazyGames'],
                onSucceedCallback: () => {
                    game.disableRewardAdsItemForCrazyGames()
                },
            })
            this.turnUpBackgroundMusic()
        }
    }

    private enableUI(): void {
        this.game?.uiManager.enableHeader()
    }

    private clearHint(): void {
        // this.game?.hint?.reset()
    }

    private clearBoard(): void {
        if (!this.game) return

        this.game.grid.destroyAll()
        this.game.pathDraw.clearMatchPath()
    }

    private loadLevel(): void {
        if (!this.game) return

        this.game.levelManager.loadLevel(this.data.level).then(() => {
            GeneralUtils.playSound('sounds/level_start')
        })

        this.game.uiManager.setCountdownTime(this.data.levelData?.time!)
    }

    private initCountdown(): void {
        if (!this.game) return

        this.game.uiManager.initCountdown()
    }

    private updateLevelText(): void {
        const { level } = this.data
        if (level === undefined || level === null) return

        this.game?.uiManager.setLevelText(level)
    }

    private stopAllSounds() {
        find('Sound')?.getComponent(Sound)?.stopAll()
    }

    private turnUpBackgroundMusic(): void {
        find('Music')?.getComponent(Music)?.setVolume(0.8)
    }

    private async startMatchAsync(): Promise<void> {
        if (this.data.isContextProcessing) {
            this.updateGameData({ isContextProcessing: false })
            return
        }

        try {
            const playerId = player.getPlayerId()
            await match.single.start.processAsync({ playerId })
        } catch (error) {
            console.warn('ReadyState', error)
        }
    }

    private logAnalytics(): void {
        if (!this.game) return

        const { level = 0 } = this.data

        analytics.levelStart(level, 3)
    }

    public initialize(): void {
        // This is initialize state, do nothing
    }

    public ready(): void {
        const state = new ReadyState(this.manager)
        this.manager.change(state)
    }

    public pause(): void {
        // Don't allow pause in initialize state
    }

    public playing(): void {
        // Don't allow play in initialize state
    }

    public fail(): void {
        // Don't allow fail in initialize state
    }

    public complete(): void {
        // Don't allow complete in initialize state
    }

    public finish(): void {
        // Don't allow finish in initialize state
    }

    public update(): void {
        //
    }
}

export default InitializeState
