import Grid from '../game-objects/grid/Grid'
import Tile from '../game-objects/tile/Tile'
import spiralTraversal from '../utils/2d/traverseSpiralIn'
import { Path, getValidPath } from './getValidPath'

// function to traverse through the grid to find the first pair of matching tiles that getValidPath() returns true
export default function findFirstMatch(grid: Grid): Path | null {
    // get coords in spiral order to optimize the search
    // NOTE: this search is skewed towards the top left corner of the grid (row first)
    const spiraledCoords = Array.from(spiralTraversal(grid.gridW, grid.gridH))

    // convert coords to tiles and filter out empty tiles
    // NOTE: this assumes that spiralTraversal() returns valid coords
    // WARNING: this will throw an error if the tile is null
    const tiles = spiraledCoords
        .map(({ x, y }) => {
            const tile = grid.getTileAt(x, y)
            if (!tile) throw new Error('Tile is null')
            return tile
        })
        .filter((tile) => !tile.isTraversable())

    const discovered: Tile[] = []

    // while version
    while (tiles.length > 0) {
        const tile = tiles.shift()!

        if (discovered.find((dis) => dis === tile)) continue

        discovered.push(tile)

        // find other tile that is the same and has a valid path
        for (const other of tiles) {
            if (!other.isSame(tile) || discovered.find((dis) => dis === other)) continue

            const path = getValidPath(grid, tile, other)
            if (path.length > 0) {
                return path
            }
        }
    }

    return null
}
