import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

type ChooseContextPayload = GameCore.Plugins.Match.Concrete.Payload.Context.Choose

const { match, player, context, event } = globalThis.game

@ccclass('FlowContext')
export class FlowContext extends Component {
    private playerId = ''

    public onLoad() {
        this.playerId = player.getPlayerId()
    }

    private showLoadingScreen(duration: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
            screenData: { duration },
        })
    }

    private closeLoadingScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    public async chooseFriends(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as ChooseContextPayload

        try {
            this.showLoadingScreen(0)

            await match.context.choose.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('chooseFriends', error)

            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async processContextData(): Promise<void> {
        try {
            this.showLoadingScreen(0)

            await context.processContextData()
        } catch (error) {
            console.warn('processContext', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public setSingleContext() {
        const contextId = ''
        const contextType = 'SOLO'
        const entryPointData = {}

        this.changeContext(contextId, contextType, entryPointData)
    }

    public setTournamentContext() {
        const contextId = 'mocked-context-id-tournament'
        const contextType = 'THREAD'

        const entryPointData = {}

        //? for tournament = true for FBInstant.getTournamentAsync() mock check
        this.changeUrl(`/?context_source_id=123&context_type=GENERIC&tournament=true`)
        this.changeContext(contextId, contextType, entryPointData)
    }

    public setShareInviteContext() {
        const sessionContextType = context.getSessionContextTypes()
        const { SHARE_INVITE } = sessionContextType

        const contextId = 'mocked-context-id-share-invite'
        const contextType = 'THREAD'
        const entryPointData = {
            type: SHARE_INVITE,
            playerId: '4766619483409000',
            playerName: 'Player',
            playerPhoto: '',
        }

        this.changeContext(contextId, contextType, entryPointData)
    }

    public setMatchingGroupContext() {
        const sessionContextType = context.getSessionContextTypes()
        const { MATCHING_GROUP } = sessionContextType

        const contextId = 'mocked-context-id-matching-group'
        const contextType = 'GROUP'
        const entryPointData = {
            type: MATCHING_GROUP,
            playerId: '4766619483409000',
            playerName: 'Player',
            playerPhoto: '',
            playerScore: 100,
        }

        this.changeContext(contextId, contextType, entryPointData)
    }

    public setChallengeFriendContext() {
        const sessionContextType = context.getSessionContextTypes()
        const { CHALLENGE_FRIEND } = sessionContextType

        const contextId = 'mocked-context-id-challenge-friend'
        const contextType = 'THREAD'
        const entryPointData = {
            type: CHALLENGE_FRIEND,
            playerId: '4766619483409000',
            playerName: 'Player',
            playerPhoto: '',
        }

        this.changeContext(contextId, contextType, entryPointData)
    }

    private changeContext = async (
        contextId: string,
        contextType: GameSDK.Type,
        entryPointData: Record<string, unknown>
    ) => {
        context.receiveContext(contextId, contextType, entryPointData)
        await context.detectContextSessionType()
        game.context.detectContextGameMode()
    }

    private changeUrl(urlPath: string) {
        window.history.pushState({}, '', urlPath)
    }
}
