import { Component, _decorator } from 'cc'
import ScreenKeys from '../../../constants/ScreenKeys'
const { ccclass } = _decorator

type JoinChallengePayload = GameCore.Plugins.Match.Concrete.Payload.Challenge.Join
type ChallengeFriendPayload = GameCore.Plugins.Match.Concrete.Payload.Challenge.Friend
type ContinueChallengePayload = GameCore.Plugins.Match.Concrete.Payload.Challenge.Continue

const { match, player, event } = globalThis.game

@ccclass('FlowChallenge')
export class FlowChallenge extends Component {
    private playerId = ''

    public onLoad() {
        this.playerId = player.getPlayerId()
    }

    private showLoadingScreen(duration: number) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
            screenData: { duration },
        })
    }

    private closeLoadingScreen() {
        event.emit(GameCore.Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.LOADING_SCREEN,
        })
    }

    private showNotificationScreen(message: string) {
        event.emit(GameCore.Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { message },
        })
    }

    public async inviteFriends() {
        try {
            this.showLoadingScreen(0)

            await match.challenge.invite.processAsync({
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('inviteFriends', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async challengeFriend(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as ChallengeFriendPayload

        try {
            this.showLoadingScreen(0)

            await match.challenge.friend.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('challengeFriend', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async joinChallenge(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as JoinChallengePayload

        try {
            this.showLoadingScreen(0)

            await match.challenge.join.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('joinChallenge', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async continueChallenge(_: Event, customEventData: string) {
        const payload = JSON.parse(customEventData) as ContinueChallengePayload

        try {
            this.showLoadingScreen(0)

            await match.challenge.continue.processAsync({
                ...payload,
                playerId: this.playerId,
            })
        } catch (error) {
            console.warn('continueChallenge', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }

    public async finishChallenge() {
        try {
            this.showLoadingScreen(0)

            await match.challenge.finish.processAsync({
                playerId: player.getPlayerId(),
                playerScore: Math.floor(Math.random() * 2),
            })
        } catch (error) {
            console.warn('finishChallenge', error)
            if (error instanceof Error) {
                this.showNotificationScreen(error.message)
            }
        } finally {
            this.closeLoadingScreen()
        }
    }
}
