import { Direction } from '../constants/Directions'
import LevelGravity from '../constants/LevelGravity'
import Grid from '../game-objects/grid/Grid'
import traverse4 from '../utils/2d/traverse4'

const SimpleGravityMap: {
    [key in LevelGravity]: DirectionType
} = {
    [LevelGravity.FallDown]: Direction.Down,
    [LevelGravity.FlyUp]: Direction.Up,
    [LevelGravity.PushLeft]: Direction.Left,
    [LevelGravity.PushRight]: Direction.Right,
}

export function canApplyGravity(grid: Grid, direction: LevelGravity): boolean {
    if (direction in SimpleGravityMap) {
        return canApplyDirectionalGravity({
            grid,
            minRow: 0,
            maxRow: grid.gridH - 1,
            minCol: 0,
            maxCol: grid.gridW - 1,
            direction: SimpleGravityMap[direction],
        })
    }

    if (direction === LevelGravity.PutColumnMiddle) {
        return (
            canApplyDirectionalGravity({
                grid,
                minRow: 0,
                maxRow: grid.gridH - 1,
                minCol: 0,
                maxCol: Math.floor(grid.gridW / 2) - 1,
                direction: Direction.Right,
            }) &&
            canApplyDirectionalGravity({
                grid,
                minRow: 0,
                maxRow: grid.gridH - 1,
                minCol: Math.floor(grid.gridW / 2),
                maxCol: grid.gridW - 1,
                direction: Direction.Left,
            })
        )
    }

    if (direction === LevelGravity.PutRowMiddle) {
        return (
            canApplyDirectionalGravity({
                grid,
                minRow: 0,
                maxRow: Math.floor(grid.gridH / 2) - 1,
                minCol: 0,
                maxCol: grid.gridW - 1,
                direction: Direction.Up,
            }) &&
            canApplyDirectionalGravity({
                grid,
                minRow: Math.floor(grid.gridH / 2),
                maxRow: grid.gridH - 1,
                minCol: 0,
                maxCol: grid.gridW - 1,
                direction: Direction.Down,
            })
        )
    }

    throw new Error(`Invalid level gravity: ${direction}`)
}

function canApplyDirectionalGravity(config: {
    grid: Grid
    minRow: number
    maxRow: number
    minCol: number
    maxCol: number
    direction: DirectionType
}): boolean {
    const { grid, minRow, maxRow, minCol, maxCol, direction } = config

    const width = maxCol - minCol + 1
    const height = maxRow - minRow + 1

    const incrementX = Direction.Left === direction ? Direction.Right : Direction.Left
    const incrementY = Direction.Down === direction ? Direction.Up : Direction.Down

    const startingCol = Direction.Left === direction ? minCol : maxCol
    const startingRow = Direction.Down === direction ? minRow : maxRow

    const rowIt = traverse4(
        {
            x: startingCol,
            y: startingRow,
        },
        incrementY,
        height - 1
    )

    let rowGen = rowIt.next()

    while (!rowGen.done) {
        const { y: row } = rowGen.value

        const colIt = traverse4(
            {
                x: startingCol,
                y: row,
            },
            incrementX,
            width - 1
        )

        let colGen = colIt.next()

        while (!colGen.done) {
            const { x: col } = colGen.value

            // Move the tile in the specified direction
            let newRow = row + direction.y
            let newCol = col + direction.x

            // Keep moving the tile until it reaches an empty space or the edge of the grid
            while (newRow >= minRow && newRow <= maxRow && newCol >= minCol && newCol <= maxCol) {
                const currTile = grid.getTileAt(newCol, newRow)
                const emptyTile = grid.getTileAt(newCol - direction.x, newRow - direction.y)

                if (!currTile || !emptyTile) {
                    throw new Error('Tile or empty tile is undefined')
                }

                if (!currTile.isTraversable() || emptyTile.isTraversable()) {
                    break
                }

                newRow += direction.y
                newCol += direction.x
            }

            if (newRow !== row + direction.y || newCol !== col + direction.x) {
                return true
            }

            colGen = colIt.next()
        }

        rowGen = rowIt.next()
    }

    return false
}

/**
 * Applies gravity to the grid by moving all the tiles in the given direction
 * @param grid The grid that will be affected by gravity
 * @param direction The direction of gravity
 */
export default function applyGravity(grid: Grid, direction: LevelGravity): Promise<void> {
    if (direction in SimpleGravityMap) {
        return applyDirectionalGravity({
            grid,
            minRow: 0,
            maxRow: grid.gridH - 1,
            minCol: 0,
            maxCol: grid.gridW - 1,
            direction: SimpleGravityMap[direction],
        })
    }

    if (direction === LevelGravity.PutColumnMiddle) {
        return new Promise((resolve) => {
            Promise.all([
                applyDirectionalGravity({
                    grid,
                    minRow: 0,
                    maxRow: grid.gridH - 1,
                    minCol: 0,
                    maxCol: Math.floor(grid.gridW / 2) - 1,
                    direction: Direction.Right,
                }),
                applyDirectionalGravity({
                    grid,
                    minRow: 0,
                    maxRow: grid.gridH - 1,
                    minCol: Math.floor(grid.gridW / 2),
                    maxCol: grid.gridW - 1,
                    direction: Direction.Left,
                }),
            ]).then(() => {
                resolve()
            })
        })
    }

    if (direction === LevelGravity.PutRowMiddle) {
        return new Promise((resolve) => {
            Promise.all([
                applyDirectionalGravity({
                    grid,
                    minRow: 0,
                    maxRow: Math.floor(grid.gridH / 2) - 1,
                    minCol: 0,
                    maxCol: grid.gridW - 1,
                    direction: Direction.Up,
                }),
                applyDirectionalGravity({
                    grid,
                    minRow: Math.floor(grid.gridH / 2),
                    maxRow: grid.gridH - 1,
                    minCol: 0,
                    maxCol: grid.gridW - 1,
                    direction: Direction.Down,
                }),
            ]).then(() => {
                resolve()
            })
        })
    }

    throw new Error(`Invalid level gravity: ${direction}`)
}

/**
 * Parse a string into level modes by extracting the words and matching them to the LevelMode enum. Using string's search()
 * @param text
 */
export function parseLevelGravity(text: string): LevelGravity[] {
    // ex 'fall-down-put-columns-middle' gets converted into ['fall-down', 'put-columns-middle']
    const words = text.split('-')
    const levelModes: LevelGravity[] = []

    let i = 0
    while (i < words.length) {
        const word = words[i]

        switch (word) {
            case 'normal':
                i += 1
                break
            case 'fall':
                levelModes.push(LevelGravity.FallDown)
                i += 2
                break
            case 'fly':
                levelModes.push(LevelGravity.FlyUp)
                i += 2
                break
            case 'push':
                if (words[i + 1] === 'left') {
                    levelModes.push(LevelGravity.PushLeft)
                    i += 2
                } else {
                    levelModes.push(LevelGravity.PushRight)
                    i += 2
                }
                break
            case 'put':
                if (words[i + 1] === 'columns') {
                    levelModes.push(LevelGravity.PutColumnMiddle)
                    i += 3
                } else {
                    levelModes.push(LevelGravity.PutRowMiddle)
                    i += 3
                }
                break
        }
    }

    return levelModes
}

function applyDirectionalGravity(config: {
    grid: Grid
    minRow: number
    maxRow: number
    minCol: number
    maxCol: number
    direction: DirectionType
}): Promise<void> {
    const { grid, minRow, maxRow, minCol, maxCol, direction } = config

    const width = maxCol - minCol + 1
    const height = maxRow - minRow + 1

    const incrementX = Direction.Left === direction ? Direction.Right : Direction.Left
    const incrementY = Direction.Down === direction ? Direction.Up : Direction.Down

    const startingCol = Direction.Left === direction ? minCol : maxCol
    const startingRow = Direction.Down === direction ? minRow : maxRow

    const rowIt = traverse4(
        {
            x: startingCol,
            y: startingRow,
        },
        incrementY,
        height - 1
    )

    let rowGen = rowIt.next()

    while (!rowGen.done) {
        const { y: row } = rowGen.value

        const colIt = traverse4(
            {
                x: startingCol,
                y: row,
            },
            incrementX,
            width - 1
        )

        let colGen = colIt.next()

        while (!colGen.done) {
            const { x: col } = colGen.value

            // Move the tile in the specified direction
            let newRow = row + direction.y
            let newCol = col + direction.x

            // Keep moving the tile until it reaches an empty space or the edge of the grid
            while (newRow >= minRow && newRow <= maxRow && newCol >= minCol && newCol <= maxCol) {
                const currTile = grid.getTileAt(newCol, newRow)
                const emptyTile = grid.getTileAt(newCol - direction.x, newRow - direction.y)

                if (!currTile || !emptyTile) {
                    throw new Error('Tile or empty tile is undefined')
                }

                if (!currTile.isTraversable() || emptyTile.isTraversable()) {
                    break
                }

                // Swap the tile with the empty space
                grid.swapTilesImmediate(currTile, emptyTile)

                newRow += direction.y
                newCol += direction.x
            }

            colGen = colIt.next()
        }

        rowGen = rowIt.next()
    }

    return grid.syncTiles()
}
