import BaseState from '../../grid/states/Base'
import Tile from '../Tile'

export default abstract class SpecialState extends BaseState {
    tile: Tile

    constructor(tile: Tile) {
        super()
        this.tile = tile
    }

    abstract getType(): TileType

    accept(other: Tile): boolean {
        return this.tile.id === other.id
    }

    abstract clear(other?: Tile): Promise<[Tile, Tile][]>
    update(_dt: number): void {
        // do nothing
    }
}
