import { Component, _decorator } from 'cc'
import { SceneManager } from '../../../components/SceneManager'
const { ccclass } = _decorator

@ccclass('AnimationDemo')
export class AnimationDemo extends Component {
    private scene!: SceneManager

    // public __preload(): void {
    //     // called before onLoad, use for initialization
    // }

    public onLoad():void {
        // called once when the component first activated, before any start methods
        this.scene = this.node.parent?.getComponent(SceneManager)!
    }

    // public start(): void {
    //     // called once when the component enabled for first time
    // }

    public switchToTestScene2(): void {
        this.scene.switchScene('TestTransitionScene')
    }
}
