import Item from '../../../../scenes/game-scene/components/game/Item'
import DevItem from '../DevItem'

import { _decorator } from 'cc'

const { ccclass, property } = _decorator

@ccclass
export default class ClearQuantityItem extends DevItem<Item[]> {
    @property({ type: Item, displayName: 'Game item' })
    public items: Item[] = []

    public activate(): Promise<Item[]> {
        this.items.forEach((item) => {
            item.quantity -= item.quantity
        })

        return Promise.resolve(this.items)
    }
}
