import { Node, UIOpacity, Vec3, _decorator, tween } from 'cc'
import { Path } from '../../../scripts/getValidPath'
import { Line } from '../Line'
import { PathDrawer } from '../PathDrawer'

const { ccclass } = _decorator

@ccclass('MatchDrawer')
export class MatchDrawer extends PathDrawer {
    public drawPath(path: Path): void {
        this.clearPath()

        path.forEach((line) => {
            const lineNode = this.linePool.alloc()
            lineNode.getComponent(Line)?.setPoints(line.a, line.b)

            const pointNode = this.pointPool.alloc()
            pointNode.setPosition(new Vec3(line.a.x, line.a.y, 0))
        })

        const pointNode = this.pointPool.alloc()
        pointNode.setPosition(new Vec3(path[path.length - 1].b.x, path[path.length - 1].b.y, 0))
        this.showPath().then(() => {
            this.scheduleOnce(() => {
                this.clearPath()
            }, 0.1)
        })
    }

    private showPath(): Promise<void> {
        return new Promise((resolve) => {
            for (const lineNode of this.lineNodes) {
                const lineOpacity = lineNode.getComponent(UIOpacity)!
                lineOpacity.opacity = 0
                tween(lineOpacity).to(0.25, { opacity: 255 }, { easing: 'quadOut' }).start()
            }

            const pointPromises: Promise<void>[] = []

            this.pointNodes.forEach((pointNode, index) => {
                pointPromises.push(this.animatePointNode(pointNode, index))
            })

            Promise.all(pointPromises).then(() => {
                resolve()
            })
        })
    }

    private animatePointNode(pointNode: Node, index: number): Promise<void> {
        return new Promise((resolve) => {
            pointNode.scale = Vec3.ZERO
            tween(pointNode)
                .delay(0.03 * index)
                .to(0.2, { scale: Vec3.ONE }, { easing: 'quadOut', onComplete: () => resolve() })
                .start()
        })
    }

    public clearPath(): void {
        for (const lineNode of this.lineNodes) {
            const lineOpacity = lineNode.getComponent(UIOpacity)!
            tween(lineOpacity)
                .to(
                    0.2,
                    { opacity: 0 },
                    {
                        onComplete: () => {
                            lineOpacity.opacity = 255
                            this.linePool.free(lineNode)
                        },
                    }
                )
                .start()
        }

        for (const point of this.pointNodes) {
            tween(point)
                .to(
                    0.2,
                    { scale: Vec3.ZERO },
                    {
                        easing: 'backIn',
                        onComplete: () => {
                            this.pointPool.free(point)
                        },
                    }
                )
                .start()
        }

        this.lineNodes = []
        this.pointNodes = []
    }
}
