import { Component, _decorator, find } from 'cc'
import { SpecialItem } from '../game-objects/item/SpecialItem'
const { ccclass } = _decorator

@ccclass('ItemManager')
export class ItemManager extends Component {
    private sceneItems: SpecialItem[] = []

    public get items(): SpecialItem[] {
        // make items immutable
        return [...this.sceneItems]
    }

    public onLoad(): void {
        this.syncItems()
    }

    public syncItems(): void {
        const uiNode = find('Canvas/UI')

        if (!uiNode) {
            return
        }

        const items = uiNode.getComponentsInChildren(SpecialItem)
        items.forEach((item) => {
            if (item.node.activeInHierarchy) {
                this.sceneItems.push(item)
            }
        })
    }
}
