import { Button, Component, Label, Node, UIOpacity, _decorator, math, tween } from 'cc'
import ScreenKeys from 'db://assets/constants/ScreenKeys'
import GameplayEvents from 'db://assets/gameplay/constants/GameEvents'
import { OpacityToggler } from '../../../../../components/common/OpacityToggler'
import SceneKeys from '../../../../../constants/SceneKeys'
const { ccclass, property } = _decorator

const { Events } = GameCore
const { language, event, match, storage } = globalThis.game

@ccclass('Header')
export class Header extends Component {
    @property(Label)
    private levelText!: Label

    @property(Node)
    private settingNode!: Node

    @property(Node)
    private homeNode!: Node

    @property(Node)
    private rescueAnimationMask!: Node

    public onLoad() {
        this.validateRequiredComponents()

        this.listenToEvents()
    }

    private validateRequiredComponents() {
        if (!this.levelText) {
            throw new Error('Header: LevelText not found')
        }

        if (!this.settingNode) {
            throw new Error('Header: SettingNode not found')
        }

        if (!this.rescueAnimationMask) {
            throw new Error('Header: RescueAnimationMask not found')
        }
    }

    private listenToEvents() {
        this.node.scene.on(GameplayEvents.RESCUE_START, this.showRescueAnimationMask, this)
        this.node.scene.on(GameplayEvents.RESCUE_FINISHED, this.hideRescueAnimationMask, this)
    }

    // ? Callback for click event of PauseButton
    public handleClickedPauseGame() {
        this.showSettingScreen()
        this.node.scene.emit(GameplayEvents.GAME_PAUSE)
    }

    // ? Callback for click event of HomeButton
    public handleReturnHome() {
        match.single.finish.processAsync().then(() => {
            storage.setStorageData('dashboard', 'animateToCurrentLevel', true)

            event.emit(Events.SWITCH_SCENE, {
                sceneName: SceneKeys.DASHBOARD_SCENE,
            })
        })
    }

    private showSettingScreen() {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.SETTINGS_SCREEN,
            screenData: {},
        })
    }

    public setLevelText(level: number) {
        const levelText = language.getText('level')
        this.levelText.string = levelText + ` ${level}`
    }

    public setProgress(_progress: number) {
        // this.levelProgressBar.setProgress(progress)
    }

    public setProgressStars(_stars: number) {
        // this.levelProgressBar.setStars(stars)
    }

    public startCountDownHintBubble(): void {
        // this.schedule(this.handlePlayBubble, 30)
    }

    public stopCountDownHintBubble(): void {
        // this.unschedule(this.handlePlayBubble)
    }

    public setButtonsSelectable(isSelectable = true): void {
        this.enableBtnNode(this.settingNode, isSelectable, true)
        this.enableBtnNode(this.homeNode, isSelectable, true)
    }

    private enableBtnNode(node: Node, enable: boolean, visible: boolean): void {
        const button = node.getComponent(Button)
        if (button) {
            button.interactable = enable
        }

        if (visible) {
            node.active = true
            // TODO: use grayscale texture instead of OpacityToggler
            const opacityToggler = node.getComponent(OpacityToggler)
            if (opacityToggler) {
                // opacityToggler.changeState(enable)
            }
        } else {
            node.active = false
        }
    }

    private showRescueAnimationMask() {
        this.rescueAnimationMask.active = true
        const uiOpacity = this.rescueAnimationMask.getComponent(UIOpacity)!

        if (!uiOpacity) {
            console.warn('RescueAnimationMask: UIOpacity not found')
            return
        }

        const startedTime = Date.now()

        tween(this.rescueAnimationMask)
            .to(
                0.5,
                { angle: 0 },
                {
                    onUpdate: () => {
                        const progress =
                            (Date.now() - startedTime) / 500 > 1
                                ? 1
                                : (Date.now() - startedTime) / 500
                        const opacityValue = math.lerp(0, 150, progress)

                        uiOpacity.opacity = opacityValue
                    },
                }
            )
            .start()
    }

    private hideRescueAnimationMask() {
        const uiOpacity = this.rescueAnimationMask.getComponent(UIOpacity)!

        if (!uiOpacity) {
            console.warn('RescueAnimationMask: UIOpacity not found')
            return
        }

        const startedTime = Date.now()

        tween(this.rescueAnimationMask)
            .to(
                0.5,
                { angle: 0 },
                {
                    onUpdate: () => {
                        const progress =
                            (Date.now() - startedTime) / 500 > 1
                                ? 1
                                : (Date.now() - startedTime) / 500
                        const opacityValue = math.lerp(0, 150, progress)

                        uiOpacity.opacity = 150 - opacityValue
                    },
                }
            )
            .call(() => {
                this.rescueAnimationMask.active = false
            })
            .start()
    }
}
