import { Button, Component, Node, UIOpacity, Vec3, _decorator, tween } from 'cc'
import GameplayEvents from '../../gameplay/constants/GameEvents'
import { BaseScreen } from '../BaseScreen'
const { ccclass, property, disallowMultiple, requireComponent } = _decorator

const { Events } = GameCore
const { event } = globalThis.game

@ccclass('CongratulationScreen')
@disallowMultiple
@requireComponent(BaseScreen)
export class CongratulationScreen extends Component {
    private screen!: BaseScreen

    @property(Node)
    public congratulation!: Node

    @property(Node)
    public confirmation!: Node

    @property(Button)
    private closeButtonComponent!: Button

    @property(Button)
    private yesButtonComponent!: Button

    @property(Button)
    private noButtonComponent!: Button

    public __preload() {
        this.getBaseScreen()
        this.listenScreenEvents()
    }

    public onDestroy(): void {
        this.unListenScreenEvents()
    }

    private getBaseScreen() {
        const baseScreen = this.getComponent(BaseScreen)

        if (!baseScreen) {
            console.warn('CongratulationScreen: BaseScreen not found')
            return
        }

        this.screen = baseScreen
    }

    public onLoad() {
        this.enabled = false
        this.validateRequiredComponents()
    }

    private validateRequiredComponents() {
        if (!this.congratulation) {
            throw new Error('CongratulationScreen: congratulation not found')
        }

        if (!this.confirmation) {
            throw new Error('CongratulationScreen: confirmation not found')
        }
    }

    private listenScreenEvents() {
        // ? Here's a demo of all the events a screen has
        event.on(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.on(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private unListenScreenEvents() {
        event.off(Events.OPENING_SCREEN, this.handleOpeningScreen)
        event.off(Events.OPENED_SCREEN, this.handleOpenedScreen)
    }

    private handleOpeningScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.closeButtonComponent.interactable = true
        this.yesButtonComponent.interactable = true
        this.noButtonComponent.interactable = true
    }

    private handleOpenedScreen = (payload: SwitchScreenPayload) => {
        if (!this.validateEventPayload(payload)) return
        this.open()
    }

    private validateEventPayload(payload: SwitchScreenPayload) {
        const { screenName } = payload
        if (screenName !== this.node.name) return false
        return true
    }

    public open() {
        if (!this.enabled) return
        this.confirmation.active = false
        this.congratulation.active = true
    }

    public close() {
        this.closeButtonComponent.interactable = false
        this.yesButtonComponent.interactable = false
        this.noButtonComponent.interactable = false
        event.emit(Events.CLOSE_SCREEN, { screenName: this.node.name })
    }

    // ? Callback for click event of StartOverButton
    public handleStartOver() {
        this.confirmation.active = true
        this.congratulation.active = false

        const opacity = this.confirmation.getComponent(UIOpacity)
        if (!opacity) {
            console.warn('CongratulationScreen: UIOpacity not found')
            return
        }

        tween(opacity).set({ opacity: 0 }).to(0.2, { opacity: 255 }, { easing: 'smooth' }).start()

        tween(this.confirmation)
            .set({ scale: new Vec3(0, 0, 0) })
            .to(0.2, { scale: new Vec3(1, 1, 1) }, { easing: 'smooth' })
            .start()
    }

    // ? Callback for click event of YesButton
    public handleYesStartOver() {
        this.close()
        this.node.scene.emit(GameplayEvents.START_OVER)
    }

    // ? Callback for click event of NoButton
    public handleNoStartOver() {
        this.close()
        this.requestGameReplay()
    }

    // ? Callback for click event of CloseButton
    public handleClickedClose(): void {
        this.close()
        this.requestGameReplay()
    }

    private requestGameReplay() {
        this.node.scene.emit(GameplayEvents.GAME_REPLAY, {
            level: this.screen.data['level'],
        })
    }

    /* public closeConfirmation() {
        this.confirmation.active = true
        const opacity = this.confirmation.getComponent(UIOpacity)
        opacity.opacity = 255
        tween(opacity).to(0.2, { opacity: 0 }, { easing: 'smooth' }).start()
        tween(this.confirmation)
            .to(0.2, { scale: new Vec3(0, 0, 0) }, { easing: 'smooth' })
            .call(() => {
                this.confirmation.active = false
            })
            .start()
    } */
}
