import {
    AssetManager,
    Canvas,
    Component,
    DynamicAtlasManager,
    ResolutionPolicy,
    View,
    _decorator,
    macro,
    profiler,
    sys,
} from 'cc'

const { ccclass, disallowMultiple, executionOrder, requireComponent } = _decorator

@ccclass('SystemManager')
@disallowMultiple
@executionOrder(-900)
@requireComponent(Canvas)
export class SystemManager extends Component {
    // private isSpritesLoadCalled = false

    public __preload() {
        this.validateRequiredComponents()

        this.setupGameConfig()
    }

    private validateRequiredComponents() {
        const canvas = this.getComponent(Canvas)

        if (!canvas) {
            throw new Error('SystemManager: Canvas not found')
        }
    }

    private setupGameConfig() {
        this.hideCocosStats()
        this.setupAutoAtlas()
        this.setupDynamicAtlas()
        this.setupResolutionPolicy()
    }

    private setupAutoAtlas() {
        AssetManager.instance.cacheAsset = true
    }

    private setupDynamicAtlas() {
        const enabled = sys.os !== sys.OS.ANDROID
        macro.CLEANUP_IMAGE_CACHE = !enabled
        DynamicAtlasManager.instance.enabled = enabled
        DynamicAtlasManager.instance.maxAtlasCount = 10
        DynamicAtlasManager.instance.textureSize = 4096
        DynamicAtlasManager.instance.maxFrameSize = 2048
    }

    private setupResolutionPolicy() {
        View.instance.setOrientation(macro.ORIENTATION_PORTRAIT)
        View.instance.setResolutionPolicy(ResolutionPolicy.SHOW_ALL)
    }

    // ? Only hide the cocos stats UI
    private hideCocosStats() {
        this.schedule(this.waitProfilerReady, 0.2, 60)
    }

    private waitProfilerReady = () => {
        if (!profiler) return

        // @ts-expect-error - private property
        profiler._showFPS = false
        // @ts-expect-error - private property
        const { _rootNode } = profiler

        if (_rootNode) {
            _rootNode.active = false
            if (_rootNode.isValid) {
                _rootNode.destroyAllChildren()
                _rootNode.removeAllChildren()
                _rootNode.destroy()
            }
        }
    }
}
