import { Component, _decorator } from 'cc'
import { SceneManager } from '../../components/SceneManager'
import GameEvents from '../../constants/GameEvents'
import ScreenKeys from '../../constants/ScreenKeys'
const { ccclass, disallowMultiple, requireComponent } = _decorator

const { Events } = GameCore
const { event } = globalThis.game

@ccclass('TestLayoutScene')
@disallowMultiple
@requireComponent(SceneManager)
export class TestLayoutScene extends Component {
    private scene!: SceneManager

    public __preload() {
        this.scene = this.getComponent(SceneManager)!

        this.validateRequiredComponents()

        this.listenGameEvents()
    }

    private validateRequiredComponents() {
        if (!this.scene) {
            throw new Error('TestLayoutScene: SceneManager not found')
        }
    }

    private listenGameEvents() {
        this.node.scene.on(GameEvents.LAYOUT_CHANGED, this.handleLayoutChange)
    }

    private handleLayoutChange = () => {
        this.closeNotificationScreen()
        this.openNotificationScreen()
    }

    private openNotificationScreen() {
        event.emit(Events.OPEN_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
            screenData: { duration: 1.5, message: 'Layout changed' },
        })
    }

    private closeNotificationScreen() {
        event.emit(Events.CLOSE_SCREEN, {
            screenName: ScreenKeys.NOTIFICATION_SCREEN,
        })
    }
}
