import { Component, _decorator } from 'cc'
import { Gameplay } from '../../Gameplay'

const { ccclass } = _decorator

@ccclass
export default abstract class DevItem<T = void> extends Component {
    public abstract activate(): Promise<T>
    protected gameplay: Gameplay | null = null

    public injectGameplay(gameplay: Gameplay) {
        this.gameplay = gameplay
    }

    public get quantity(): number {
        return 1
    }

    public set quantity(value: number) {
        console.warn(
            `Do not set quantity on ${this.constructor.name}. It is a dev item. Tried to set to ${value}.`
        )
    }

    public canActivate(): boolean {
        return true
    }
}
