import { Component, _decorator } from 'cc'
const { ccclass } = _decorator

const { Utils } = GameCore
const { player } = globalThis.game

@ccclass('Vibrate')
export class Vibrate extends Component {
    public vibrate(time: number): void {
        if (!this.isVibrateEnable()) return

        const isSuccess = Utils.Browser.vibrate(time)

        if (!isSuccess) {
            if ('performHapticFeedbackAsync' in GameSDK) {
                GameSDK.performHapticFeedbackAsync()
                return
            }

            console.warn('Vibrate failed')
        }
    }

    private isVibrateEnable(): boolean {
        return !!player.getPlayerSetting('vibrate')
    }
}
